import {Box} from "../../_components/box";
import {useEffect, useState} from "react";
import {useDispatch} from "react-redux";
import {Spinner} from "../../_components/spinner";
import {InputText} from "../../_components/input";
import {Button} from "../../_components/button";
import {CheckIcon} from "../../_components/icons";
import {Text} from "../../_components/text";
import {errorToast} from "../../_store/toast/toast.slice";
import {getCompetitions, storeCompetition} from "../../_api/competition.api";
import {useCompetitions, useOrganizationOptions, useOrganizations} from "../../_utils/hooks";
import {useNavigate} from "react-router-dom";
import {resetCompetition} from "../../_store/competition/competition.slice";
import {Select, SelectWrapper} from "../../_components/select";
import {getOrganizations} from "../../_api/organization.api";

export const CompetitionsCreateRoute = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {isLoading, isSuccess} = useCompetitions()
    const organizationOptions = useOrganizationOptions()

    const [form, setForm] = useState({
        name: "",
        organization_id: ""
    })
    const onSave = () => {
        if (!form.name) {
            dispatch(errorToast('Please input a name'))
            return
        }
        if (!form.organization_id) {
            dispatch(errorToast('Please select an organization'))
            return
        }
        console.log(form)
        dispatch(storeCompetition(form))

    }
    const onOrganizationChange = (e) => {
        setForm(prevForm => ({
            ...prevForm,
            organization_id: e.value
        }))
    }
    useEffect(() => {
        dispatch(getOrganizations())
    }, []);

    useEffect(() => {
        if (!isLoading && isSuccess) {
            navigate('/v3/competitions')
            dispatch(resetCompetition())
        }
    }, [isLoading]);
    return (
        <div className={"flex flex-col"}>
            <Box className={"w-full mt-4"}
                 title={"Create a new Competition"}
                 body={(
                     <div
                         className={"w-full flex flex-col p-4 !bg-gray-800 gap-4 border-gray-600 border-x border-b rounded-b-lg"}>
                         <InputText value={form.name}
                                    onChange={(value) => setForm({...form, name: value.target.value})}
                                    className={"p-3  border-gray-300 border"}
                                    placeholder={"Please enter competition name"}/>
                         <SelectWrapper title={"Organization"}
                                        className={"bg-white border-gray-300 border rounded-md"}
                                        children={(
                                            <Select options={organizationOptions}
                                                    searchable={true}
                                                    onChange={onOrganizationChange}
                                            />
                                        )}
                         />
                         <Button icon rounded className={"bg-green-200 text-white"} onClick={onSave}
                                 isLoading={isLoading}>
                             <CheckIcon color={"white"}/>
                             <Text>Save</Text>
                         </Button>
                     </div>
                 )}
            />
        </div>
    )
}
