import {createAsyncThunk} from "@reduxjs/toolkit";
import {EventApi} from "../_utils/api";

export const fetchEventById = createAsyncThunk(
    'events/fetchById',
    async (eventId, thunkAPI) => {
        const response = await EventApi.getData(eventId)
        return response.data.data.event
    },
)
export const setEventStatusById = createAsyncThunk(
    'events/setStatus',
    async (data, thunkAPI) => {
        const response = await EventApi.setStatus(data)
        return response.data.data
    },
)



