import {useNavigate, useParams} from "react-router-dom";
import {VideoPlayer} from "../../../_components/video-player";
import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {deleteHighlight, fetchEventById, separateHighlights} from "../../../_api/event.api";
import {Select} from "../../../_components/select";
import {Spinner} from "../../../_components/spinner";
import {Button} from "../../../_components/button";
import {Caption, Text, Title} from "../../../_components/text";
import {Box} from "../../../_components/box";
import {
    getFullTimeSeconds,
    getTimeSeconds,
    secondsToTime,
    secondsToTimeView,
    SubtractTimes
} from "../../../_utils/helpers";
import {
    ArrowDownIcon,
    ArrowUpIcon,
    CheckIcon, CommentIcon, DeleteIcon, DownloadIcon,
    EditIcon, EmptyFlagIcon, EmptyStarIcon, FilterIcon,
    FlagIcon,
    PauseIcon,
    PlayIcon, SearchIcon,
    StarIcon, TimesIcon
} from "../../../_components/icons";
import {InputRadio} from "../../../_components/input";
import {openModal} from "../../../_store/modal/modal.slice";
import {confirmToast, errorToast, successToast} from "../../../_store/toast/toast.slice";
import {HighlightVideoPlayer} from "../../../_components/highlight-video-player";
import {ButtonMenu} from "../../../_components/button-menu";
import {List, ListItem} from "../../../_components/list";
import {SearchButton} from "../../../_components/search-button";
import {FilterBar, FilterMenu} from "../../../_components/filter-menu";
import {getSettings} from "../../../_api/setting.api";
import {cloneDeep} from "lodash";
import {useFilterMenu, useHighlightFilter} from "../../../_utils/hooks";
import {userHasPermission} from "../../../_hooks/user.hooks";
import {list} from "postcss";

export const HighlightRoute = () => {

    const userCanSeparateVideos = userHasPermission('event-videos')
    const userCanCreateClip = userHasPermission('event-videos')
    const userCanDownloadVideo = userHasPermission('event-videos')
    const userCanDeleteHighlight = userHasPermission('highlight-delete')
    const userCanEditHighlight = userHasPermission('highlight-edit')
    const {eventId, filter} = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const {selectedEvent} = useSelector(state => state.event)
    const {resetSignal} = useSelector(state => state.modal)
    const setting = useSelector(state => state.setting)
    const sections = selectedEvent?.sections
    const [filterCallback, setFilterCallback] = useState((item) => () => true)
    const [openSections, setOpenSections] = useState([])
    const [openedTopBar, setOpenedTopBar] = useState("")
    const [selectedPlay, setSelectedPlay] = useState({
        start: null,
        end: null,
        sectionId: null,
        highlight: null,
    })
    const internalRoutingOptions = [
        {
            name: 'All Highlights',
            value: 'all',
        },
        {
            name: 'Flagged Highlights',
            value: 'flagged',
        },
        {
            name: 'Starred Highlights',
            value: 'starred',
        },
        {
            name: 'Highlights without videos',
            value: 'without_videos',
        },
    ]
    const computeTotalHighlights = (highlights) => secondsToTimeView(
        highlights.reduce((sum, highlight) => {
            return getTimeSeconds(highlight.until_time.substring(3)) - getTimeSeconds(highlight.start_time.substring(3)) + sum
        }, 0)
    )

    const toggleSections = (section) => {
        let newOpens = [...openSections]
        const foundIndex = newOpens.findIndex(o => o === section.id)
        if (foundIndex > -1) {
            newOpens.splice(foundIndex, 1)
        } else {
            newOpens.push(section.id)
        }
        setOpenSections(newOpens)
    }

    const onRouteChange = (option) => {
        navigate(`/v3/events/${selectedEvent.id}/tagger/highlights/${option.value}`)
    }
    const resetSelectedPlay = () => {
        setPlayedHighlight({})
        setSelectedPlay({
            start: null,
            end: null,
            highlight: null,
            sectionId: null,
        })
    }
    const playHighlight = (highlight) => {

        if (playedHighlight.id === highlight.id) {
            resetSelectedPlay()

            return
        }
        setPlayedHighlight(highlight)
        setSelectedPlay({
            start: getFullTimeSeconds(highlight.from),
            end: getFullTimeSeconds(highlight.to),
            sectionId: highlight.section_id,
            highlight
        })
    }

    const [selectedHighlights, setSelectedHighlights] = useState([])
    const [selectedSections, setSelectedSections] = useState([])
    const selectHighlight = highlight => {
        let newArray = [...selectedHighlights]
        const foundIndex = newArray.findIndex(i => i.id === highlight.id)
        if (foundIndex > -1) {
            newArray.splice(foundIndex, 1)
        } else {
            newArray.push(highlight)
        }
        setSelectedHighlights(newArray)
    }
    const selectHighlights = section => {
        if (!selectedSections.includes(section)) {
            setSelectedSections(prev => [...prev, section])
            setSelectedHighlights(prev => {
                let newArray = [...prev]
                section.highlights.forEach(highlight => {
                    if (!newArray.includes(highlight)) {
                        newArray.push(highlight)
                    }
                })
                return newArray
            })
            return
        }
        setSelectedSections(prev => {
            let newArray = [...prev]
            const foundIndex = newArray.findIndex(i => i.id === section.id)
            if (foundIndex > -1) {
                newArray.splice(foundIndex, 1)
            }
            return newArray
        })
        setSelectedHighlights(prev => {
            let newArray = [...prev]
            section.highlights.forEach(highlight => {
                const foundIndex = newArray.findIndex(item => item.id === highlight.id)
                if (foundIndex > -1) {
                    newArray.splice(foundIndex, 1)
                }
            })
            return newArray
        })

    }
    const highlightIsSelected = highlight => (openedTopBar !== '' && selectedHighlights.includes(highlight)) || playedHighlight.id === highlight.id

    const onSeparateHighlight = () => {

        dispatch(confirmToast({
            message: 'Are you sure you want to separate highlights?',
            callback: () => {
                const data = {
                    eventId,
                    highlight_ids: selectedHighlights.map(s => (s.id))
                }
                dispatch(separateHighlights(data))
                resetForm()
            }
        }))
    }
    const resetForm = () => {
        setSelectedHighlights([])
        setOpenedTopBar('')
    }
    const onCreateClip = () => {
        const data = {
            eventId,
            highlight_ids: selectedHighlights.map(s => (s.id))
        }
        dispatch(openModal({
            element: 'CreateClip',
            props: data,
            title: 'Create a Clip'
        }))

    }
    const [playedHighlight, setPlayedHighlight] = useState("")

    useEffect(() => {
        if (!resetSignal) return
        resetForm()
    }, [resetSignal])

    useEffect(() => {
        if (selectedEvent) return
        dispatch(getSettings(eventId))
        dispatch(fetchEventById(eventId))
    }, []);
    useEffect(() => {
        let func = () => true
        switch (filter) {
            case 'flagged':
                func = (item) => item.has_video_problem
                break;
            case 'starred':
                func = (item) => item.is_starred
                break;
            case 'without_videos':
                func = (item) => !item.has_video
                break;
            case 'all':
                func = () => true
                break;
        }
        setFilterCallback((prevState) => func)
    }, [filter]);

    const downloadVideo = (highlight) => {
        if (!highlight.download_url) {
            dispatch(errorToast('This video has not gotten a download url yet.'))
            return
        }
        window.open(highlight.download_url)
    }
    const onDeleteHighlight = (highlight) => {
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this highlight?',
            callback: () => {
                dispatch(deleteHighlight({
                    eventId,
                    sectionId: highlight.section_id,
                    highlightId: highlight.id
                }))
            }
        }))
    }
    const onEditHighlight = (highlight) => {
        navigate(`/v3/events/${eventId}/tagger/highlight/edit/${highlight.id}`)
    }
    const onChangeLink = (highlight) => {
        dispatch(openModal({
            element: 'ChangeHighlightLinkModal',
            props: {
                highlight,
                eventId: eventId
            },
            title: `Change Video Link for ${highlight.id}`
        }))
    }
    const onChangeDescription = (highlight) => {
        dispatch(openModal({
            element: 'ChangeHighlightDescription',
            props: {
                highlight,
                eventId: eventId
            },
            title: `Change Description for ${highlight.id}`
        }))
    }
    const onCopyLink = async (highlight) => {
        if (!highlight.download_url) {
            dispatch(errorToast('No Download Url Available'))
            return
        }
        const result = await copyToClipboard(highlight.download_url)
       if(result){
           dispatch(successToast('Copied Successfully!'))
       }else{
           dispatch(errorToast('Could not copy link due to an insecure connection'))
       }
    }
    const [optionIsOpen, setOptionIsOpen] = useState("")
    async function copyToClipboard(textToCopy) {
        // Navigator clipboard api needs a secure context (https)
        if (navigator.clipboard && window.isSecureContext) {
            await navigator.clipboard.writeText(textToCopy);
        } else {
            // Use the 'out of viewport hidden text area' trick
            const textArea = document.createElement("textarea");
            textArea.value = textToCopy;

            // Move textarea out of the viewport so it's not visible
            textArea.style.position = "absolute";
            textArea.style.left = "-999999px";

            document.body.prepend(textArea);
            textArea.select();

            try {
                document.execCommand('copy');
            } catch (error) {
                console.error(error);
            } finally {
                textArea.remove();
            }
        }
    }
    const {
        filterMenu,
        filterMenuIsOpen,
        setFilterMenuIsOpen,
        onSelectFilter,
        onSearchQueryChange,
        filterFn,
        searchQuery,
        selectedFilters,
        clearAllFilters
    } = useHighlightFilter()

    // useEffect(()=>{
    //     const listener = e=>{
    //
    //         if(optionIsOpen.id && !e.target.classList.contains('is-fixed-button-menu')){
    //             setOptionIsOpen({})
    //         }
    //     }
    //     document.addEventListener('click',listener)
    //     return () => document.removeEventListener('click',listener)
    // },[optionIsOpen])
    if (!selectedEvent) {
        return <Spinner type={"block"}/>
    }
    return (
        <div>
            <HighlightVideoPlayer selectedPlay={selectedPlay} resetSelectedPlay={resetSelectedPlay}/>
            {/*<VideoPlayer durationToPlay={selectedPlay}/>*/}
            <div className={"flex flex-col items-center justify-center p-4 mt-16"}>


                <div className={"flex flex-row items-center justify-between w-full"}>
                    {openedTopBar === '' && (
                        <React.Fragment>
                            <Select
                                className={"bg-gray-300 p-2 rounded-lg"}
                                options={internalRoutingOptions}
                                onChange={onRouteChange}
                                selected={filter}
                            ></Select>
                            <div className={"flex flex-row gap-2 items-center justify-start"}>
                                {userCanCreateClip && <Button className={"rounded-full border border-gray-300"}
                                                              onClick={() => setOpenedTopBar('clip')}>
                                    <Text>Create Clip</Text>
                                </Button>}
                                {userCanSeparateVideos && <Button className={"rounded-full border border-gray-300"}
                                                                  onClick={() => setOpenedTopBar('separate')}>
                                    <Text>Separate Highlights</Text>
                                </Button>}

                                {/*Filter*/}

                                <div className={"relative"}>
                                    <Button className={"rounded-full border border-gray-300"} icon
                                            onClick={() => setFilterMenuIsOpen(!filterMenuIsOpen)}>
                                        <FilterIcon/>
                                        <Text>Filter</Text>
                                    </Button>
                                    <FilterMenu
                                        isVisible={filterMenuIsOpen}
                                        selectedFilters={selectedFilters}
                                        onSelectFilter={onSelectFilter}
                                        onClose={() => setFilterMenuIsOpen(false)}
                                        filterOptions={filterMenu}/>
                                </div>
                                <Button className={"rounded-full border border-gray-300"}>
                                    <DeleteIcon/>
                                </Button>
                                <Button className={"rounded-full border border-gray-300"}>
                                    <DownloadIcon/>
                                </Button>
                                <SearchButton query={searchQuery} onChange={onSearchQueryChange}/>
                            </div>
                        </React.Fragment>
                    )}
                    {openedTopBar !== '' && (
                        <React.Fragment>
                            <div className={"bg-blue-50 p-4 rounded-2xl w-full flex items-center justify-between"}>
                                {selectedHighlights.length === 0
                                    ? <Title>Select Highlights
                                        to {openedTopBar === 'separate' ? 'To Separate' : 'Create Clip'}</Title>
                                    : (
                                        <div>
                                            <Title
                                                className={"text-blue-1-hold font-extrabold"}>{selectedHighlights.length} Highlights</Title>
                                            <Caption
                                                className={"text-blue-1-hold"}>
                                                {selectedHighlights.reduce((sum, highlight) => highlight.details.length + sum, 0)} Tags, {computeTotalHighlights(selectedHighlights)}
                                            </Caption>
                                        </div>
                                    )}
                                <div className={"flex flex-row items-center gap-2"}>
                                    {openedTopBar === 'separate' &&
                                        <Button className={"rounded-full bg-blue-100 text-white"}
                                                onClick={onSeparateHighlight}
                                                disabled={selectedHighlights.length === 0}>
                                            <Text>Separate Highlights</Text>
                                        </Button>}
                                    {openedTopBar === 'clip' &&
                                        <Button className={"rounded-full bg-blue-100 text-white"}
                                                onClick={onCreateClip}
                                                disabled={selectedHighlights.length === 0}>
                                            <Text>Create Clip</Text>
                                        </Button>}
                                    <Button className={"rounded-full bg-white"} onClick={() => resetForm()}>
                                        <Text>Cancel</Text>
                                    </Button>
                                </div>
                            </div>
                        </React.Fragment>
                    )}

                </div>
                {selectedFilters?.length > 0 && (
                    <FilterBar onSelectFilter={onSelectFilter} selectedFilters={selectedFilters} clearAllFilters={clearAllFilters} />
                )}
                <Box className={"w-full mt-4"}
                     title={"Highlights"}
                     headerClass={"border-none !bg-gray-700"}
                     body={(
                         <div className={"bg-gray-700 p-2 rounded-b-2xl overflow-x-auto"}>
                             {sections.map(section => (
                                 <table key={section.id} className={" mt-4 relative "}>
                                     <thead onClick={() => toggleSections(section)}>
                                     <tr>
                                         {openedTopBar !== '' && <td colSpan={1}>
                                             <InputRadio className={"bg-transparent"}
                                                         onChange={() => selectHighlights(section)}
                                                         selected={selectedSections.includes(section)}
                                             />
                                         </td>}
                                         <td colSpan={32} className={"px-4 py-2"}>
                                             <div className={"flex flex-row items-center justify-between"}>
                                                 <div className={"flex flex-col gap-2"}>
                                                     <Text>{section.name}</Text>
                                                     <Caption>{section.highlights.filter(filterCallback).filter(filterFn).length} Highlights
                                                         , {computeTotalHighlights(section.highlights.filter(filterCallback).filter(filterFn))}</Caption>
                                                 </div>
                                                 <Button onClick={() => toggleSections(section)}>
                                                     {openSections.includes(section.id) ? <ArrowDownIcon/> :
                                                         <ArrowUpIcon/>}
                                                 </Button>
                                             </div>
                                         </td>
                                     </tr>
                                     </thead>
                                     {openSections.includes(section.id) && <thead>
                                     <tr>
                                         {openedTopBar !== '' && <th colSpan={1}>

                                         </th>}
                                         <th colSpan={1}>#</th>
                                         <th colSpan={2}>Start Time</th>
                                         <th colSpan={2}>End Time</th>
                                         <th colSpan={1} className={"text-left"}>Tag</th>
                                         <th colSpan={2} className={"text-left"}>Team</th>
                                         <th colSpan={4} className={"text-left"}>Player</th>
                                         <th colSpan={3} className={"text-left"}>SubTags</th>
                                         <th colSpan={4} className={"text-left"}>Tag Description</th>
                                         <th colSpan={4} className={"text-left"}>Highlight Description</th>
                                         <th colSpan={2}>Flag</th>
                                         <th colSpan={2}>Starred</th>
                                         <th colSpan={2}>Video</th>
                                         <th colSpan={3}>Action</th>
                                     </tr>
                                     </thead>}
                                     {openSections.includes(section.id) && <tbody>
                                     {section.highlights.filter(filterCallback).filter(filterFn).map((highlight, key) => (
                                         <tr key={highlight.id}
                                             className={highlightIsSelected(highlight) ? 'bg-blue-400 border border-blue-300' : ''}>
                                             {openedTopBar !== '' && <td className={"!p-0"} colSpan={1}>
                                                 <InputRadio onChange={() => selectHighlight(highlight)}
                                                             className={"bg-transparent p-4 justify-center"}
                                                             selected={selectedHighlights.includes(highlight)}
                                                 />
                                             </td>}
                                             <td colSpan={1}><Text className={"font-light"}>{key + 1}</Text></td>
                                             <td colSpan={2}><Text
                                                 className={"font-light"}>{highlight.start_time}</Text></td>
                                             <td colSpan={2}><Text
                                                 className={"font-light"}> {highlight.until_time}</Text></td>
                                             <td colSpan={1}
                                                 className={"p-0  " + (highlightIsSelected(highlight) ? 'bg-blue-50' : 'bg-gray-800')}>
                                                 <div className={"flex flex-col text-left"}>
                                                     {highlight.details.map(detail => (
                                                         <div
                                                             key={detail.id}
                                                             className={"border-b border-gray-600 last-of-type:border-none flex flex-row items-center p-1 gap-1"}
                                                         >
                                                             <Text className={"font-extrabold"}>{detail.type} {detail.exactTime ? ' - ' + detail.sectionExactTime : ''}</Text>

                                                         </div>
                                                     ))}
                                                 </div>
                                             </td>
                                             <td colSpan={2}
                                                 className={"p-0  " + (highlightIsSelected(highlight) ? 'bg-blue-50' : 'bg-gray-800')}>
                                                 <div className={"flex flex-col text-left"}>
                                                     {highlight.details.map(detail => (
                                                         <div
                                                             key={detail.id}
                                                             className={"border-b border-gray-600 last-of-type:border-none flex flex-row items-center p-1 gap-1"}
                                                         >
                                                             <Caption
                                                                 className={"text-gray-200"}>{detail.team}</Caption>

                                                         </div>
                                                     ))}
                                                 </div>
                                             </td>
                                             <td colSpan={4}
                                                 className={"p-0  " + (highlightIsSelected(highlight) ? 'bg-blue-50' : 'bg-gray-800')}>
                                                 <div className={"flex flex-col text-left"}>
                                                     {highlight.details.map(detail => (
                                                         <div
                                                             key={detail.id}
                                                             className={"border-b border-gray-600 last-of-type:border-none flex flex-row items-center p-1 gap-1"}
                                                         >
                                                             <Caption
                                                                 className={"text-gray-200"}>{detail.player}</Caption>
                                                         </div>
                                                     ))}
                                                 </div>
                                             </td>
                                             <td colSpan={3}
                                                 className={"p-0  " + (highlightIsSelected(highlight) ? 'bg-blue-50' : 'bg-gray-800')}>
                                                 <div className={"flex flex-col text-left"}>
                                                     {highlight.details.map((detail, key) => (
                                                         <div
                                                             key={key}
                                                             className={"border-b border-gray-700 last-of-type:border-none flex flex-row items-center px-1"}>
                                                             {detail.subTags.map((subTag, key) => (

                                                                 <Text key={key}>{subTag.name}
                                                                     {key + 1 !== highlight.details.length && ', '}
                                                                 </Text>
                                                             ))}
                                                             {detail.subTags.length === 0 && <Text>-</Text>}
                                                         </div>
                                                     ))}
                                                 </div>
                                             </td>
                                             <td colSpan={4}
                                                 className={"p-0"}>
                                                 {highlight.details.map((detail, key) => (
                                                     <div
                                                         key={detail.id}
                                                         className={"border-b border-gray-600 last-of-type:border-none flex flex-row items-center p-1 gap-1"}
                                                     >
                                                         {detail.comment && <Text className={"font-extrabold max-w-[240px] break-all overflow-x-auto"}>{detail.comment}</Text>}
                                                         {!detail.comment && <Text>-</Text>}


                                                     </div>
                                                 ))}
                                             </td>
                                             <td colSpan={4}
                                                 className={"p-0"}>

                                                 <Text className={"font-extrabold max-w-[240px] break-all overflow-x-auto"}>{highlight.comment}</Text>
                                                 {!highlight.comment && <Text>-</Text>}

                                             </td>
                                             <td colSpan={2}>
                                                 {highlight.has_video_problem ?
                                                     <FlagIcon className={"flex items-center justify-center"}/> :
                                                     <EmptyFlagIcon className={"flex items-center justify-center"}
                                                                    color={"#D4D5D6"}/>}
                                             </td>
                                             <td colSpan={2}>
                                                 {highlight.is_starred ?
                                                     <StarIcon className={"flex items-center justify-center"}/> :
                                                     <EmptyStarIcon className={"flex items-center justify-center"}
                                                                    color={"#D4D5D6"}/>}
                                             </td>
                                             <td colSpan={2}>
                                                 {highlight.has_video && highlight.download_url !== null ?
                                                     <Button onClick={() => downloadVideo(highlight)}>
                                                         <DownloadIcon/>
                                                     </Button> :
                                                     (highlight.aws_job_id !== null && highlight.download_url === null ? (
                                                         <Spinner/>
                                                     ) : (
                                                         <DownloadIcon className={"flex items-center justify-center"}
                                                                       color={"lightgray"}/>
                                                     ))}
                                             </td>
                                             <td colSpan={3} className={"p-0"}>
                                                 <div className={"flex flex-row"}>
                                                     <Button
                                                         onClick={() => playHighlight(highlight)}
                                                         className={"flex-1  flex items-center justify-center border-r border-gray-300"}>
                                                         {playedHighlight.id === highlight.id ? <PauseIcon/> :
                                                             <PlayIcon color={"#2B87FF"}/>}
                                                     </Button>
                                                     <ButtonMenu
                                                         className={"flex-1 flex items-center justify-center is-fixed-button-menu"}
                                                         isOpen={optionIsOpen.id === highlight.id}
                                                         onOpen={() => {
                                                             if(optionIsOpen.id === highlight.id) {
                                                                 setOptionIsOpen({})
                                                             }else {
                                                                 setOptionIsOpen(highlight)
                                                             }
                                                         }}
                                                         onClose={() => setOptionIsOpen({})}
                                                     >
                                                         <List
                                                             className={"bg-white border border-gray-600 z-20 min-w-24 rounded-lg m-2 is-fixed-button-menu"}>
                                                             {userCanEditHighlight && highlight.has_video_problem ? <ListItem
                                                                 className={"border-b border-gray-600 text-left is-fixed-button-menu"}>
                                                                 <Button className={"p-2 text-left is-fixed-button-menu"}
                                                                         onClick={() => onChangeLink(highlight)}
                                                                 >
                                                                     <Text className={"is-fixed-button-menu"}>Change Link</Text>
                                                                 </Button>
                                                             </ListItem> : ''}
                                                             {userCanDownloadVideo && highlight.download_url !== null ? <ListItem
                                                                 className={"border-b border-gray-600 text-left"}>
                                                                 <Button className={"p-2 text-left is-fixed-button-menu"}
                                                                         onClick={() => onCopyLink(highlight)}
                                                                 >
                                                                     <Text className={"is-fixed-button-menu"}>Copy Link</Text>
                                                                 </Button>
                                                             </ListItem> : ''}

                                                             {userCanEditHighlight && <ListItem className={" border-b border-gray-600 is-fixed-button-menu"}>
                                                                 <Button icon className={"p-2"}
                                                                         onClick={() => onChangeDescription(highlight)}
                                                                 >
                                                                     <CommentIcon/>
                                                                     <Text className={"is-fixed-button-menu"}>Add Description</Text>
                                                                 </Button>
                                                             </ListItem>}
                                                             {userCanEditHighlight && <ListItem className={" border-b border-gray-600 is-fixed-button-menu"}>
                                                                 <Button icon className={"p-2"}
                                                                         onClick={() => onEditHighlight(highlight)}
                                                                 >
                                                                     <EditIcon/>
                                                                     <Text >Edit Highlight</Text>
                                                                 </Button>
                                                             </ListItem>}
                                                             {userCanDeleteHighlight && <ListItem>
                                                                 <Button icon className={"p-2 is-fixed-button-menu "}
                                                                         onClick={() => onDeleteHighlight(highlight)}
                                                                 >
                                                                     <DeleteIcon color={"#BA1119"}/>
                                                                     <Text className={"text-red-200 is-fixed-button-menu"}>
                                                                         Delete
                                                                     </Text>
                                                                 </Button>
                                                             </ListItem>}
                                                         </List>
                                                     </ButtonMenu>
                                                     {/*<Button className={"flex-1 flex items-center justify-center"}*/}
                                                     {/*        onClick={() => navigate(`/v3/events/${selectedEvent.id}/tagger/highlight/edit/${highlight.id}`)}*/}
                                                     {/*>*/}
                                                     {/*    <EditIcon/>*/}
                                                     {/*</Button>*/}
                                                 </div>
                                             </td>

                                         </tr>
                                     ))}
                                     </tbody>}
                                 </table>
                             ))}
                         </div>
                     )}
                />
            </div>

        </div>
    )
}
