import {Title, Text} from "./text";
import {ListItem} from "./list";
import {Button} from "./button";
import {ArrowDownIcon, ArrowRightIcon, ArrowUpIcon, PlusIcon} from "./icons";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../_store/modal/modal.slice";
import {useState} from "react";
import {useNavigate} from "react-router-dom";
import {permissionToast} from "../_store/toast/toast.slice";
import {userHasPermission} from "../_hooks/user.hooks";
import {useEventSectionsAreReady} from "../_utils/hooks";

export const Team = (props) => {
    const classList = "" + props?.className
    const imageClass = props.imageClass;
    const textClass = props.textClass;
    const onClick = props.onClick ?? (() => {
    })
    const showPlayerCount = props.playerCount ?? true
    return (
        <div className={classList} onClick={onClick}>
            <img src={props.team.logo} className={"w-10 h-10 " + imageClass} alt={props.team.name}/>
            <Title
                className={textClass}>{props.team.name} {showPlayerCount && props.team.players.length > 0 && " (" + props.team.players.length + ")"}</Title>

            {props.children}
        </div>
    )
}

export const TeamPlayers = (props) => {
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const userCanAddPlayer = userHasPermission('player-create')
    const userCanEditPlayer = userHasPermission('player-edit')
    const team = props.team
    const [isOpen, setIsOpen] = useState(false)
    const {selectedEvent} = useSelector(state => state.event)

    const navigateToPlayer = (playerId) => {
        if(!userCanEditPlayer) {
            dispatch(permissionToast())
            return
        }
        if (selectedEvent.sections.length > 0) {
            navigate(`player/edit/${playerId}`)
        }
    }
    const videosAreReady = useEventSectionsAreReady()
    const navigateToPlayerMain = () => {
        if(!userCanAddPlayer) {
            dispatch(permissionToast())
            return
        }
        if (videosAreReady) {
            navigate('player')
        }
    }
    const onAddPlayer = (e) => {
        e.stopPropagation()
        if(!userCanAddPlayer) {
            dispatch(permissionToast())
            return
        }
        dispatch(openModal({
            element: 'AddPlayersModal',
            props: team,
            title: 'Add Players to ' + team.name
        }))
    }
    return (
        <div className={"flex flex-col rounded-xl"}>
            <div
                className={`flex flex-row items-center justify-between bg-gray-800 p-2 rounded-t-xl ${videosAreReady && 'cursor-pointer'}`}
                onClick={navigateToPlayerMain}
            >
                <Team className={"flex flex-row gap-1"}
                      textClass={"flex items-center justify-start"} imageClass={"w-8 h-8"}
                      team={team}/>
                <div className={"flex flex-row items-center gap-2"}>
                    <Button
                        className={"flex flex-row gap-2 items-center justify-center w-36 px-4 border-r border-gray-300"}
                        onClick={onAddPlayer}>
                        <Text className={"font-bold text-blue-100"}>Add Players</Text>
                        <PlusIcon color={"#0571FC"}/>
                    </Button>
                    <Button className={"flex items-center justify-center p-4"} onClick={() => setIsOpen(!isOpen)}>
                        {videosAreReady ? <ArrowRightIcon /> : (isOpen ? <ArrowDownIcon/> : <ArrowUpIcon/>)}
                    </Button>
                </div>
            </div>
            {isOpen && team.players.map(player => (
                <ListItem key={player.id}
                          className={"bg-white last-of-type:rounded-b-md border-t border-gray-600 flex flex-row items-center justify-between"}>
                    <Text className={"p-4 px-3 "}>{player.squad_number} - {player.full_name}</Text>
                    <div className={"flex flex-row items-center justify-center gap-2"}>
                        <Text className={"text-gray-100"}>
                            {player.squad_number ? player.squad_number : 'No Squad Number'}
                            - {player.position_name ? player.position_name : 'No Position'}
                            - {player.position_side ? player.position_side : 'No Position Side'}</Text>
                        <Button className={"flex items-center justify-center p-4"}
                                onClick={() => navigateToPlayer(player.id)}>
                            <ArrowRightIcon/>
                        </Button>
                    </div>
                </ListItem>
            ))}
        </div>
    )
}
