export const ListItem = (props) => {
    const classList = " " + props.className
    return (
        <div className={classList} onClick={props.onClick}>
            {props.children}
        </div>
    )
}

export const List = props => {
    const classList = "flex flex-col "+ props.className

    return (
        <div className={classList}>
            {props.children}
        </div>
    )
}
