import {useNavigate, useParams} from "react-router-dom";
import {useEventPlayer} from "../../../../_utils/hooks";
import React, {useEffect} from "react";
import {Button} from "../../../../_components/button";
import {ArrowLeftIcon, PanelSideOpenIcon} from "../../../../_components/icons";
import {Title} from "../../../../_components/text";
import {PlayerForm} from "../../../../_forms/player.form";
import {NotAuthorizedComponent} from "../../../../_components/not-authorized.component";
import {userHasPermission} from "../../../../_hooks/user.hooks";

export const EditPlayer = ({setIsOpen,currentTime}) => {
    const {eventId,playerId} = useParams()
    const navigate = useNavigate()
    const {team, player} = useEventPlayer(null, playerId)
    const userCanEditPlayer = userHasPermission('player-edit')
    useEffect(() => {
        if (!playerId) return
        setIsOpen(true)
    }, [playerId])

    if(!userCanEditPlayer){
        return <NotAuthorizedComponent />
    }
    return (
        <React.Fragment>
            <div className={"flex flex-row items-center justify-between"}>
                <div className={"flex flex-row gap-2 items-center"}>
                    <Button onClick={() => navigate(`/v3/events/${eventId}/tagger/player`)}>
                        <ArrowLeftIcon color={"black"}/>
                    </Button>
                    <Title>Player Details</Title>
                </div>
                <Button
                    className={"flex justify-center"}
                    onClick={() => setIsOpen(false)}
                >
                    <PanelSideOpenIcon/>
                </Button>
            </div>
            <div className={""}>
                <PlayerForm playerId={player.id}
                            disabledCardView={true}
                            currentTime={currentTime}
                />
            </div>
        </React.Fragment>
    )
}
