import {Caption, Text} from "./text";
import {InputRadio} from "./input";
import React, {useEffect, useState} from "react";
import {cloneDeep} from "lodash";
import {Button} from "./button";
import {ArrowLeftIcon, ArrowRightIcon, TimesIcon} from "./icons";
import {useOutsideClick} from "../_utils/hooks";


export const FilterBar = ({className="",selectedFilters,onSelectFilter,clearAllFilters}) => {
    return (
        <div
            className={`border border-gray-300 rounded-lg p-2 flex flex-wrap items-center justify-start gap-2 w-full mt-2 ${className}`}>
            <Caption className={"text-gray-100"}>Filters</Caption>
            {selectedFilters.map(filter => (
                <div
                    className={"flex flex-row items-center justify-center gap-2 bg-blue-50 rounded-full px-4 py-1 text-gray-100"}>
                    {filter.icon && <img src={filter.icon} className={"w-6 h-6 rounded-full bg-white"}/>}
                    <Text>{filter.name}</Text>
                    <Button className={"flex items-center justify-center p-1"}
                            onClick={() => onSelectFilter(filter)}>
                        <TimesIcon/>
                    </Button>
                </div>
            ))}
            <Button
                className={"border border-gray-300 rounded-full bg-white"}
                onClick={()=>clearAllFilters()}
            >
            <Text className={"text-gray-100"}>Clear All</Text>
            </Button>
        </div>
    )
}
export const FilterMenu = ({filterOptions, isVisible, onClose, selectedFilters, onSelectFilter}) => {
    const [openedSubMenu, setOpenedSubMenu] = useState([])

    const openSubMenu = (filterOption) => {
        let nextMenu = [...openedSubMenu]
        nextMenu.push(filterOption.children)
        setOpenedSubMenu(nextMenu)
    }
    const handleGoBack = () => {
        let newSubMenu = [...openedSubMenu]
        newSubMenu.pop()
        setOpenedSubMenu(newSubMenu)
    }
    const filterIsSelected = (filterOption) => !!selectedFilters.find(selectedFilter => selectedFilter.type === filterOption.type && selectedFilter.value === filterOption.value)
    const ref = useOutsideClick(() => onClose())
    useEffect(() => {
        let nextState = [...openedSubMenu]
        nextState.push(filterOptions)
        setOpenedSubMenu(nextState)
    }, []);
    useEffect(() => {
        // console.log(selectedFilters)
    }, [selectedFilters]);
    return (
        <div
            ref={ref}
            className={"absolute top-12 min-w-[240px] right-0 bg-white rounded-lg flex flex-col z-20 " + (isVisible ? 'visible' : 'hidden')}>
            {openedSubMenu.length > 1 && (
                <Button className={"flex-1 border border-gray-300 border-b-none p-4 rounded-t-lg flex flex-row items-center justify-start"} icon onClick={handleGoBack}>
                    <ArrowLeftIcon/>
                    <Text>Back</Text>
                </Button>
            )}
            {openedSubMenu.length > 0 && openedSubMenu[openedSubMenu.length - 1].map((filterOption, index) => (
                <div
                    key={index}
                    className={"p-1 flex flex-row items-center justify-between border-b border-x border-gray-300 first-of-type:border-t last-of-type:border-b first-of-type:rounded-t-lg last-of-type:rounded-b-lg "
                + (openedSubMenu.length > 1 ? 'first-of-type:border-t-none first-of-type:rounded-t-none' : '')}>
                    <div className={"flex flex-row items-center justify-center gap-2 p-3"}>
                        {filterOption.icon &&
                            <img src={filterOption.icon} className={"w-7 h-7 bg-white rounded-full"}/>}
                        <div className={"flex flex-col gap-2 items-start justify-start"}>
                            <Text>{filterOption.name}</Text>
                            {(filterOption.children && filterOption.children.length > 0) && (
                                <Text
                                    className={"text-gray-200"}>{filterOption.children.length} {filterOption.childrenLabel}</Text>
                            )}
                        </div>
                    </div>
                    <div className={"flex flex-row items-center justify-between "}>
                        {!filterOption.selectable &&
                            <InputRadio className={"bg-white"} selected={filterIsSelected(filterOption)}
                                        onChange={(selected) => onSelectFilter(filterOption, selected)}/>}
                        {(filterOption.children && filterOption.children.length > 0) && (
                            <Button onClick={() => openSubMenu(filterOption)}>
                                <ArrowRightIcon/>
                            </Button>
                        )}
                    </div>
                </div>
            ))}
        </div>
    )
}
