import {Text} from "./text";
import React from "react";

export const Box = (props) => {
    const className = " flex flex-col " + props.className
    const headerClass = "flex flex-row justify-between border border-gray-600 bg-gray-800 rounded-t-md "  + props.headerClass
    const title = props.title
    const actions = props.actions ? props.actions : []
    return (
        <div className={className}>
            <div className={headerClass}>
                <Text className={"font-bold p-4"}>{title}</Text>
                {actions.map((action, key) =>(
                    <React.Fragment key={key}>
                        {action}
                    </React.Fragment>
                ))}
            </div>
            {props.body}
        </div>
    )
}
