import React from "react";
import {ListItem} from "../_components/list";
import {ArrowRightIcon, PlusIcon} from "../_components/icons";
import {Box} from "../_components/box";
import {useEvent, useEventSectionsAreReady} from "../_utils/hooks";
import {Button} from "../_components/button";
import {Text} from "../_components/text";
import {useNavigate} from "react-router-dom";
import {userHasPermission} from "../_hooks/user.hooks";
import {useDispatch} from "react-redux";
import {errorToast, permissionToast} from "../_store/toast/toast.slice";

export const TagsAndHighlightsBox = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const event = useEvent()

    const userCanListHighlight = userHasPermission('highlight-list')
    const userCanAddHighlight = userHasPermission('highlight-create')

    const videosAreReady = useEventSectionsAreReady()
    const onHighlightCreate = () => {
        if(!userCanAddHighlight){
            dispatch(permissionToast())
            return
        }
        if(!videosAreReady) {
            dispatch(errorToast('You have not set section times yet'))
            return
        }
        navigate('highlight')
    }
    if (!userCanListHighlight) return
    return (
        <Box title={"Highlights and Clips"}
             actions={[
                 <Button onClick={onHighlightCreate}
                         className={"flex-1 flex flex-row gap-2 items-center justify-end w-20 px-4"}>
                     <Text
                         className={"text-blue-100"}>{event.sections[0]?.highlights.length > 0 ? 'Add or Edit Tags' : 'Start Tagging'}</Text>
                     <PlusIcon color={"#0571FC"}/>
                 </Button>
             ]}
             body={
                 <React.Fragment>
                     <ListItem
                         className={"bg-white last-of-type:rounded-b-md border-gray-600 flex flex-row justify-between items-center"}>
                         <Text className={"p-4 px-3 "}>{event.total_highlights} Highlights</Text>
                         <Button onClick={() => {
                             if(!videosAreReady) {
                                 dispatch(errorToast('You have not set section times yet'))
                                 return
                             }
                             navigate('highlights/all')
                         }}
                                 className={"w-10 h-auto p-4 px-6"}
                         >
                             <ArrowRightIcon/>
                         </Button>
                     </ListItem>
                     <ListItem
                         className={"bg-white last-of-type:rounded-b-md border-gray-600 flex flex-row justify-between items-center"}>
                         <Text className={"p-4 px-3 "}>{event.total_clips} Clips</Text>
                         <Button onClick={() => {
                             if(!videosAreReady) {
                                 dispatch(errorToast('You have not set section times yet'))
                                 return
                             }
                             navigate('clips')
                         }}
                                 className={"w-10 h-auto p-4 px-6"}
                         >
                             <ArrowRightIcon/>
                         </Button>
                     </ListItem>

                 </React.Fragment>
             }
        />
    )
}

