import {createSlice} from '@reduxjs/toolkit'
import {getAuth} from "../../_api/auth.api";


const initialState = {
    user: undefined,
    permissions: [],
    isLoading:false
}

export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getAuth.fulfilled, (state, action) => {
            state.user = {
                name: action.payload.user.first_name + ' ' + action.payload.user.last_name
            }
            state.permissions = action.payload.user.roles[0].permissions
            state.isLoading = false
        })
        builder.addCase(getAuth.pending, (state, action) => {
            state.isLoading = true;
        })
    }

})

// export const {set} = settingSlice.actions

export default authSlice.reducer
