import React from 'react'
import {Title} from "../_components/text";
import {ModalButtons} from "../_components/modal";
import {useDispatch, useSelector} from "react-redux";
import {useState} from "react";
import {closeModal, setResetSignal} from "../_store/modal/modal.slice";
import {InputText, TextArea} from "../_components/input";
import {errorToast} from "../_store/toast/toast.slice";
import {storeCustomHighlight} from "../_api/event.api";


export const CreateClipModal = (props) => {
    const dispatch = useDispatch()
    const [form, setForm] = useState({
        highlight_ids: props.data.highlight_ids,
        eventId: props.data.eventId
    })
    const {isLoading } = useSelector(state => state.event.isLoading)
    const onClose = () => dispatch(closeModal())
    const onDone = () => {
        if (!form.title) {
            dispatch(errorToast('Please input clip title'))
            return
        }
        dispatch(setResetSignal(true))
        dispatch(storeCustomHighlight(form))
    }
    return (
        <React.Fragment>
            <Title className={"p-4 border-b border-gray-600"}>{props.title}</Title>
            <div className={"flex flex-col p-4 gap-2"}>
                <InputText
                    placeholder={"Title"}
                    value={form.title}
                    onChange={(v) => setForm({...form, title: v.target.value})}
                />
                <TextArea
                    placeholder={"Description"}
                    value={form.description}
                    onChange={(v) => setForm({...form, description: v})}
                />
            </div>
            <ModalButtons isLoading={isLoading} onClose={onClose} onDone={onDone}/>

        </React.Fragment>
    )
}
