import {InputMinute} from "./input";
import {SelectTime, SelectVideoTime, SelectVideoTimeWithCaption} from "./select";
import React, {useEffect, useRef, useState} from "react";
import {Caption, Text} from "./text";
import {getTimeSeconds, secondsToTime} from "../_utils/helpers";
import {parseInt} from "lodash";


export const RangeTimeSingle = props => {

    const onChange = (e) => {
        let val = typeof e === 'string' ? getTimeSeconds(e) : e.target.value
        // console.log('RangeVal',val)
        props.onChange(secondsToTime(val))
    }
    return (
        <div className={"flex flex-row items-center gap-2 pb-2 border-b border-gray-600"}>
            <SelectVideoTimeWithCaption
                title={"Exact Time"}
                value={props.value}
                min={getTimeSeconds(props.min)}
                max={getTimeSeconds(props.max)}
                onChange={onChange}
            />
            <input
                type={"range"}
                value={getTimeSeconds(props.value)}
                min={getTimeSeconds(props.min)}
                max={getTimeSeconds(props.max)}
                onChange={onChange}
                className={"flex-1 customRange"}
            />
        </div>
    )
}
export const RangeTime = (props) => {
    const [initials, setInitials] = useState({
        min: typeof props.min === 'number' ? props.min : getTimeSeconds(props.min),
        max: typeof props.max === 'number' ? props.max : getTimeSeconds(props.max),
    })

    console.log('Initials',initials)
    const startElement = useRef()
    const endElement = useRef()


    const handleInputStart = (e, v) => {
        let [from, to] = getInputs();
        if (v) {
            from = v
        }
        fillSliderShort()
        if (from >= to) {
            props.onChange({
                from: secondsToTime(to),
                to: secondsToTime(to),
                last: to
            })

        } else {
            props.onChange({
                from: secondsToTime(from),
                to: secondsToTime(to),
                last: from
            })
        }
    }
    const handleInputEnd = (e, v) => {

        let [from, to] = getInputs();
        if (v) {
            to = v
        }
        fillSliderShort()
        setToggleAccessible();
        if (to <= from) {
            props.onChange({
                from: secondsToTime(from),
                to: secondsToTime(from),
                last: from
            })
        } else {
            props.onChange({
                from: secondsToTime(from),
                to: secondsToTime(to),
                last: to
            })
        }
    }
    const getInputs = () => [Number(startElement.current.value), Number(endElement.current.value)]
    const fillSliderShort = () => {
        fillSlider(startElement.current, endElement.current, "#D4D5D6", "black", endElement.current);
    }
    const fillSlider = (from, to, sliderColor, rangeColor, controlSlider) => {
        const rangeDistance = to.max - to.min;
        const fromPosition = from.value - to.min;
        const toPosition = to.value - to.min;
        controlSlider.style.background = `linear-gradient(
      to right,
      ${sliderColor} 0%,
      ${sliderColor} ${(fromPosition / rangeDistance) * 100}%,
      ${rangeColor} ${(fromPosition / rangeDistance) * 100}%,
      ${rangeColor} ${(toPosition / rangeDistance) * 100}%,
      ${sliderColor} ${(toPosition / rangeDistance) * 100}%,
      ${sliderColor} 100%)`;
    }

    const setToggleAccessible = () => {

        if (Number(endElement.current.value) <= 0) {
            endElement.current.style.zIndex = 12;
        } else {
            endElement.current.style.zIndex = 2;
        }
    }


    useEffect(() => {
        fillSliderShort();
        setToggleAccessible();
    }, [props]);
    return (<div className={"flex flex-row items-center justify-between gap-2"}>
        <SelectVideoTimeWithCaption
            title={"Start"}
            value={props.start}
            min={initials.min}
            max={getTimeSeconds(props.end)}
            onChange={(v) => handleInputStart({}, getTimeSeconds(v))}
            allowLengthOne={false}
        />

        <div className={"w-full relative customDualRange flex items-center"}>
            <input ref={startElement}
                   tabIndex={-1}
                   onInput={handleInputStart}
                   value={getTimeSeconds(props.start)}
                   type={"range"}
                   className={"from"}
                   min={initials.min}
                   max={initials.max}
            />
            <input ref={endElement}
                   tabIndex={-1}
                   onInput={handleInputEnd}
                   value={getTimeSeconds(props.end)}
                   type={"range"}
                   className={"to"}
                   min={initials.min}
                   max={initials.max}
            />
        </div>
        <SelectVideoTimeWithCaption
            title={"End"}
            value={props.end}
            min={getTimeSeconds(props.start)}
            max={initials.max}
            onChange={(v) => handleInputEnd({}, getTimeSeconds(v))}
            allowLenghOne={false}
        />
    </div>)
}
