import {useNavigate, useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import React, {useEffect, useState} from "react";
import {deleteSection, fetchEventById, updateSection} from "../../../_api/event.api";
import {VideoOld} from "../../../_components/video-player";
import {Card} from "../../../_components/card";
import {Text, Title} from "../../../_components/text";
import {InputMinute, InputText} from "../../../_components/input";
import {SelectVideoTimeBetween, SelectWrapper} from "../../../_components/select";
import {Button} from "../../../_components/button";
import {DeleteIcon} from "../../../_components/icons";
import {selectSection} from "../../../_store/event/event.slice";
import {parseInt} from "lodash";
import {userHasPermission} from "../../../_hooks/user.hooks";
import {errorToast, permissionToast} from "../../../_store/toast/toast.slice";
import {getTimeSeconds} from "../../../_utils/helpers";

export const SectionsRoute = (props) => {
    const {eventId, sectionId} = useParams()
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {selectedEvent, isLoading} = useSelector(state => state.event)
    const [section, setSection] = useState()
    const userCanEditSection = userHasPermission('section-edit')
    const userCanDeleteSection = userHasPermission('section-delete')

    const onDelete = (selectedSection) => {
        if (!userCanDeleteSection) {
            dispatch(permissionToast())
            return
        }
        dispatch(deleteSection({
            eventId: selectedEvent.id,
            section_id: selectedSection.id
        }))
        navigate(`/v3/events/${eventId}/tagger`)
    }

    const onSave = () => dispatch(updateSection({
        eventId: selectedEvent.id,
        section,
    }))


    useEffect(() => {
        if (selectedEvent !== null) return
        (async () => {
            await dispatch(fetchEventById(eventId))
        })()
    }, [selectedEvent]);
    useEffect(() => {
        if (selectedEvent === null) return
        if (!sectionId) {
            const foundSection = selectedEvent.sections.at(0)
            setSection({
                ...foundSection,
                duration: foundSection.duration ?? foundSection.section_type_duration
            })
            return
        }
        const foundSection = selectedEvent.sections.find(section => Number(section.id) === Number(sectionId))
        setSection({
            ...foundSection,
            duration: foundSection.duration ?? foundSection.section_type_duration
        })

    }, [selectedEvent, sectionId])

    const changeEnd = (value) => {
        if (section.video_start_time && getTimeSeconds(value) < getTimeSeconds(section.video_start_time)) {
            dispatch(errorToast('"End Time" can not be sooner that "Start Time"'))
            return;
        }
        setSection({
            ...section, video_end_time: value
        })
    }
    const changeStart = (value) => {
        if (section.video_end_time && getTimeSeconds(value) > getTimeSeconds(section.video_end_time)) {
            dispatch(errorToast('"Start Time" can not be later that "End Time"'))
            return;
        }
        setSection({
            ...section, video_start_time: value
        })
    }
    if (!userCanEditSection) {
        return (<div>Not Authorized</div>)
    }
    if (isLoading || !section) {
        return (<div>Loading...</div>)
    }
    return (<div>
        {!isLoading && section && (<React.Fragment>
            <VideoOld
                src={section.video_url}
                changeEnd={changeEnd}
                changeStart={changeStart}
            />
            <div className={"grid grid-rows-* lg:grid-cols-2 grid-cols-1 gap-3 m-3"}>
                <Card
                    header={<Title>Edit {section.name}</Title>}
                    body={<div className={"flex flex-col"}>
                        <SelectWrapper
                            title={"Veo Url"}
                            className={"border border-gray-600 border-t-none !bg-white"}
                        >
                            <div className={"flex flex-row items-center gap-2 flex-1"}>
                                <InputText className={"border-none w-full"} value={section.video_url}
                                           onChange={(e) => setSection({
                                               ...section, video_url: e.target.value
                                           })}/>
                            </div>
                        </SelectWrapper>
                        <SelectWrapper
                            title={"Time"}
                            className={"border border-gray-600 border-t-none !bg-white"}
                        >
                            <SelectVideoTimeBetween
                                allowLengthOne={true}
                                start={section.video_start_time}
                                end={section.video_end_time}
                                onChangeStart={(value) => {
                                    setSection({
                                        ...section, video_start_time: value
                                    })
                                }}
                                onChangeEnd={(value) => setSection({
                                    ...section, video_end_time: value
                                })}></SelectVideoTimeBetween>
                        </SelectWrapper>
                        <SelectWrapper
                            title={"Section Duration"}
                            className={"border border-gray-600 border-t-none !bg-white"}
                        >
                            <div className={"flex flex-row items-center gap-2"}>
                                <Text>{section.duration ?? 0} m</Text>
                                <InputMinute max={200} min={0} value={section.duration ?? section.section_type_duration}
                                             onChange={(value) => setSection({
                                                 ...section,
                                                 duration: value
                                             })}
                                             className={"bg-white border border-gray-600 p-2 rounded-md w-[40px]"}/>
                            </div>
                        </SelectWrapper>

                        <Button className={"w-full bg-blue-100 text-white p-4 mt-4 rounded-full"}
                                onClick={onSave}>
                            <Text>Save</Text>
                        </Button>

                    </div>}
                />
                <Card
                    header={<Title>Sections</Title>}
                    body={<>
                        <div className={"p-4 flex flex-col gap-2"}>
                            {selectedEvent.sections.map((section, key) =>
                                <div key={key} className={"bg-white p-4 pr-2 rounded-lg flex flex-row"}>
                                    <div className={"flex flex-col"}>
                                        <div className={"flex flex-row items-center gap-2"}>
                                            <Title>{section.name}</Title>
                                            {section.duration &&
                                                <Text className={"text-[#6A7181]"}>{section.duration} Minutes</Text>}
                                        </div>
                                        <div className={"flex flex-row items-center gap-2 text-[#6A7181]"}>
                                            <Text>{section.video_start_time ?? '00:00'}</Text>
                                            <Text>to</Text>
                                            <Text>{section.video_end_time ?? '00:00'}</Text>
                                        </div>
                                    </div>
                                    <div className={"flex flex-row ml-auto gap-2"}>
                                        {section.video_url && <Button className={"border-r border-gray-300 w-14"}
                                                                      onClick={() => navigate(`/v3/events/${eventId}/tagger/sections/edit/${section.id}`)}>
                                            <Text className={"text-blue-100"}>Edit</Text>
                                        </Button>}
                                        {userCanDeleteSection && <Button className={"border-gray-300 w-10"}
                                                                         onClick={() => onDelete(section)}>
                                            <DeleteIcon/>
                                        </Button>}
                                    </div>
                                </div>
                            )}
                        </div>
                    </>}
                />
            </div>
        </React.Fragment>)}

    </div>)
}
