import {createAsyncThunk} from "@reduxjs/toolkit";
import {CompetitionApi} from "../_utils/api";

export const getCompetitions = createAsyncThunk(
    'competition/get',
    async (eventId, thunkAPI) => {
        const response = await CompetitionApi.get()
        return response.data.data
    }
)

export const storeCompetition = createAsyncThunk(
    'competition/store',
    async (data, thunkAPI) => {
        const response = await CompetitionApi.store(data)
        return response.data.data
    }
)
export const updateCompetition = createAsyncThunk(
    'competition/update',
    async (data, thunkAPI) => {
        const response = await CompetitionApi.update(data)
        return response.data.data
    }
)
export const deleteCompetition = createAsyncThunk(
    'competition/delete',
    async (data, thunkAPI) => {
        const response = await CompetitionApi.delete(data)
        return response.data.data
    }
)

