import {createSlice} from '@reduxjs/toolkit'
import {getSettings} from "../../_api/setting.api";


const initialState = {
    positions: [],
    tags: [],
    subTags: [],
    waterMarks: [],
    types: [],
    footbars: [],
    isLoading: false
}

export const settingSlice = createSlice({
    name: 'event',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getSettings.fulfilled, (state, action) => {
            state.positions = action.payload.positions
            state.tags = action.payload.tags
            state.subTags = action.payload.subTags
            state.waterMarks = action.payload.waterMarks
            state.types = action.payload.types
            state.footbars = action.payload.footbarSessions
            state.isLoading = false
        })
        builder.addCase(getSettings.pending, (state, action) => {
            state.isLoading = true;
        })
    }

})

// export const {set} = settingSlice.actions

export default settingSlice.reducer
