import {Button, ButtonBack, ButtonBackToMain} from "../../_components/button";
import {Box} from "../../_components/box";
import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {useCompetition, useCompetitions, useOrganizationOptions} from "../../_utils/hooks";
import {getCompetitions, updateCompetition} from "../../_api/competition.api";
import {Spinner} from "../../_components/spinner";
import {CheckIcon, PlusIcon} from "../../_components/icons";
import {Text} from "../../_components/text";
import {InputText} from "../../_components/input";
import {useNavigate, useParams} from "react-router-dom";
import {errorToast} from "../../_store/toast/toast.slice";
import {Select, SelectWrapper} from "../../_components/select";
import {getOrganizations} from "../../_api/organization.api";

export const CompetitionsEditRoute = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {id} = useParams()
    const {isLoading,isSuccess} = useCompetitions()
    const organizationOptions = useOrganizationOptions()
    const model = useCompetition(id)
    const [form, setForm] = useState({
        id: null,
        name: "",
        organization_id: ""
    })
    const onUpdate = () => {
        if (!form.name) {
            dispatch(errorToast('Please input a name'))
            return
        }
        dispatch(updateCompetition(form))
    }
    const onOrganizationChange = (e) => {
        setForm(prevForm => ({
            ...prevForm,
            organization_id: e.value
        }))
    }
    useEffect(() => {
        dispatch(getOrganizations())
    }, []);
    useEffect(() => {
        if (!model) return
        setForm({
            id: model.id,
            name: model.name,
            organization_id: model.organization?.id
        })
    }, [model])

    if (!model) return
    return (
        <div className={"flex flex-col"}>

            <Box className={"w-full mt-4"}
                 title={"Edit Competition : " + model.name}
                 body={(
                     <div
                         className={"w-full flex flex-col p-4 !bg-gray-800 gap-4 border-gray-600 border-x border-b rounded-b-lg"}>
                         <InputText value={form.name}
                                    className={"border-gray-300 border p-3"}
                                    onChange={(value) => setForm({...form, name: value.target.value})}
                                    placeholder={"Please enter competition name"}/>

                             <SelectWrapper title={"Organization"}
                                            className={"bg-white border-gray-300 border rounded-md"}
                                            children={(
                                                <Select options={organizationOptions}
                                                        className={"p-2 min-w-[240px]"}
                                                        disabled={form.organization_id}
                                                        searchable={true}
                                                        selected={form.organization_id}
                                                        onChange={onOrganizationChange}
                                                />
                                            )}
                             />
                         <Button icon rounded className={"bg-green-200 text-white"} onClick={onUpdate}
                                 isLoading={isLoading}>
                             <CheckIcon color={"white"}/>
                             <Text>Save</Text>
                         </Button>
                     </div>
                 )}
            />
        </div>
    )
}
