import {forwardRef, useEffect, useRef, useState} from "react";
import {Text} from "./text";
import {Button} from "./button";
import {CheckCircleIcon, EmptyCircleIcon, TimesIcon} from "./icons";
import {parseInt} from "lodash";
import {useHotKeys} from "../_utils/hooks";


export const InputDescription = (props) => {
    const ref = useRef()
    const onChange = (e) => {
        props.onChange(e.target.value)
    }


    const onKeyUp = (e) => {
        if (e.key === 'Enter') {
            e.target.blur()
        }
    }

    return (
        <div className={"flex flex-col gap-2 border border-gray-300 rounded-lg p-2"}>
            <Text className={"text-gray-100"}>Description</Text>
            <textarea onKeyUp={onKeyUp} ref={ref} className={"flex-1 border border-gray-300 rounded-md"}
                      value={props.value}
                      onChange={onChange}></textarea>
            <Button
                className={"py-3 px-4 bg-gray-700 rounded-lg self-end"}
                onClick={props.onDelete}
            >
                <Text>Delete</Text>
            </Button>
        </div>
    )
}

export const InputRadio = (props) => {
    const className = "p-2  rounded-lg flex flex-row gap-2 items-center cursor-pointer " + props.className
    const name = props.name
    const selected = props.selected ?? false

    const onChange = e => props.onChange(!selected)

    // useHotKeys((e) => {
    //     switch (e.key) {
    //         case 'Enter':
    //             if (!props.tabIndex) return
    //             onChange(!selected)
    //             break;
    //     }
    // })

    const onKeyUp = (e) => {
        switch (e.key) {
            case 'Enter':
                if (!props.hasHotKey) return
                onChange(!selected)
                break;
        }
    }
    return (
        <div className={className + (selected ? " bg-blue-400" : " bg-gray-300")} onKeyUp={onKeyUp} onClick={onChange}
             tabIndex={props.tabIndex ?? -1}>
            {name && <Text>{name}</Text>}
            {selected
                ? (
                    <CheckCircleIcon/>
                )
                : (
                    <EmptyCircleIcon/>
                )}
        </div>
    )
}
export const InputMinute = (props) => {

    const disabled = props.disabled ?? false
    const className = "py-2  w-8 bg-transparent text-center " + props.className + (disabled ? " !bg-gray-500" : "")

    const onChange = (e) => {
        const value = enforceMinMax(e.target.value, e.target.min, e.target.max)
        props.onChange(value)
    }
    const onBlur = (e) => {
        const value = enforceMinMax(e.target.value, e.target.min, e.target.max)
        props.onFocusOut(value)
    }

    const onKeyUp = (e) => {
        const value = enforceMinMax(e.target.value, e.target.min, e.target.max)
        props.onChange(value, e.key)
    }


    const tabIndex = props.tabIndex

    const ref = useRef()

    const enforceMinMax = (value, min, max) => {
        let nextValue

        if (!value) return min
        if (parseInt(value) < parseInt(min)) {
            nextValue = min;
            return nextValue
        }

        if (parseInt(value) > parseInt(max)) {
            nextValue = max;
            return nextValue
        }


        if (parseInt(value).toString().length === 1) {
            nextValue = '0' + Number(value);
        } else {
            nextValue = parseInt(value)
        }


        ref.current.value = nextValue
        return nextValue
    }
    return (
        <input disabled={disabled}
               ref={ref}
               className={className}
               tabIndex={tabIndex ?? 0}
               value={props.value ?? "00"}
               onChange={onChange}
               onBlur={onBlur}
               onKeyUp={onKeyUp}
               type={"number"}
               inputMode={"numeric"}
               pattern={"/d*"}
               min={props.min}
               max={props.max}/>
    )
}

export const TextArea = (props) => {
    const className = "p-2 rounded-md border border-gray-600 " + props.className
    const onChange = (e) => {
        props.onChange(e.target.value)
    }
    const value = props.value ?? ""
    return (
        <textarea className={className} value={value} onChange={onChange} placeholder={props.placeholder}/>
    )
}
export const InputText = forwardRef(function InputText(props, ref) {

    const className = "p-2 rounded-md border border-gray-600 " + (props.className ?? "")
    const onChange = (e) => {
        props.onChange(e.target.value)
    }
    const value = props.value ?? ""
    return (
        <input ref={ref} className={className} type="text" value={value} onChange={onChange}
               placeholder={props.placeholder} {...props} />
    )


})

export const InputTextCopy = (props) => {
    const className = "rounded-md bg-white border border-gray-600 flex flex-row items-center justify-start " + props.className
    const label = props.label
    const [value, setValue] = useState(props.value ?? "")
    const id = props.id
    const onChange = (e) => {
        setValue(e.target.value)
        changeParent(e.target.value)
    }
    const resetValue = (e) => {
        setValue("")
        changeParent("")
    }
    const pasteValue = (e) => {
        setValue("")
        changeParent("")
    }

    const changeParent = (value) => props.onChange({
        id,
        label,
        value
    })
    return (
        <div className={className}>
            <Text className={" min-w-[140px] text-gray-100 p-4 font-semibold"}>{label}</Text>
            <input className={"outline-none bg-white p-4 font-size-16 flex-1"}
                   type="text"
                   value={value}
                   placeholder={props.placeholder ?? ""}
                   onChange={onChange}/>
            {value
                && <Button className={"p-4 ml-auto"} onClick={resetValue}>
                    <TimesIcon/>
                </Button>
            }
        </div>

    )
}
