import React, {useState} from "react";
import {Caption, Text, Title} from "./text";
import {Button} from "./button";
import {
    CommentIcon,
    DeleteIcon,
    EditIcon,
    EmptyStarIcon, FlagIcon,
    MoreIcon,
    PauseIcon,
    PlayIcon,
    SearchIcon,
    SortAscending, StarIcon
} from "./icons";
import {useEvent} from "../_utils/hooks";
import {useDispatch, useSelector} from "react-redux";
import {deleteHighlight, deleteHighlightDetail} from "../_api/event.api";
import {Spinner} from "./spinner";
import {getFullTimeSeconds, secondsToTime, secondsToTimeView, SubtractFullTimesPure} from "../_utils/helpers";
import {useNavigate} from "react-router-dom";
import {confirmToast, permissionToast} from "../_store/toast/toast.slice";
import {userHasPermission} from "../_hooks/user.hooks";

export const TeamHighlights = ({highlights, tags, handlePlaySingleDuration, isPlayingSingleDuration}) => {
    const [playedId, setPlayedId] = useState(null)
    return highlights.sort(highlight => highlight.i)
        .map((highlight, key) => <Highlight key={key}
                                            highlight={highlight}
                                            handlePlaySingleDuration={handlePlaySingleDuration}
                                            isPlayingSingleDuration={isPlayingSingleDuration}
                                            playedId={playedId}
                                            setPlayedId={setPlayedId}
        />)
}
export const Highlight = ({
                              highlight,
                              handlePlaySingleDuration,
                              isPlayingSingleDuration,
                              setPlayedId,
                              playedId,
                              withoutButtons
                          }) => {
    const userCanEditHighlight = userHasPermission('highlight-edit')
    const userCanDeleteHighlight = userHasPermission('highlight-delete')
    const event = useEvent()
    const navigate = useNavigate()
    const isStoringHighlight = useSelector(state => state.event.isStoringHighlight)
    const dispatch = useDispatch()
    const onDeleteHighlight = () => {
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this highlight?',
            callback: () => {
                dispatch(deleteHighlight({
                    eventId: event.id,
                    highlightId: highlight.id,
                    sectionId: highlight.section_id
                }))
            }
        }))
    }
    const handlePlayHighlight = () => {

        if (playedId === highlight.id && isPlayingSingleDuration) {
            setPlayedId(null)
        } else {
            setPlayedId(highlight.id)
        }

        handlePlaySingleDuration({
            start: secondsToTime(getFullTimeSeconds(highlight.section_start_time)),
            end: secondsToTime(getFullTimeSeconds(highlight.section_until_time)),
            highlight
        }, highlight.id)
    }

    const onEditHighlight = () => {
        navigate(`/v3/events/${event.id}/tagger/highlight/edit/${highlight.id}`)
    }
    const onDeleteDetailFromDatabase = (detail) => {
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this tag?',
            callback: () => {
                dispatch(deleteHighlightDetail({
                    eventId: event.id,
                    sectionId: highlight.section_id,
                    highlightId: highlight.id,
                    highlightDetailId: detail.id
                }))
            }
        }))
    }
    return (<div
        className={"bg-white flex-1 flex flex-col items-center justify-between p-2 rounded-2xl " + (playedId === highlight.id ? 'border border-blue-200 bg-blue-50' : 'border border-transparent')}>
        {isStoringHighlight && <Spinner/>}
        <div className={"w-full flex flex-row items-center justify-between"}>
            <div className={"flex flex-col gap-1"}>
                <div className={"flex flex-row items-center justify-between gap-2"}>
                    <Text className={"text-gray-200"}>Highlight {highlight.i}</Text>
                    {highlight.has_video_problem == 1 && <FlagIcon/>}
                    {highlight.is_starred == 1 && <StarIcon/>}
                </div>
                <div className={"flex flex-row items-center justify-center gap-2 self-start"}>
                    <Text className={"text-gray-200"}>
                        {secondsToTimeView(SubtractFullTimesPure(highlight.section_start_time, highlight.section_until_time))} -
                    </Text>
                    <Text
                        className={"font-extrabold text-gray-100"}>{secondsToTime(getFullTimeSeconds(highlight.section_start_time))}-{secondsToTime(getFullTimeSeconds(highlight.section_until_time))}</Text>
                </div>
            </div>
            <div className={"flex flex-row items-center justify-between gap-2"}>

                {!withoutButtons && (
                    <React.Fragment>
                        {userCanEditHighlight &&  <Button className={""} onClick={onEditHighlight}>
                            <EditIcon color={"#8B8C8F"}/>
                        </Button>}
                        {userCanDeleteHighlight &&  <Button className={"border-l border-gray-700"} onClick={onDeleteHighlight}
                                                            disabled={isStoringHighlight}>
                            <DeleteIcon color={"#8B8C8F"}/>
                        </Button>}
                        <Button className={"border-l border-gray-700 py-1"}
                                onClick={() => handlePlayHighlight()}
                        >
                            {!isPlayingSingleDuration ? <PlayIcon/> : (playedId === highlight.id ?
                                <PauseIcon color={"#2B87FF"}/> : <PlayIcon/>)}
                        </Button>
                    </React.Fragment>
                )}
                {/*<Button className={"border-l border-gray-700"}>*/}
                {/*    <MoreIcon color={"#8B8C8F"}/>*/}
                {/*</Button>*/}
            </div>
        </div>
        {highlight.details.map(((detail, key) => <HighlightDetail
            key={key}
            detail={detail}
            onDeleteFromDatabase={onDeleteDetailFromDatabase}
            withoutButtons={withoutButtons}
        />))}

        {highlight.comment &&
            <div className={"w-full my-2 rounded-lg flex flex-row items-center justify-start gap-2 text-gray-200"}>
                {/*<Caption >Highlight Comment:</Caption>*/}
                <Caption className={"!font-regular break-words max-w-[360px]"}>{highlight.comment}</Caption>
            </div>}
    </div>)
}

export const HighlightDetail = ({
                                    detail,
                                    selected,
                                    setTag,
                                    onDeleteFromDatabase,
                                    onDeleteFromDraft,
                                    withoutButtons
                                }) => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const selectedEvent = useEvent()

    const userCanEditHighlight = userHasPermission('highlight-edit')
    const userCanDeleteHighlightTag = userHasPermission('highlight-tag-delete')

    const onDeleteDetail = () => {

        if (detail.id) {
            if(!userCanDeleteHighlightTag){
                dispatch(permissionToast())
                return
            }
            onDeleteFromDatabase(detail)
        } else {
            onDeleteFromDraft(detail)
        }
    }
    const onEditTag = () => {
        if (detail.id) {
            navigate(`/v3/events/${selectedEvent.id}/tagger/highlight/edit/${detail.highlight_id}/${detail.id}`)
            return
        }
        if (detail.highlight_id) {
            navigate(`/v3/events/${selectedEvent.id}/tagger/highlight/edit/${detail.highlight_id}`)
        }
        setTimeout(() => {
            setTag(detail)
        }, 0)


    }
    if (!detail.id) {
        return (<div
            className={"bg-gray-800 rounded-[8px] w-full my-2 p-2 flex flex-col " + (selected && '!bg-gray-700 border border-blue-1-hold')}>
            <div className={"flex flex-row items-center justify-between w-full border-b border-gray-300 pb-2"}>
                <div className={"flex flex-col gap-1"}>
                    <Title>{detail.mainTag?.name} {detail.exactTime}</Title>
                    <div className={"flex flex-row gap-1"}>
                        {detail.subTags.map(((subTag, key) => <Caption key={key}
                                                                       className={'text-gray-200'}>{subTag.name}</Caption>))}
                    </div>
                </div>
                <div className={"flex flex-row items-center justify-center self-start"}>

                    {!withoutButtons && (
                        <React.Fragment>
                            {userCanEditHighlight && <Button className={"border-r border-gray-700 py-0 px-2"}
                                     onClick={onEditTag}>
                                <EditIcon color={"#5E6163"}/>
                            </Button>}
                            {userCanDeleteHighlightTag && <Button className={"py-0 px-2"}
                                     onClick={onDeleteDetail}
                            >
                                <DeleteIcon color={"#5E6163"}/>
                            </Button>}
                        </React.Fragment>
                    )}
                </div>
            </div>
            <div className={"flex flex-row items-center justify-between border-b border-gray-300 py-2"}>
                <Caption
                    className={'text-gray-200'}>Player:</Caption>
                <Caption
                    className={'text-gray-200'}>{detail.selectedPlayer?.full_name_with_data}</Caption>
            </div>
            <div className={"flex flex-row items-center justify-between py-2"}>
                <Caption
                    className={'text-gray-200'}>Team:</Caption>
                <Caption
                    className={'text-gray-200'}>{detail.selectedPlayer?.team_name}</Caption>
            </div>
            {detail.comment && (
                <div className={"flex flex-row items-center justify-between  border-t border-gray-300 py-2"}>
                    <Caption
                        className={'text-gray-200'}>Comment:</Caption>
                    <Caption
                        className={'text-gray-200 max-w-[240px]'}>{detail.comment}</Caption>
                </div>
            )}
        </div>)
    }
    return (<div
        className={"bg-gray-800 rounded-[8px] w-full my-1 p-2 flex flex-col " + (selected && '!bg-gray-700 border border-blue-1-hold')}>
        <div className={"flex flex-row items-center justify-between w-full border-b border-gray-300 pb-2"}>
            <div className={"flex flex-col gap-1"}>
                <div className={"flex flex-row items-center justify-center gap-2"}>
                    <Title>{detail.mainTag?.name}</Title>
                    <Caption>{detail.exactTime}</Caption>
                </div>
                <div className={"flex flex-row gap-1"}>
                    {detail.subTags.map(((subTag, key) => <Caption key={key}
                                                                   className={'text-gray-200'}>{subTag.name}</Caption>))}
                </div>
            </div>
            <div className={"flex flex-row items-center justify-center self-start"}>

                {!withoutButtons && (
                    <React.Fragment>
                        {userCanEditHighlight && <Button className={"border-r border-gray-700 py-0 px-2"}
                                 onClick={onEditTag}>
                            <EditIcon color={"#5E6163"}/>
                        </Button>}
                        {userCanDeleteHighlightTag && <Button className={"py-0 px-2"}
                                 onClick={onDeleteDetail}
                        >
                            <DeleteIcon color={"#5E6163"}/>
                        </Button>}
                    </React.Fragment>
                )}
            </div>
        </div>
        <div className={"flex flex-row items-center justify-between border-b border-gray-300 py-2"}>
            <Caption
                className={'text-gray-200'}>Player:</Caption>
            <Caption
                className={'text-gray-200'}>{detail.selectedPlayer?.full_name_with_data}</Caption>
        </div>
        <div className={"flex flex-row items-center justify-between py-2"}>
            <Caption
                className={'text-gray-200'}>Team:</Caption>
            <Caption
                className={'text-gray-200'}>{detail.selectedPlayer?.team_name}</Caption>
        </div>
        {detail.comment && (
            <div className={"flex flex-row items-center justify-between  border-t border-gray-300 py-2"}>
                <Caption
                    className={'text-gray-200'}>Comment:</Caption>
                <Caption
                    className={'text-gray-200 break-words max-w-[240px]'}>{detail.comment}</Caption>
            </div>
        )}
    </div>)
}
