
import React, {useEffect, useState} from "react";
import {useEvent, useEventPlayers, useTeamEventPlayers, useTeamPlayerOptions, useTeamPlayers} from "../_utils/hooks";
import {Button} from "../_components/button";
import {ReplaceIcon} from "../_components/icons";
import {Text} from "../_components/text";
import {Select} from "../_components/select";
import {useDispatch, useSelector} from "react-redux";
import {confirmToast} from "../_store/toast/toast.slice";
import {fetchEventById, replacePlayer} from "../_api/event.api";
import {useNavigate} from "react-router-dom";


const ReplacePlayerForm = ({player}) => {
    const selectedEvent = useEvent()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const teamPlayerOptions = useTeamPlayerOptions(
        useTeamEventPlayers(player.teamId).filter(item=>item.id !== player.id)
    )
    const [replaceForm,setReplaceForm] = useState({
        playerId: "",
        replacedPlayerId: ""
    })
    const isReplacingPlayer = useSelector(state => state.event.isReplacingPlayer)
    const [replacePlayerIsOpen, setReplacePlayerIsOpen] = useState(false)

    const onPlayerChange = (e) => {
        let newForm = {
            ...replaceForm,
            replacedPlayerId: e.value
        }
        setReplaceForm(newForm)
        dispatch(confirmToast({
            message: `Are you sure you want to move all tags from "${player.full_name_with_data}" to "${e.name}" ?`,
            callback: () => {
                dispatch(replacePlayer({
                    eventId: selectedEvent.id,
                    ...newForm
                }))
                dispatch(fetchEventById(selectedEvent.id))
                navigate(`/v3/events/${selectedEvent.id}/tagger/player`)

            }
        }))

    }
    useEffect(() => {
        setReplaceForm(prevForm=>({
            ...prevForm,
            playerId: player.id
        }))

    }, [player.id]);

    return (
        <React.Fragment>
            <Button
                className={"w-full bg-white p-4 mt-4 rounded-md " + (replacePlayerIsOpen ? "!bg-blue-400" : "")}
                icon={true}
                loading={isReplacingPlayer}
                onClick={() => setReplacePlayerIsOpen(!replacePlayerIsOpen)}
            >
                <ReplaceIcon/>
                <Text className={"text-blue-100"}>Replace</Text>
            </Button>
            {replacePlayerIsOpen && (
                <Select
                    disabled={isReplacingPlayer}
                    className={"bg-white p-2 py-4 w-full rounded-md mt-4"}
                    options={teamPlayerOptions.filter(item => Number(item.value) !== Number(replaceForm.playerId))}
                    searchable={true}
                    onChange={onPlayerChange}
                />
            )}
        </React.Fragment>
    )
}
export default ReplacePlayerForm
