import React, {useCallback, useEffect, useRef, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {computedTime, getTimeSeconds, sectionFilterFn} from "./helpers";
import {selectSection, setSectionDuration} from "../_store/event/event.slice";
import {Caption, Text} from "../_components/text";
import {Button} from "../_components/button";
import {
    BackFiveSecondIcon,
    BackOneSecondIcon,
    ForwardFiveSecondIcon,
    ForwardOneSecondIcon,
    OneXIcon,
    PauseIcon,
    PlayIcon,
    VolumeIcon
} from "../_components/icons";
import {useEvent} from "./hooks";


export const VideoHandleBar = (props) => {
    const {
        onChangeDuration,
        currentSection,
        currentTime,
        handleRewind,
        handleForward,
        handlePlaybackSpeed,
        handlePlay,
        isPlaying,
        playerBackSpeed,
        isPlayingSingle,
        singleDuration,
    } = props
    return (
        <div className={"w-full absolute bottom-0 flex flex-col"} style={{
            background: isPlayingSingle ? 'linear-gradient(180deg, #0A3D8000 0%,  #0A3D80 100%)' : 'linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%)'
        }}>
            <div
                className={"flex-1 p-3 min-h-12 flex flex-col align-items-center justify-center gap-1"}>
                <input tabIndex={-1} className={""} type="range" min="0" value={currentTime} onChange={onChangeDuration}
                       max={currentSection.video_duration}/>
                <div className={"flex flex-row items-center justify-between"}>
                    <div className={"flex flex-row items-center justify-between gap-2"}>
                        {(isPlayingSingle && (singleDuration.start >= 0 || singleDuration.start.length > 0)) &&
                            <Caption className={"text-white"}>{computedTime(typeof singleDuration.start === "number" ? singleDuration.start : getTimeSeconds(singleDuration.start))}</Caption>}
                        <Text className={"text-white"}>{computedTime(currentTime)}</Text>

                    </div>
                    <div className={"flex flex-row items-center justify-between gap-2"}>
                        <Text className={"text-white"}>{computedTime(currentSection.video_duration)}</Text>
                        {(isPlayingSingle && (singleDuration.end >= 0 || singleDuration.end.length > 0)) &&
                            <Caption className={"text-white"}>{computedTime(typeof singleDuration.end === "number" ? singleDuration.end : getTimeSeconds(singleDuration.end))}</Caption>}
                    </div>
                </div>
            </div>
            <div
                className={"flex-1 flex items-center justify-between"}>
                <Button className={"p-3"}>
                    <VolumeIcon/>
                </Button>
                <div className={"flex flex-row gap-2 items-center"}>
                    <Button className={"p-3"} onClick={() => handleRewind(1)}>
                        <BackOneSecondIcon color={"white"}/>
                    </Button>
                    <Button className={"p-3"} onClick={() => handleRewind(5)}>
                        <BackFiveSecondIcon color={"white"}/>
                    </Button>
                    <Button className={"p-3"} onClick={handlePlay}>
                        {isPlaying ? <PauseIcon color={"white"}/> : <PlayIcon color={"white"}/>}
                    </Button>
                    <Button className={"p-3"} onClick={() => handleForward(5)}>
                        <ForwardFiveSecondIcon color={"white"}/>
                    </Button>
                    <Button className={"p-3"} onClick={() => handleForward(1)}>
                        <ForwardOneSecondIcon color={"white"}/>
                    </Button>
                </div>
                <div className={"flex items-center justify-center gap-2"}>
                    <Button className={"p-3"} onClick={handlePlaybackSpeed}>
                        <OneXIcon color={"white"} speed={playerBackSpeed}/>
                    </Button>
                </div>
            </div>
        </div>
    )
}

export const useVideoDimensions = () => {
    const [dimensions, setDimensions] = useState({
        width: 0,
        height: 0,
    })
    const onLoadedData = (e) => {
        const videoWidth = e.target.videoWidth
        const videoHeight = e.target.videoHeight
        const aspectRatio = videoWidth / videoHeight

        const windowHeight = window.innerHeight
        const windowWidth = window.innerWidth
        let newWidth, newHeight;

        if (windowWidth / windowHeight < aspectRatio) {
            newWidth = windowWidth;
            newHeight = windowWidth / aspectRatio;
        } else {
            newHeight = windowHeight;
            newWidth = windowHeight * aspectRatio;
        }
        setDimensions({
            width: newWidth,
            height: newHeight
        })
    }
    return [dimensions, onLoadedData]
}

export const useVideoHandles = (props) => {
    const [isPlaying, setIsPlaying] = useState(false)
    const {selectedSection} = useSelector(state=>state.event)

    const videoRef = props.videoRef
    const handlePause = () => {
        if (isPlaying) {
            videoRef.current.pause()
            setIsPlaying(false)
        }
    }
    const handlePlay = (e) => {
        if (isPlaying) {
            handlePause(e)
            return
        }
        videoRef.current.play()
        setIsPlaying(true)
    }
    const handleRewind = (amount) => {
        if(videoRef.current.currentTime - amount <= getTimeSeconds(selectedSection.video_start_time)){
            return
        }
        videoRef.current.currentTime -= amount
        props.setCurrentTime(prev => prev - amount)
    }
    const handleForward = (amount) => {
        if(videoRef.current.currentTime + amount >= getTimeSeconds(selectedSection.video_end_time)){
            return
        }
        videoRef.current.currentTime += amount
        props.setCurrentTime(prev => prev + amount)

    }
    const handlePlaybackSpeed = () => {
        switch (videoRef.current.playbackRate) {
            case 0.5:
                videoRef.current.playbackRate = 0.75
                break;
            case 0.75:
                videoRef.current.playbackRate = 1
                break;
            case 1:
                videoRef.current.playbackRate = 1.25
                break;
            case 1.25:
                videoRef.current.playbackRate = 1.5
                break;
            case 1.5:
                videoRef.current.playbackRate = 1.75
                break
            case 1.75:
                videoRef.current.playbackRate = 2
                break
            case 2.0:
                videoRef.current.playbackRate = 0.5
        }
    }



    return {isPlaying, setIsPlaying, handlePlay, handlePause, handleRewind, handleForward, handlePlaybackSpeed}
}

export const useEventVideos = (props) => {
    const dispatch = useDispatch()
    const [loaded, setLoaded] = useState([])
    const [isLoaded, setIsLoaded] = useState(false)
    const [sectionArray, setSectionArray] = useState([])
    const [currentTime, setCurrentTime] = useState('00')

    const sections = props.sections
    const videoRef = props.videoRef

    const createSectionsArray = () => {
        let array = []
        sections.filter(sectionFilterFn).forEach((section, key) => {
            if (!section.video_url) return
            if (key === 0) {
                array.push({
                    id: section.id,
                    name: section.name,
                    sort: key,
                    start_at: 0,
                    end_at: section.real_video_end_at - section.real_video_start_at,
                    real_duration: section.real_video_duration
                })
                return
            }
            array.push({
                id: section.id,
                name: section.name,
                sort: key,
                start_at: 0 + array[key - 1].end_at - array[key - 1].start_at,
                end_at: array[key - 1].end_at - array[key - 1].start_at + section.real_video_end_at - section.real_video_start_at,
                real_duration: section.real_video_duration
            })
        })
        return array
    }
    const calculateFullMatchDuration = useCallback(async () => {
            for (const section of sections.filter(sectionFilterFn)) {
                let tempVideo = document.createElement('video');
                tempVideo.preload = "metadata"; // Only load metadata
                tempVideo.src = section.video_url;
                await new Promise((resolve) => {
                    tempVideo.addEventListener("loadedmetadata", (event) => {
                        let duration = getTimeSeconds(section.video_end_time) - getTimeSeconds(section.video_start_time);
                        dispatch(setSectionDuration({
                            id: section.id,
                            duration,
                            realDuration: Number(tempVideo.duration)
                        }));
                        setLoaded(prevLoaded => {
                            let newLoaded = [...prevLoaded];
                            newLoaded.push(section.id);
                            return newLoaded;
                        });
                        tempVideo.remove();
                        resolve(); // Resolve the promise when metadata is loaded
                    });
                });
                await new Promise(resolve => setTimeout(resolve, 100)); // Wait for 100 ms
            }
        }
    ,[sections])
    const goToVideoTime = (seconds) => setCurrentTime(seconds)


    const loadSection = (sectionArrayItem, sourceUrl = undefined, duration = undefined) => {
        setIsLoaded(false)
        const section = sections.find(section => section.id === sectionArrayItem.id)
        dispatch(selectSection(section.id))
        if (sourceUrl) {
            videoRef.current.src = sourceUrl
        } else {
            if (videoRef.current.src !== section.video_url) {
                videoRef.current.src = section.video_url
            }
        }
        videoRef.current.load()
        setIsLoaded(true)

        const durationToLookFor = duration ? Number(duration) : 0

        videoRef.current.currentTime = section.real_video_start_at + durationToLookFor
        goToVideoTime(durationToLookFor)
    }

    useEffect(() => {
        if (loaded.length === 0) {
            dispatch(selectSection(sections[0].id))
            calculateFullMatchDuration()
        }
        if (loaded.length === sections.filter(sectionFilterFn).length) {
            setLoaded([
                ...loaded,
                "toBe"
            ])
        }
        if (loaded.length === sections.filter(sectionFilterFn).length + 1) {
            setSectionArray(createSectionsArray)
            setIsLoaded(true)
        }

    }, [loaded]);


    return {isLoaded, sectionArray, currentTime, setCurrentTime, loadSection, setIsLoaded}
}
