import {createSlice} from '@reduxjs/toolkit'


const initialState = {
    element: null,
    elementProps: null,
    title: null,
    isOpen: false,
    resetSignal: false,
}

export const modalSlice = createSlice({
    name: 'modal',
    initialState,
    reducers: {
        openModal: (state, action) => {
            state.element = action.payload.element
            state.elementProps = action.payload.props
            state.title = action.payload.title
            state.isOpen = true;
        },
        closeModal: (state) => {
            state.isOpen = false;
            state.element = null
            state.elementProps = null
            state.title = null
            state.resetSignal = false;
        },
        setResetSignal: (state, action) => {
            state.resetSignal = action.payload
        }
    },

})

export const {openModal, closeModal,setResetSignal} = modalSlice.actions

export default modalSlice.reducer
