import {useDispatch, useSelector} from "react-redux";
import React, {useState} from "react";
import {closeModal, setResetSignal} from "../_store/modal/modal.slice";
import {errorToast} from "../_store/toast/toast.slice";
import {changeHighlightDownloadLink, storeCustomHighlight} from "../_api/event.api";
import {Title} from "../_components/text";
import {InputDescription, InputText, InputTextCopy, TextArea} from "../_components/input";
import {ModalButtons} from "../_components/modal";

export const ChangeHighlightLinkModal = () => {
    const dispatch = useDispatch()
    const modal = useSelector(state => state.modal)
    const props = modal.elementProps
    const [form, setForm] = useState({
        highlight_id: props.highlight.id,
        eventId: props.eventId,
        download_url: props.highlight.download_url
    })
    const {isLoading} = useSelector(state => state.event.isLoading)
    const onClose = () => dispatch(closeModal())
    const onDone = () => {
        if (!form.download_url) {
            dispatch(errorToast('Please input a download URL'))
            return
        }
        dispatch(changeHighlightDownloadLink(form))

    }
    return (
        <React.Fragment>
            <Title className={"p-4 border-b border-gray-600"}>{modal.title}</Title>
            <div className={"flex flex-col p-4 gap-2"}>
                <InputTextCopy
                    label={"Download Url"}
                    value={form.download_url}
                    placeholder={"Please input your generated VEO Download URL"}
                    onChange={(v) => setForm({...form,download_url:v.value})}
                />

            </div>
            <ModalButtons isLoading={isLoading} onClose={onClose} onDone={onDone}/>

        </React.Fragment>
    )
}
