import {useEffect} from "react";
import {useDispatch} from "react-redux";
import {getAuth} from "../_api/auth.api";
import {useAuth} from "../_utils/hooks";

export const UserProvider = () => {
    const dispatch = useDispatch()
    const auth = useAuth()
    useEffect(() => {
        if(auth.user) return
        dispatch(getAuth())
    }, []);
}
