export const Title = (props) => {
    const classList = "text-base leading-5 font-semibold " + props.className;
    return (
        <p className={classList}>{props.children}</p>
    )
}

export const Caption = (props) => {
    const classList = "text-xs leading-[14px] font-bold " + props.className;

    return (
        <p className={classList} >{props.children}</p>
    )
}
export const Text = (props) => {
    const classList = "text-sm leading-[18px] " + props.className;

    return (
        <p className={classList} >{props.children}</p>
    )
}

