import {createSlice} from '@reduxjs/toolkit'
import {fetchEventById, setEventStatusById} from "@tagger/api/event.api";
import {
    addPlayer,
    changeHighlightDescription,
    changeHighlightDownloadLink,
    deleteCustomHighlight,
    deleteHighlight,
    deleteHighlightDetail,
    deletePlayer,
    deleteSection, replacePlayer,
    separateHighlights,
    setEventTeamColor,
    storeCustomHighlight,
    storeHighlight,
    storeSection,
    updateHighlight,
    updateSection
} from "../../_api/event.api";
import {getTimeSeconds} from "../../_utils/helpers";


const initialState = {
    events: [],
    selectedEvent: null,
    selectedSection: null,
    isLoading: true,
    isAddingPlayer: false,
    isReplacingPlayer: false,
    isStoringHighlight: false,
}

export const eventSlice = createSlice({
    name: 'event',
    initialState,
    reducers: {
        setSectionDuration: (state, action) => {
            let sectionToEdit = state.selectedEvent.sections.find(section => action.payload.id === section.id)
            if (!sectionToEdit) return
            sectionToEdit.video_duration = Math.round(action.payload.duration)
            sectionToEdit.real_video_duration = Math.round(action.payload.realDuration)
            sectionToEdit.real_video_start_at = getTimeSeconds(sectionToEdit.video_start_time)
            sectionToEdit.real_video_end_at = getTimeSeconds(sectionToEdit.video_end_time)

        },
        selectSection: (state, action) => {
            state.selectedSection = state.selectedEvent.sections.find(s => s.id === action.payload)
        },
        getEvent: (state, action) => {
            state.selectedEvent = 1
        },

    },
    extraReducers: (builder) => {
        // Fetch Event By Id
        builder.addCase(fetchEventById.fulfilled, (state, action) => {
            state.selectedEvent = action.payload
            state.isLoading = false;
        })
        builder.addCase(fetchEventById.pending, (state, action) => {
            state.isLoading = true;
        })
        // Set Event Status
        builder.addCase(setEventStatusById.fulfilled, (state, action) => {
            state.selectedEvent.status = action.payload
            state.isLoading = false;

        })
        builder.addCase(setEventStatusById.pending, (state, action) => {
            state.isLoading = true;
        })
        // Set Event Status
        builder.addCase(setEventTeamColor.fulfilled, (state, action) => {
            state.selectedEvent[action.payload.team].color = action.payload.color
            state.isLoading = false;

        })
        builder.addCase(setEventTeamColor.pending, (state, action) => {
            state.isLoading = true;
        })
        // Store Section
        builder.addCase(storeSection.fulfilled, (state, action) => {
            state.selectedEvent.sections = action.payload
            state.selectedEvent.sections.sort((prev, next) => prev.sort > next.sort)
            state.isLoading = false;

        })
        builder.addCase(storeSection.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(storeSection.rejected, (state, action) => {
            state.isLoading = false;
        })

        // Update Section
        builder.addCase(updateSection.fulfilled, (state, action) => {
            const foundIndex = state.selectedEvent.sections.findIndex(section => action.payload.id === section.id)
            state.selectedEvent.sections[foundIndex] = action.payload
            state.isLoading = false;

        })
        builder.addCase(updateSection.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(updateSection.rejected, (state, action) => {
            state.isLoading = false;
        })
        // Delete Section
        builder.addCase(deleteSection.fulfilled, (state, action) => {
            const foundIndex = state.selectedEvent.sections.findIndex(section => action.payload.id === section.id)
            state.selectedEvent.sections.splice(foundIndex, 1)
            state.isLoading = false;
        })
        builder.addCase(deleteSection.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(deleteSection.rejected, (state, action) => {
            state.isLoading = false;
        })
        // Add Player
        builder.addCase(addPlayer.fulfilled, (state, action) => {
            state.isAddingPlayer = false;
            const playerToInsert = action.payload
            let teamIndex = state.selectedEvent.homeTeam.id === playerToInsert.team_id ? 'homeTeam' : 'awayTeam'

            const foundIndex = state.selectedEvent[teamIndex].players.findIndex(p => Number(p.id) === Number(playerToInsert.id))
            if (foundIndex > -1) {
                state.selectedEvent[teamIndex].players[foundIndex] = playerToInsert
                return
            }
            state.selectedEvent[teamIndex].players.push(playerToInsert)

        })
        builder.addCase(addPlayer.pending, (state, action) => {
            state.isAddingPlayer = true;
        })
        builder.addCase(addPlayer.rejected, (state, action) => {
            state.isAddingPlayer = false;
        })
        // Delete Player
        builder.addCase(deletePlayer.fulfilled, (state, action) => {
            state.isAddingPlayer = false;
            const playerToDelete = action.payload
            let teamIndex = state.selectedEvent.homeTeam.id === playerToDelete.team_id ? 'homeTeam' : 'awayTeam'

            const foundIndex = state.selectedEvent[teamIndex].players.findIndex(p => Number(p.id) === Number(playerToDelete.id))
            if (foundIndex > -1) {
                state.selectedEvent[teamIndex].players.splice(foundIndex, 1)
            }

        })
        builder.addCase(deletePlayer.pending, (state, action) => {
            state.isAddingPlayer = true;
        })
        builder.addCase(deletePlayer.rejected, (state, action) => {
            state.isAddingPlayer = false;
        })
        // Replace Player
        builder.addCase(replacePlayer.fulfilled, (state, action) => {
            state.isReplacingPlayer = false;

        })
        builder.addCase(replacePlayer.pending, (state, action) => {
            state.isReplacingPlayer = true;
        })
        builder.addCase(replacePlayer.rejected, (state, action) => {
            state.isReplacingPlayer = false;
        })
        // Store Highlight
        builder.addCase(storeHighlight.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const storedHighlight = action.payload
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === storedHighlight.section_id)
            state.selectedEvent.sections[sectionIndex].highlights.push({
                ...storedHighlight,
                // i: state.selectedEvent.total_highlights + 1
            })
            state.selectedEvent.total_highlights += 1

        })
        builder.addCase(storeHighlight.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(storeHighlight.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Update Highlight
        builder.addCase(updateHighlight.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const updatedHighlight = action.payload
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === updatedHighlight.section_id)
            const highlightIndex = state.selectedEvent.sections[sectionIndex].highlights.findIndex(highlight => highlight.id === updatedHighlight.id)
            const prevHighlightIter = state.selectedEvent.sections[sectionIndex].highlights[highlightIndex].i
            state.selectedEvent.sections[sectionIndex].highlights[highlightIndex] = {
                ...updatedHighlight,
                // i: prevHighlightIter
            }

        })
        builder.addCase(updateHighlight.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(updateHighlight.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Change Highlight Description
        builder.addCase(changeHighlightDescription.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const updatedHighlight = action.payload
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === updatedHighlight.section_id)
            const highlightIndex = state.selectedEvent.sections[sectionIndex].highlights.findIndex(highlight => highlight.id === updatedHighlight.id)
            state.selectedEvent.sections[sectionIndex].highlights[highlightIndex] = {
                ...updatedHighlight,
            }
        })
        builder.addCase(changeHighlightDescription.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(changeHighlightDescription.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Change Highlight Download URL
        builder.addCase(changeHighlightDownloadLink.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const updatedHighlight = action.payload
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === updatedHighlight.section_id)
            const highlightIndex = state.selectedEvent.sections[sectionIndex].highlights.findIndex(highlight => highlight.id === updatedHighlight.id)
            state.selectedEvent.sections[sectionIndex].highlights[highlightIndex] = {
                ...updatedHighlight,
            }
        })
        builder.addCase(changeHighlightDownloadLink.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(changeHighlightDownloadLink.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Delete Highlight
        builder.addCase(deleteHighlight.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === action.payload.section_id)
            const deletedHighlightIndex = state.selectedEvent.sections[sectionIndex].highlights.findIndex(highlight => highlight.id === action.payload.highlight_id)
            state.selectedEvent.sections[sectionIndex].highlights.splice(deletedHighlightIndex, 1)

        })
        builder.addCase(deleteHighlight.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(deleteHighlight.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Delete Highlight Detail
        builder.addCase(deleteHighlightDetail.fulfilled, (state, action) => {
            state.isStoringHighlight = false;
            const sectionIndex = state.selectedEvent.sections.findIndex(section => section.id === action.payload.section_id)
            const highlightIndex = state.selectedEvent.sections[sectionIndex].highlights.findIndex(highlight => highlight.id === action.payload.highlight_id)
            const deletedHighlightDetailIndex = state.selectedEvent.sections[sectionIndex].highlights[highlightIndex].details.findIndex(detail => detail.id === action.payload.highlight_detail_id)
            state.selectedEvent.sections[sectionIndex].highlights[highlightIndex].details.splice(deletedHighlightDetailIndex, 1)

            if (action.payload.highlight_deleted === true) {
                state.selectedEvent.sections[sectionIndex].highlights.splice(highlightIndex, 1)
            }
        })
        builder.addCase(deleteHighlightDetail.pending, (state, action) => {
            state.isStoringHighlight = true;
        })
        builder.addCase(deleteHighlightDetail.rejected, (state, action) => {
            state.isStoringHighlight = false;
        })
        // Store Custom Highlight
        builder.addCase(storeCustomHighlight.fulfilled, (state, action) => {
            state.isLoading = false;
            state.selectedEvent.clips.push(action.payload)
        })
        builder.addCase(storeCustomHighlight.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(storeCustomHighlight.rejected, (state, action) => {
            state.isLoading = false;
        })
        // Separate Highlights
        builder.addCase(separateHighlights.fulfilled, (state, action) => {
            state.isLoading = false;
        })
        builder.addCase(separateHighlights.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(separateHighlights.rejected, (state, action) => {
            state.isLoading = false;
        })
        // Delete Custom Highlight
        builder.addCase(deleteCustomHighlight.fulfilled, (state, action) => {
            state.isLoading = false;
            const deletedId = action.payload.id
            const foundIndex = state.selectedEvent.clips.findIndex(clip => clip.id === Number(deletedId))
            if (foundIndex > -1) {
                state.selectedEvent.clips.splice(foundIndex, 1)
            }
        })
        builder.addCase(deleteCustomHighlight.pending, (state, action) => {
            state.isLoading = true;
        })
        builder.addCase(deleteCustomHighlight.rejected, (state, action) => {
            state.isLoading = false;
        })
    },
})

export const {setSectionDuration, selectSection} = eventSlice.actions

export default eventSlice.reducer
