import React, {useEffect, useRef, useState} from "react";
import {Button} from "../_components/button";
import {

    PanelSideIcon, PlusIcon,

} from "../_components/icons";
import {Route, Routes, useLocation, useNavigate} from "react-router-dom";
import {IndexHighlight} from "../_routes/events/tagger/highlight/index.highlight.route";
import {CreateHighlight} from "../_routes/events/tagger/highlight/create.highlight.route";
import {EditHighlight} from "../_routes/events/tagger/highlight/edit.highlight.route";
import {activeElementIsInput, useEvent, useHotKeys} from "../_utils/hooks";


export const TaggerSidebar = ({
                                  videoHandles, currentTime, changeTime, height, getSectionVideo,
                                  handlePlaySingleDuration,
                                  isPlayingSingleDuration
                              }) => {
    const navigate = useNavigate()
    const [isOpen, setIsOpen] = useState(false)
    const location = useLocation()

    const isPressed = useHotKeys((e) => {
            if (!activeElementIsInput()) {
                switch (e.key) {
                    case 'ArrowRight':
                        videoHandles.handleForward(5)
                        break
                    case 'ArrowLeft':
                        videoHandles.handleRewind(5)
                        break;
                    case 'ArrowUp':
                        if (e.ctrlKey) {
                            videoHandles.handleForward(0.03)
                            return
                        }
                        videoHandles.handleForward(1)
                        break
                    case 'ArrowDown':
                        if (e.ctrlKey) {
                            videoHandles.handleRewind(0.03)
                            return
                        }
                        videoHandles.handleRewind(1)
                        break;

                    // case 'l':
                    //     // if (!isOpen) {
                    //     handleOpen()
                    //     // }
                    //     break;
                    // case 'x':
                    //     // if (isOpen) {
                    //     handleClose()
                    //     // }
                    //     break;
                    // case 'Enter':
                    case '+':
                        if (!isOpen || !location.pathname.includes('create')) {
                            videoHandles.handlePause()
                            navigate('create')
                            handleOpen()
                        }
                        break;
                    // case 'g':
                    // case 'p':
                    // case 'a':
                    // case 'd':
                    // case 's':
                    // case 'f':
                    //     // if (!isOpen) {
                    //         if (!location.pathname.includes('edit')) {
                    //     navigate('create')
                    //     }
                    //     handleOpen()
                    //     // }
                    //
                    //     break
                    case 'Escape':
                        // if (isOpen) {
                        handleClose()
                        // }
                        break
                    case ' ':
                        if (videoHandles.isPlaying) {
                            videoHandles.handlePause()
                        } else {
                            videoHandles.handlePlay()
                        }
                        break;
                }
            }
        }
    )
    const handleClose = () => {
        if (!isOpen) return
        setIsOpen(false)
    }
    const handleOpen = () => {
        if (isOpen) return
        setIsOpen(true)
    }
    useEffect(() => {

    }, [videoHandles.isPlaying]);


    return (
        <div className={"flex flex-row items-center justify-between gap-4 absolute top-0 right-0 z-20"}>
            <div className={"flex flex-row items-center justify-between gap-4 absolute top-4 right-4 z-20"}>

                <Button
                    className={"bg-[#00000080] rounded-full items-center flex justify-center p-3"}
                    onClick={() => {
                        videoHandles.handlePause()
                        handleOpen()
                        navigate('create')
                    }}
                >
                    <PlusIcon color={"white"}/>
                </Button>
                <Button
                    className={"bg-[#00000080] rounded-full items-center flex justify-center p-3"}
                    onClick={() => {
                        handleOpen()
                    }}
                >
                    <PanelSideIcon/>
                </Button>
            </div>

            <div
                className={"absolute top-3 right-0 bg-[#EDEEF0B2] min-w-[482px] z-30 rounded-l-2xl flex flex-col border-b border-gray-600 overflow-y-auto " + (!isOpen && 'hidden opacity-0')}
                style={{
                    backdropFilter: 'blur(2px)',
                    boxShadow: '-4px 0px 17px 0px #00000033',
                    minHeight: Math.round(height - 32) + 'px',
                    maxHeight: Math.round(height - 32) + 'px',
                }}
            >
                <div className={"p-4 flex flex-col gap-4"}>
                    <Routes>
                        <Route path={''} index element={<IndexHighlight
                            videoHandles={videoHandles}
                            handlePlaySingleDuration={handlePlaySingleDuration}
                            isPlayingSingleDuration={isPlayingSingleDuration}
                            setIsOpen={setIsOpen}/>}
                        />
                        <Route path={'create'} index
                               element={<CreateHighlight
                                   isOpen={isOpen}
                                   handlePlaySingleDuration={handlePlaySingleDuration}
                                   isPlayingSingleDuration={isPlayingSingleDuration}
                                   currentTime={currentTime}
                                   changeTime={changeTime}
                                   handleClose={handleClose}
                                   handleOpen={handleOpen}
                               />}
                        />
                        <Route path={'edit/:highlightId/:tagId?'}
                               element={<EditHighlight
                                   getSectionVideo={getSectionVideo}
                                   handlePlaySingleDuration={handlePlaySingleDuration}
                                   isPlayingSingleDuration={isPlayingSingleDuration}
                                   currentTime={currentTime}
                                   changeTime={changeTime}
                                   handleClose={handleClose}
                                   handleOpen={handleOpen}
                               />}
                        />
                    </Routes>
                </div>
            </div>
        </div>)
}




