import {useDispatch} from "react-redux";
import {useNavigate} from "react-router-dom";
import {usePlayer, useTeams} from "../../../../_utils/hooks";
import React, {useEffect, useRef, useState} from "react";
import {Caption, Text, Title} from "../../../../_components/text";
import {Button} from "../../../../_components/button";
import {PanelSideOpenIcon, PlusIcon, SearchIcon} from "../../../../_components/icons";
import {Team} from "../../../../_components/team";
import {openModal} from "../../../../_store/modal/modal.slice";
import {InputText} from "../../../../_components/input";
import {PlayerDetailCard} from "../../../../_components/card";
import {userHasPermission} from "../../../../_hooks/user.hooks";
import {NotAuthorizedComponent} from "../../../../_components/not-authorized.component";
import {permissionToast} from "../../../../_store/toast/toast.slice";

export const IndexPlayer = ({setIsOpen, setSelectedTeam, selectedTeam}) => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userCanAddPlayer = userHasPermission('player-create')
    const userCanListPlayer = userHasPermission('player-list')
    const userCanEditPlayer = userHasPermission('player-edit')
    const {homeTeam, awayTeam} = useTeams()
    // const [selectedTeam, setSelectedTeam] = useState({})
    const [searchMenuIsOpen, setSearchMenuIsOpen] = useState(false)
    const [searchValue, setSearchValue] = useState("")
    const handleSearch = (e) => {
        setSearchValue(e.target.value.toString().toLowerCase().replaceAll(' ', ''))
    }
    const searchFn = (item) => {
        if (searchValue === "") return true
        return item.full_name_with_data.toString().toLowerCase().replaceAll(' ', '').includes(searchValue)
    }
    const playersWithoutData = () => selectedTeam
        .players
        .filter(player => {
            if (player.position_id === null
                || player.in_section_id === null
                || player.out_section_id === null
                || player.minutes_played === null
            ) {
                return true;
            }
        })

    const onAddPlayer = () => {
        if (!userCanAddPlayer) {
            dispatch(permissionToast())
            return
        }
        dispatch(openModal({
            element: 'AddPlayersModal',
            props: selectedTeam,
            title: 'Add Players to ' + selectedTeam.name
        }))
    }
    const onSelectPlayer = (player) => {
        if (!userCanEditPlayer) {
            dispatch(permissionToast())
            return
        }
        const foundPlayer = usePlayer(player.id, selectedTeam)
        navigate(`edit/${foundPlayer.id}`)
    }

    const searchRef = useRef()
    const openSearchMenu = () => {
        setSearchMenuIsOpen(!searchMenuIsOpen)
        setTimeout(()=>{
            searchRef.current.focus()
        },50)
    }

    if (!userCanListPlayer) {
        return <NotAuthorizedComponent/>
    }
    if (!selectedTeam.id) return
    return (
        <React.Fragment>
            <div className={"flex flex-row items-center justify-between"}>
                <div className={"flex flex-row gap-2 items-center"}>
                    <Title>Players</Title>
                </div>
                <Button
                    className={"flex justify-center"}
                    onClick={() => setIsOpen(false)}
                >
                    <PanelSideOpenIcon/>
                </Button>
            </div>
            <div className={"bg-[#FFFFFFB2] flex-1 flex flex-row gap-2 rounded-xl p-[3px]"}>
                {[homeTeam, awayTeam].map((team, key) =>
                    <Team
                        onClick={() => setSelectedTeam(team)}
                        key={key}
                        className={"flex-1 flex flex-row items-center gap-2 p-1 justify-center cursor-pointer border border-[transparent] "
                            + (selectedTeam?.id === team.id ? "bg-white  !border-[#C7C8C9] rounded-lg" : "grayscale text-gray-100")}
                        imageClass={"w-6 h-6"}
                        team={team}/>
                )}
            </div>
            {selectedTeam?.id &&
                (
                    <React.Fragment>
                        <div
                            className={"bg-[#FFFFFFB2] rounded-[8px] flex-1 flex items-center justify-between p-2 flex-row relative"}
                            aria-label={"OverView"}>
                            {!searchMenuIsOpen && <React.Fragment>
                                <div className={"flex flex-col items-start justify-start gap-2 text-left"}>
                                    <Title>{selectedTeam.players.length} Players</Title>
                                    <Caption
                                        className={"text-gray-200"}> {playersWithoutData().length} Players
                                        does
                                        not
                                        have data</Caption>
                                </div>
                                <div className={"flex flex-row items-center justify-center"}>
                                    <Button
                                        className={"flex flex-row items-center justify-between gap-2 border-r border-[#0000001A]"}
                                        onClick={onAddPlayer}>
                                        <PlusIcon color={"#0571FC"}/>
                                        <Text className={"text-blue-100"}>Add</Text>
                                    </Button>
                                    <Button className={"flex items-center justify-center"}
                                            onClick={openSearchMenu}>
                                        <SearchIcon/>
                                    </Button>
                                </div>
                            </React.Fragment>}
                            <div className={"flex-1 flex flex-row gap-2 " + (searchMenuIsOpen ? 'visible' : 'hidden')}>
                                <InputText
                                    tabIndex={0}
                                    ref={searchRef}
                                    value={searchValue}
                                    onChange={handleSearch}
                                    className={"bg-white rounded-lg w-full pl-8 flex-1"}
                                    placeholder={"Search"}/>
                                <Button onClick={() => {
                                    setSearchMenuIsOpen(false)
                                    setSearchValue("")
                                }}>
                                    <Text>Cancel</Text>
                                </Button>
                            </div>
                        </div>

                        {selectedTeam.players?.filter(searchFn).map((player, key) => <PlayerDetailCard
                            key={key}
                            onClick={() => onSelectPlayer(player)}
                            player={player}
                            sideView={true}/>)}
                    </React.Fragment>
                )
            }
        </React.Fragment>
    )
}
