import {Caption, Text} from "./text";
import {Button} from "./button";
import {ArrowRightIcon, DeleteIcon} from "./icons";
import React from "react";
import {useSelector} from "react-redux";

export const Card = (props) => {
    const className = "bg-gray-700 rounded-xl p-2 " + props.className
    return (
        <div className={className}>
            <div className={"flex flex-row justify-between border-b border-gray-600 p-4 "}>
                {props.header}
            </div>
            <div className={"flex flex-col gap-2 p-4"}>
                {props.body}
            </div>
        </div>
    )
}
export const ActionCard = (props) => {

    return (

        <div className={"flex flex-col"}>
            <div className={"bg-gray-800 flex flex-row justify-between items-center px-2  " +
                "rounded-lg border border-gray-600 flex flex-row items-center justify-between"}>
                {props.title}
                {props.actions}
            </div>
        </div>
    )
}

export const PlayerDetailCard = (props) => {
    const {sections} = useSelector(state => state.event.selectedEvent)
    const player = props.player
    const isSideView = props.sideView ?? false
    const selected = Number(props.selected) === Number(player.id)
    return (
        <div
            onClick={props.onClick}
            className={"p-2 " +
                " cursor-pointer flex flex-col bg-white rounded-lg " + (selected && "border border-blue-200 !bg-blue-400")
                + (isSideView ? "" : "hover:bg-blue-400")}>
            <div className={"flex flex-row items-stretch justify-between border-b border-gray-300"}>
                <div className={"flex flex-col gap-1"}>
                    <Text className={"font-semibold"}>{player.full_name_with_data}</Text>
                    {/*<Text>{player.squad_number} - {player.position_name} - {player.position_side}</Text>*/}
                </div>
                {isSideView
                    ? (
                        <div className={"flex flex-row items-center gap-2 self-start"}>
                            {/*<Button className={"p-2 pr-4 border-r border-gray-300"} onClick={(e) => props.onDelete(e)}>*/}
                            {/*    <DeleteIcon color={"gray"}/>*/}
                            {/*</Button>*/}
                            <Button className={"p-2"}>
                                <ArrowRightIcon/>
                            </Button>
                        </div>
                    )
                    : <Button className={"p-2"}>
                        <DeleteIcon color={"gray"}/>
                    </Button>}
            </div>
            <div className={"flex flex-col"}>
                {player.in_section_id &&
                    <div className={"flex flex-row items-center justify-between border-b border-gray-300 py-1"}>
                        <Caption className={"text-gray-200"}>In/Out Time</Caption>
                        <Caption className={"text-gray-200"}>
                            {sections.find(s => s.id === player.in_section_id)?.name} {player.in_time} - {sections.find(s => s.id === player.out_section_id)?.name} {player.out_time}
                        </Caption>
                    </div>
                }
                {player.minutes_played &&
                    <div className={"flex flex-row items-center justify-between " + (player.description ? "border-b border-gray-300 py-1" : "")}>
                        <Caption className={"text-gray-200"}>Duration</Caption>
                        <Caption className={"text-gray-200"}>{player.minutes_played}</Caption>
                    </div>}

                {player.footbars.length > 0 ?
                    player.footbars.map(footbar=> (
                        <div
                            className={"flex flex-col items-start justify-between border-y border-gray-300 py-2 "}>
                                <div className={'flex flex-row items-center justify-between w-full'}>
                                    <Caption className={"text-black font-light"}>
                                    Footbar Tracker
                                    </Caption>
                                    <Caption className={"text-black font-light"}>
                                    {footbar.tracker_number}
                                    </Caption>
                                </div>
                                <div className={'flex flex-row items-center justify-between w-full'}>
                                    <Caption className={"text-black font-light"}>
                                    Footbar Time
                                    </Caption>
                                    <Caption className={"text-black font-light"}>
                                        {footbar.start_at} - {footbar.stop_at}
                                    </Caption>
                                </div>
                                <div className={'flex flex-row items-center justify-between w-full'}>
                                    <Caption className={"text-black font-light"}>
                                    Footbar Sensor Uptime
                                    </Caption>
                                    <Caption className={"text-black font-light"}>
                                        {player.sensor_uptime}
                                    </Caption>
                                </div>
                        </div>
                    )) : ''}
                {player.description &&
                    <div className={"flex flex-row items-center justify-between pt-1"}>
                        <Caption className={"text-gray-200"}>Description</Caption>
                        <Caption className={"text-gray-200"}>{player.description}</Caption>
                    </div>}

            </div>

        </div>
    )
}
