import {Caption, Text, Title} from "../_components/text";
import {Button} from "../_components/button";
import {
    ArrowDownIcon, ArrowLeftIcon,
    ArrowRightIcon,
    ArrowUpIcon,
    CheckIcon,
    CommentIcon,
    DeleteIcon, EditIcon, EmptyFlagIcon,
    EmptyStarIcon, FlagIcon, PanelSideOpenIcon, PauseIcon, PlayIcon, PlusCircleIcon, PlusIcon, StarIcon, TimesIcon
} from "../_components/icons";
import React, {forwardRef, useEffect, useRef, useState} from "react";
import {getTimeSeconds, secondsToTime} from "../_utils/helpers";
import {useDispatch, useSelector} from "react-redux";
import {Team} from "../_components/team";
import {InputDescription, InputRadio} from "../_components/input";
import {activeElementIsInput, useEventPlayers, useHighlights, useHotKeys, useTeams} from "../_utils/hooks";
import {RangeTime, RangeTimeSingle} from "../_components/range";
import {useNavigate, useParams, useLocation} from "react-router-dom";
import {confirmToast, errorToast} from "../_store/toast/toast.slice";
import {uniqueId} from "lodash";
import {SearchButtonInput, SearchInput} from "../_components/search-button";


export const TagForm = ({
                            form,
                            setForm,
                            currentTime,
                            changeTime,
                            handleClose,
                            handleOpen,
                            selectedTag,
                            selectedHighlight,
                            setSelectedTag,
                            handlePlaySingleDuration,
                            isPlayingSingleDuration,
                            onHighlightSave,
                            onFocusTimes,
                            onBlurTimes,
                        }) => {
    const dispatch = useDispatch()
    const {eventId} = useParams()
    const navigate = useNavigate()
    const highlights = useHighlights()
    const players = useEventPlayers()
    const tagTypes = useSelector(state => state.setting.tags)
    const subTagTypes = useSelector(state => state.setting.subTags)
    const {homeTeam, awayTeam} = useTeams()
    const [selectedTeam, setSelectedTeam] = useState({})

    const [exactTime, setExactTime] = useState('00:00')
    const [start, setStart] = useState('00:00')
    const [end, setEnd] = useState('00:00')
    const [isOpen, setIsOpen] = useState(true)

    const [commentIsOpen, setCommentIsOpen] = useState(false)
    const [tagCommentIsOpen, setTagCommentIsOpen] = useState(false)

    const [step, setStep] = useState(1)
    const initialTagForm = {
        exactTime: form.exactTime,
        comment: "",
        mainTag: {},
        subTags: [],
        selectedPlayer: {},
        index: form.tags.length
    }
    const [tagForm, setTagForm] = useState(initialTagForm)

    const handleCommentChange = (comment) => {
        setForm(prevForm => ({
            ...prevForm,
            comment
        }))
    }
    const handleCommentDelete = () => {
        setCommentIsOpen(false)
        setForm(prevForm => ({
            ...prevForm,
            comment: null
        }))
    }
    const handleTagCommentChange = (comment) => {
        setTagForm(prevForm => ({
            ...prevForm,
            comment
        }))
    }
    const handleTagCommentDelete = () => {
        setTagCommentIsOpen(false)
        setTagForm(prevForm => ({
            ...prevForm,
            comment: ""
        }))
    }
    const handleSetExactTime = (v) => {

        setExactTime(v)
        setForm(prevForm => ({
            ...prevForm,
            isEditing: true,
        }))
        setTagForm(prevState => ({
            ...prevState,
            exactTime: v,
        }))
        changeTime(getTimeSeconds(v))
    }
    const subTagsRef = useRef()
    const onSetMainTag = (tagType) => {
        setForm(prevForm => ({
            ...prevForm,
            isEditing: true,
        }))
        setTagForm(prevTagForm => ({
            ...prevTagForm,
            mainTag: tagType,
            subTags: [],
        }))


        setStep(2)
        setTimeout(() => {
            subTagsRef.current?.focus()
        }, 50)
    }

    const onSetSubTag = (subTagType) => {
        let nextSubTags = [...tagForm.subTags]

        const foundIndex = nextSubTags.findIndex(item => item.id === subTagType.id)
        if (foundIndex > -1) {
            nextSubTags.splice(foundIndex, 1)
        } else {
            nextSubTags.push(subTagType)
        }
        setTagForm({
            ...tagForm,
            subTags: nextSubTags,
        })
    }
    const subTagSelected = (subTagType) => tagForm.subTags?.findIndex(item => item.id === subTagType.id) > -1

    const onPlayerSelect = (player) => {
        setSelectedTeam({})
        setTagForm(prevState => ({
            ...prevState,
            selectedPlayer: player,
        }))

    }
    const onRangeChange = (v) => {
        setForm(prevState => ({
            ...prevState,
            start: v.from,
            end: v.to,
            isEditing: true,
        }))

        // console.log('v',v)
        changeTime(v.last)
    }

    const onClear = () => {
        setTagForm({
            ...initialTagForm,
            exactTime: form.start
        })
        setExactTime(form.start)
        setStep(1)
    }
    const tagFormIsComplete = () => tagForm?.selectedPlayer?.id && tagForm?.mainTag.id
    const tagExactTimeIsTheSame = () => {
        let count = form.tags.filter(prevTag => getTimeSeconds(prevTag.exactTime) === getTimeSeconds(tagForm.exactTime)).length
        if(count > 0){
            dispatch(errorToast('You should set a exact time other than previous tags.'))
            return true;
        }
        return false;
    }
    const onSaveTag = () => {
        if(tagExactTimeIsTheSame()) return
        if (!tagFormIsComplete()) return

        let newTags = JSON.parse(JSON.stringify(form.tags))

        let isEditDatabaseTag = false
        const foundIndex = newTags.findIndex(i => {
            if (!i.id) {
                return i.index === tagForm.index
            }
            isEditDatabaseTag = true
            return i.id === tagForm.id
        })

        if (foundIndex > -1) {
            newTags[foundIndex] = tagForm
        } else {
            newTags.push({
                ...tagForm,
                highlight_id: isEditDatabaseTag ? selectedHighlight.id : undefined,
                index: uniqueId()
            })
        }

        setForm(prevState => ({
            ...prevState,
            tags: newTags,
        }))
        setTagForm({
            ...initialTagForm,
            exactTime: newTags.at(-1)?.exactTime,
            index: uniqueId()
        })
        setExactTime(newTags.at(-1)?.exactTime)
        setStep(1)

        setTagCommentIsOpen(false)
        if (isEditDatabaseTag) {
            navigate(`/v3/events/${eventId}/tagger/highlight/edit/${selectedHighlight.id}`)
        }
        setSelectedTag(undefined)
    }

    const handleGoBack = () => {

        if (form.isEditing) {
            dispatch(confirmToast({
                message: 'You have a draft tag, Are you sure you want reset the current state?',
                callback: () => {
                    if (isPlayingSingleDuration) {
                        handlePlaySingleDuration({
                            start: null,
                            end: null
                        }, null)
                    }
                    navigate(`/v3/events/${eventId}/tagger/highlight`)
                }
            }))

            return
        }
        if (isPlayingSingleDuration) {
            handlePlaySingleDuration({
                start: null,
                end: null
            }, null)
        }
        navigate(`/v3/events/${eventId}/tagger/highlight`)
    }
    const closeTagPlayerSelect = () => {
        setSelectedTeam({})
    }

    const isPressed = useHotKeys((e) => {
            if (activeElementIsInput()) return

            let tag
            switch (e.key) {

                // case 'Backspace':
                //     if (e.ctrlKey) {
                //         handleGoBack()
                //
                //     }
                //     break;
                case 'PageUp':
                    if (step !== 3 || tagForm.selectedPlayer.id !== undefined) return
                    setSelectedTeam(homeTeam)

                    break;
                case 'PageDown':
                    if (step !== 3 || tagForm.selectedPlayer.id !== undefined) return
                    setSelectedTeam(awayTeam)

                    break;
                case 'Enter':

                    if (e.ctrlKey && step === 2) {
                        setStep(3)
                        return
                    }
                    if (step === 3) {
                        onSaveTag()
                        return
                    }
                    if (e.ctrlKey && form.tags.length > 0) {
                        onHighlightSave({})
                    }
                    break;
                case 'Escape':
                    handleGoBack()
                    break;
                case 'g':
                case 'G':
                    switch (tagForm.mainTag.name) {
                        case 'Goal':
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'GKmoves')
                            break
                        case 'GKmoves':
                        default:
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Goal')
                            break

                    }
                    onSetMainTag(tag)
                    break;
                case 'p':
                case 'P':

                    switch (tagForm.mainTag.name) {
                        case 'Pass':
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Penalty')
                            break
                        case 'Penalty':
                        default:
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Pass')
                            break
                    }
                    onSetMainTag(tag)
                    break;
                case 's':
                case 'S':
                    switch (tagForm.mainTag.name) {
                        case 'Skill':
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Save')
                            break
                        case 'Save':
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Shot')
                            break
                        case 'Shot':
                        default:
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Skill')
                            break

                    }
                    onSetMainTag(tag)
                    break;
                case 'd':
                case 'D':
                    switch (tagForm.mainTag.name) {
                        case 'Defence':
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Duel')
                            break
                        case 'Duel':
                        default:
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Defence')
                            break
                    }
                    onSetMainTag(tag)

                    break;
                case 'a':
                case 'A':
                    switch (tagForm.mainTag.name) {
                        default:
                            tag = tagTypes.find(tagTypes => tagTypes.name === 'Assist')
                            break

                    }
                    onSetMainTag(tag)
                    break;

            }
        }
    )


    useEffect(() => {
        if (!selectedTag || Object.keys(selectedTag).length === 0) {
            setTagForm({
                ...initialTagForm,
                index: form.tags.length + 1
            })
            return
        }
        if (selectedTag.id) {
            let selectedPlayer = players.find(p => p.id === selectedTag.player_id)
            let tempExactTimeToSet = selectedTag.exactTime ?? secondsToTime(currentTime)
            setExactTime(tempExactTimeToSet)
            let newTagForm = {
                ...selectedTag,
                exactTime: tempExactTimeToSet,
                mainTag: tagTypes.find(tagType => tagType.id === selectedTag.tag_id),
                subTags: selectedTag.subTags,
                selectedPlayer: selectedPlayer ?? {},
            }
            setTagForm(newTagForm)
            return
        }
        setTagForm(selectedTag)
    }, [selectedTag])
    useEffect(() => {
        setStart(form.start)
        setEnd(form.end)
    }, [form]);
    useEffect(() => {
        if (selectedTag) return
        setExactTime(secondsToTime(currentTime))
    }, []);
    if (!tagForm) return
    return (
        <React.Fragment>
            <div className={"flex flex-row items-center justify-between"}>
                <div className={"flex flex-row gap-2 items-center"}>
                    <Button onClick={handleGoBack}>
                        <ArrowLeftIcon color={"black"}/>
                    </Button>
                    <Title>{selectedHighlight ? 'Edit Highlight' : 'Add New Highlight'}</Title>
                </div>
                <Button
                    className={"flex justify-center"}
                    onClick={handleClose}
                >
                    <PanelSideOpenIcon/>
                </Button>
            </div>
            <div className={"bg-white flex-1 flex flex-col p-2 gap-2 rounded-2xl"}>
                <div className={"flex flex-row justify-between items-center"}>
                    <div className={"flex flex-row items-center justify-between"}>
                        <Text
                            className={"font-extrabold"}>Highlight {selectedHighlight ? selectedHighlight.id : highlights.length + 1}</Text>
                    </div>
                    <div className={"flex flex-row items-center justify-between"}>

                        <Button className={"border-r border-gray-600 py-1"}
                                onClick={() => setCommentIsOpen(true)}
                        >
                            <CommentIcon/>
                        </Button>
                        <Button className={"border-r border-gray-600 py-1"}
                                onClick={() => setForm(prevForm => ({
                                    ...prevForm,
                                    starred: !form.starred,
                                }))}
                        >
                            {form.starred ? <StarIcon/> : <EmptyStarIcon/>}
                        </Button>
                        <Button className={"border-r border-gray-600 py-1"}
                                onClick={() => handlePlaySingleDuration({
                                    start: form.start,
                                    end: form.end
                                })}
                        >
                            {!isPlayingSingleDuration ? <PlayIcon/> : <PauseIcon color={"#2B87FF"}/>}
                        </Button>
                        <Button
                            onClick={() => setForm(prevForm => ({
                                ...prevForm,
                                flagged: !form.flagged,
                            }))}
                        >
                            {form.flagged ? <FlagIcon/> : <EmptyFlagIcon/>}
                        </Button>
                    </div>
                </div>
                <RangeTime
                    onFocus={onFocusTimes}
                    onBlur={onBlurTimes}
                    onChange={(v) => onRangeChange(v)}
                    start={start}
                    end={end}
                    min={form.min}
                    max={form.max}
                />
                {(commentIsOpen || selectedHighlight?.comment) && (
                    <div className={"pt-2 border-t border-gray-300"}>
                        <InputDescription
                            value={form.comment}
                            onChange={(value) => handleCommentChange(value)}
                            onDelete={handleCommentDelete}
                        />
                    </div>
                )}
            </div>
            <TagPlayerSelect
                isVisible={selectedTeam.id && tagForm.selectedPlayer.id === undefined}
                team={selectedTeam}
                closeSelect={closeTagPlayerSelect}
                onChange={onPlayerSelect}/>

            {!selectedTeam.id && <Text className={"text-gray-100"}>Tags in Highlight</Text>}

            <div className={"bg-white flex-1 flex flex-col p-2 gap-2 rounded-2xl "
                + (selectedTeam.id && 'hidden')
            }>

                <div className={"flex flex-row justify-between items-center"}>
                    <div className={"flex flex-row items-center justify-between"}>
                        <Button
                            className={"font-extrabold text-blue-100 flex flex-row items-center justify-between gap-2"}
                            onClick={() => setIsOpen(!isOpen)}
                        >
                            {selectedTag?.mainTag && selectedTag.mainTag?.id ? (
                                <React.Fragment>
                                    <EditIcon color={"blue"}/>
                                    <Title>{selectedTag.mainTag.name}</Title>
                                </React.Fragment>
                            ) : (
                                <React.Fragment>
                                    <PlusIcon color={"blue"}/>
                                    <Title>{tagForm.mainTag?.id ? tagForm.mainTag.name : 'New tag'}</Title>
                                </React.Fragment>
                            )}

                        </Button>
                    </div>
                    <div className={"flex flex-row items-center justify-between"}>

                        <Button className={"border-r border-gray-600 py-1"}
                                onClick={() => setTagCommentIsOpen(true)}
                        >
                            <CommentIcon/>
                        </Button>
                        <Button className={"border-r border-gray-600 py-1"}
                                onClick={() => setTagForm(prevTagForm => ({
                                    ...prevTagForm,
                                    starred: !tagForm.starred
                                }))}>
                            {tagForm.starred ? <StarIcon/> : <EmptyStarIcon/>}
                        </Button>
                        <Button onClick={() => setIsOpen(!isOpen)}>
                            {isOpen ? <ArrowUpIcon/> : <ArrowDownIcon/>}
                        </Button>
                    </div>
                </div>
                {isOpen && (
                    <React.Fragment>
                        <RangeTimeSingle
                            value={exactTime}
                            min={start}
                            max={end}
                            onChange={(value) => handleSetExactTime(value)}
                        />
                        <div
                            className={"bg-white rounded-full border border-gray-600 text-gray-disabled flex flex-row items-center justify-evenly text-xs"}>
                            <Button onClick={() => setStep(1)}
                                    className={"p-[10px] " + (step === 1 && 'text-black font-extrabold')}>Main
                                Tag</Button>
                            <ArrowRightIcon color={"#AAAAAD"}/>
                            <Button onClick={() => setStep(2)}
                                    className={"p-[10px] " + (step === 2 && 'text-black font-extrabold')}>Sub
                                Tag</Button>
                            <ArrowRightIcon color={"#AAAAAD"}/>
                            <Button onClick={() => setStep(3)}
                                    className={"p-[10px] " + (step === 3 && 'text-black font-extrabold')}>Details</Button>
                        </div>
                        {step === 1 && <div className={"flex flex-wrap gap-1 max-w-[420px]"}>
                            {tagTypes.map(tagType =>
                                <Button onClick={() => onSetMainTag(tagType)}
                                        className={"flex-auto p-[9px] border border-transparent bg-gray-700 rounded-md "
                                            + (tagForm.mainTag?.id === tagType.id && 'font-extrabold bg-blue-300 border !border-blue-1-hold')}
                                        key={tagType.id}><Text>{tagType.name}</Text></Button>)}
                        </div>}
                        {step === 2 && <div className={"flex flex-col gap-2"}>
                            <Title>{tagForm.mainTag.name}</Title>
                            <div tabIndex={0} className={"flex flex-wrap gap-1 max-w-[420px]"} ref={subTagsRef}>
                                {subTagTypes
                                    .filter(subTagType => subTagType.tag_id === tagForm.mainTag.id)
                                    .map(subTagType => <Button tabIndex={0} onClick={() => onSetSubTag(subTagType)}
                                                               className={"flex-auto p-[9px] border border-transparent bg-gray-700 rounded-md " + (subTagSelected(subTagType) && 'font-extrabold bg-blue-300 border !border-blue-1-hold')}
                                                               key={subTagType.id}><Text>{subTagType.name}</Text></Button>)}
                            </div>
                        </div>}
                        {step === 3 && <div className={"flex flex-col gap-2"}>
                            <div className={"flex flex-col gap-2 bg-gray-300 p-2 rounded-xl my-2"}>
                                <div className={"flex flex-col gap-2"}>
                                    <Title>{tagForm.mainTag.name} {tagForm.exactTime && tagForm.exactTime}</Title>
                                    <div className={"flex flex-row gap-2"}>
                                        {tagForm.subTags.map(subTag => <Text key={subTag.id}>{subTag.name}</Text>)}
                                    </div>
                                </div>
                                {tagForm.selectedPlayer.id !== undefined && (
                                    <React.Fragment>
                                        <div
                                            className={"flex flex-row items-center justify-between border-y border-white py-2"}>
                                            <Caption
                                                className={'text-black'}>Player:</Caption>
                                            <Caption
                                                className={'text-black'}>{tagForm.selectedPlayer.full_name_with_data}</Caption>
                                        </div>
                                        <div className={"flex flex-row items-center justify-between"}>
                                            <Caption
                                                className={'text-black'}>Team:</Caption>
                                            <Caption
                                                className={'text-black'}>{tagForm.selectedPlayer.team_name}</Caption>
                                        </div>
                                        <Button
                                            className={"w-full bg-white flex flex-row items-center justify-between rounded-xl my-1"}
                                            onClick={() => {
                                                setTagForm(prevState => ({
                                                    ...prevState,
                                                    selectedPlayer: {}
                                                }))
                                            }}>
                                            <Text>Set Different Player</Text>
                                            <DeleteIcon/>
                                        </Button>
                                    </React.Fragment>
                                )}

                            </div>
                            {tagForm.selectedPlayer.id === undefined &&
                                <Text className={"pl-2 text-blue-100 font-semibold"}>Please select a player</Text>}
                            <div className={"bg-gray-700 rounded-xl flex flex-col"}>

                                {tagForm.selectedPlayer.id === undefined && [homeTeam, awayTeam].map((team, key) => (
                                    <div
                                        key={key}
                                        onClick={() => {
                                            setSelectedTeam(team)
                                        }}
                                        className={"cursor-pointer flex flex-row items-center justify-between p-2 px-4 first-of-type:border-b border-gray-600"}>
                                        <Team imageClass={"w-8 h-8"}
                                              className={"flex flex-row items-center gap-2 max-w-[240px]"}
                                              textClass={"font-light !font-xs"} team={team}/>
                                        <Button className={"flex flex-row gap-2 items-center"}>
                                            <div className={"w-16 h-4 rounded-full"} style={{
                                                backgroundColor: team.color
                                            }}>
                                            </div>
                                            <ArrowRightIcon/>
                                        </Button>
                                    </div>
                                ))}
                            </div>

                        </div>}

                        {(tagCommentIsOpen || tagForm.comment) && (
                            <div className={"pt-2 border-t border-gray-300"}>
                                <InputDescription
                                    value={tagForm.comment}
                                    onChange={(value) => handleTagCommentChange(value)}
                                    onDelete={handleTagCommentDelete}
                                />
                            </div>
                        )}

                        {step < 3 && (
                            <div className={"flex flex-row items-center justify-between gap-2"}>
                                <Button
                                    tabIndex={0}
                                    className={"flex flex-row items-center justify-center gap-2 bg-blue-100 border text-white flex-1 rounded-lg"}
                                    disabled={step === 1 && !tagForm.mainTag?.id}
                                    onClick={() => setStep(step + 1)}>
                                    <Text>Next</Text>
                                </Button>
                            </div>
                        )}
                        {step === 3 && (
                            <div className={"flex flex-row items-center justify-between gap-2"}>
                                <Button
                                    className={"flex flex-row items-center justify-center gap-2 flex-1 rounded-lg border border-gray-600"}
                                    onClick={onClear}
                                >
                                    <DeleteIcon/>
                                    <Text>Clear</Text>
                                </Button>
                                <Button
                                    className={"flex flex-row items-center justify-center gap-2 border text-blue-100 flex-1 rounded-lg "
                                        + (tagFormIsComplete() && 'border-blue-100')}
                                    disabled={!tagFormIsComplete()}
                                    onClick={onSaveTag}>
                                    <PlusIcon color={tagFormIsComplete() && "#0571FC"}/>
                                    <Text>{'highlight_id' in tagForm ? 'Update Tag' : 'Add Tag'}</Text>

                                </Button>
                            </div>
                        )}
                    </React.Fragment>
                )}
            </div>
        </React.Fragment>
    )
}
const TagPlayerSelect = ({team, onChange, isVisible, closeSelect}) => {
    const sections = useSelector(state => state.event.selectedEvent.sections)
    const ref = useRef()
    const [open, setOpen] = useState("")
    const [searchQuery, setSearchQuery] = useState("")
    const onSearch = e => {
        setSearchQuery(e.target.value)
    }
    const filterArray = (player) => player.full_name_with_data.toLowerCase().replace(" ", "")
        .includes(searchQuery.toLowerCase().replace(" ", ""))

    useEffect(() => {
        if (!isVisible) {
            setSearchQuery("")
        }
        ref.current.focus()
    }, [isVisible]);


    return (
        <div className={"flex flex-col gap-2"} tabIndex={0}>
            <div className={"flex flex-row items-center justify-between"}>
                {isVisible && <Team team={team} className={"flex flex-row gap-2 my-2 "} imageClass={"w-6 h-6"}/>}
                {isVisible && <Button onClick={() => closeSelect()}>
                    <TimesIcon/>
                </Button>}
            </div>
            <SearchInput className={!isVisible ? 'hidden' : ''}
                         onChange={onSearch}
                         query={searchQuery}
                         ref={ref}/>
            {team?.players?.filter(filterArray).length === 0 &&
                <div className={"d-flex items-center justify-center p-2 w-full"}>
                    <Title className={"text-center"}>No Players found!</Title>
                </div>}
            {team?.players?.filter(filterArray).map((player, key) => (
                <div key={key}
                     className={"bg-white p-4 rounded-2xl flex flex-col cursor-pointer"}
                     onClick={() => onChange(player)}>
                    <div className={"flex flex-row items-center justify-between"}>
                        <InputRadio onChange={() => onChange(player)}
                                    tabIndex={0}
                                    hasHotKey={true}
                                    className={"bg-transparent p-4 justify-center"}

                        />
                        <div className={"flex flex-col text-left w-full"}>
                            <Text className={"font-extrabold"}>{player.first_name} {player.last_name}</Text>
                            <Text>{player.squad_number} - {player.position_name} - {player.position_side}</Text>
                        </div>
                        <Button tabIndex={-1} onClick={(e) => {
                            e.stopPropagation()
                            setOpen(player)
                        }}>
                            {open.id === player.id ? <ArrowUpIcon/> : <ArrowDownIcon/>}
                        </Button>
                    </div>
                    {open.id === player.id && (
                        <div className={"flex flex-col border-t border-gray-300 mt-1"}>
                            {player.in_section_id &&
                                <div
                                    className={"flex flex-row items-center justify-between border-b border-gray-300 py-1"}>
                                    <Caption className={"text-gray-200"}>In/Out Time</Caption>
                                    <Caption className={"text-gray-200"}>
                                        {sections.find(s => s.id === player.in_section_id)?.name} {player.in_time} - {sections.find(s => s.id === player.out_section_id)?.name} {player.out_time}
                                    </Caption>
                                </div>
                            }
                            {player.minutes_played &&
                                <div className={"flex flex-row items-center justify-between "}>
                                    <Caption className={"text-gray-200"}>Duration</Caption>
                                    <Caption className={"text-gray-200"}>{player.minutes_played}</Caption>
                                </div>}
                        </div>
                    )}
                </div>
            ))}

        </div>
    )
}
