import {useDispatch, useSelector} from "react-redux";
import React, {useEffect, useRef, useState} from "react";
import {clearToast} from "../_store/toast/toast.slice";
import {CheckIcon, TimesIcon} from "./icons";
import {Button} from "./button";
import {Text, Title} from "./text";
import {closeModal} from "../_store/modal/modal.slice";

export const Toast = () => {
    const dispatch = useDispatch()
    const {message, type, timer, isConfirm, callback} = useSelector(state => state.toast)
    const [color, setColor] = useState("")

    const timeoutRef = useRef({})
    const modalRefConfirm = useRef()
    const onConfirm = () => {
        dispatch(clearToast())
        callback()
    }


    useEffect(() => {
        switch (type) {
            case 'success':
                setColor('bg-green-200')
                break
            case 'error':
                setColor('bg-red-200')
                break
            case 'confirm':
                setColor('bg-gray-200')
                break
        }
    }, [type]);
    useEffect(() => {
        if (isConfirm) return

        timeoutRef.current = setTimeout(() => {
            dispatch(clearToast())
        }, timer)

        return () => clearTimeout(timeoutRef.current)
    }, [message]);

    useEffect(() => {
        if (!modalRefConfirm.current) return
        console.log(modalRefConfirm.current)
        modalRefConfirm.current.focus()

    }, [modalRefConfirm.current])
    if (message === null) return

    if (isConfirm) return (
        <React.Fragment>
            <div
                className={"backdrop-blur-lg fixed bg-gray-700 shadow-xl mx-auto sm:w-3/4 md:w-3/4 max-w-[482]  text-center inset-x-0 mb-auto top-[42%] container w-3/4 rounded-2xl visible opacity-1 z-50 shadow-lg"}>
                <Title className={"flex w-full items-center justify-center p-4"}>{message}</Title>

                <div className={"flex flex-row gap-2 items-center border-t border-gray-600"}
                     ref={modalRefConfirm}
                     tabIndex={4}
                >
                    <Button
                        tabIndex={4}
                        className={"flex-1 p-4 rounded-lg border-r border-gray-600"} icon onClick={() => dispatch(clearToast())}>
                        <Text className={"font-semibold"}>Cancel</Text>
                        <TimesIcon/>
                    </Button>
                    <Button
                        tabIndex={4}
                        icon
                        className={"flex-1  p-4 rounded-lg"} onClick={onConfirm}>
                        <Text className={"text-blue-100 font-semibold"}>Accept</Text>
                        <CheckIcon color={"#0571FC"} />
                    </Button>
                </div>
            </div>
            <div onClick={() => dispatch(closeModal())} className={"fixed top-0 left-0 w-full h-full z-10"}
                 style={{background: "rgba(0,0,0,.6)"}}></div>
        </React.Fragment>
    )
    return (
        <div className={"fixed top-0 md:top-10 mx-auto inset-x-0 w-full sm:w-3/4 md:w-1/2 max-w-[480px]  flex items-center justify-center z-[60]"}>
            <div
                className={"flex items-center justify-between text-white m-0 md:m-4 md:rounded-lg  max-w-[482px] w-full " + color + (type === 'confirm' ? " flex-wrap" : "")}>
                <Text className={"p-[18px] " + (type === 'confirm' && " w-full")}>{message}</Text>
                <Button className={"p-[18px]"} onClick={() => dispatch(clearToast())}>
                    <TimesIcon color={"white"}/>
                </Button>
            </div>
        </div>
    )
}
