import {useEvent} from "../../../_utils/hooks";
import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {deleteCustomHighlight, fetchEventById} from "../../../_api/event.api";
import {useParams} from "react-router-dom";
import {Box} from "../../../_components/box";
import {Spinner} from "../../../_components/spinner";
import {Caption, Text} from "../../../_components/text";
import {CheckIcon, DeleteIcon, DownloadIcon} from "../../../_components/icons";
import {Button, ButtonBackToMain} from "../../../_components/button";
import {errorToast} from "../../../_store/toast/toast.slice";

export const ClipsRoute = () => {
    const {eventId} = useParams()
    const dispatch = useDispatch()
    const event = useEvent()
    const isLoading = useSelector(state => state.event.isLoading)

    const onDelete = (clip) => {
        dispatch(deleteCustomHighlight({
            eventId,
            clipId: clip.id
        }))
    }
    const downloadVideo = (clip) => {
        if (!clip.download_url) {
            dispatch(errorToast('This video has not gotten a download url yet.'))
            return
        }
        window.open(clip.download_url)
    }

    useEffect(() => {
        if (event !== null) return
        dispatch(fetchEventById(eventId))
    }, [event]);

    if (!event) {
        return <Spinner type={"block"}/>
    }
    return (
        <div className={"flex flex-col items-center justify-center p-4"}>
            <ButtonBackToMain />
            <div className={"flex flex-row items-center justify-between w-full"}>
                <Box className={"w-full mt-4"}
                     title={"Clips"}
                     headerClass={"border-none !bg-gray-700"}
                     body={(
                         <div className={"bg-gray-700 p-2 rounded-b-2xl overflow-x-auto"}>

                             <table className={" mt-4 "}>
                                 <thead>
                                 <tr>
                                     <th>#</th>
                                     <th>Title</th>
                                     <th>Duration</th>
                                     <th>Description</th>
                                     <th>Highlights</th>
                                     <th>AWS</th>
                                     <th>Video</th>
                                     <th>Action</th>
                                 </tr>
                                 </thead>
                                 <tbody>
                                 {event.clips.map((clip, key) => (
                                     <tr key={key}>
                                         <td>{key + 1}</td>
                                         <td>{clip.title}</td>
                                         <td>{clip.duration}</td>
                                         <td>{clip.description}</td>
                                         <td>
                                             <div className={"flex flex-col"}>
                                                 {clip.highlights.map(highlight => (
                                                     <div
                                                         key={highlight.id}
                                                         className={"flex-1 w-full last-of-type:border-none border-b border-gray-300 p-2"}>
                                                         <Caption>{highlight.start_time} - {highlight.until_time}</Caption>
                                                     </div>
                                                 ))}
                                             </div>
                                         </td>
                                         <td>{clip.has_aws_job ? <CheckIcon/> : '-'}</td>
                                         <td>{clip.download_url ? <Button onClick={() => downloadVideo(clip)}>
                                             <DownloadIcon/>
                                         </Button> : <DownloadIcon className={"flex items-center justify-center"}
                                                                   color={"lightgray"}/>}</td>
                                         <td>
                                             <div className={"flex flex-row items-center justify-center gap-2"}>
                                                 {clip.is_deletable && <Button
                                                     isLoading={isLoading}
                                                     onClick={() => onDelete(clip)}>
                                                     <DeleteIcon/>
                                                 </Button>}
                                             </div>
                                         </td>
                                     </tr>
                                 ))}
                                 </tbody>
                             </table>

                         </div>
                     )}
                />

            </div>
        </div>
    )
}
