import {Button, ButtonBack, ButtonBackToMain} from "../../_components/button";
import {Box} from "../../_components/box";
import {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {useCompetitions} from "../../_utils/hooks";
import {getCompetitions} from "../../_api/competition.api";
import {Spinner} from "../../_components/spinner";
import {PlusIcon} from "../../_components/icons";
import {Text} from "../../_components/text";
import {Route, Routes} from "react-router-dom";
import {CompetitionsIndexRoute} from "./competitions.index.route";
import {CompetitionsCreateRoute} from "./competitions.create.route";
import {CompetitionsEditRoute} from "./competitions.edit.route";

export const CompetitionsRoute = () => {
    const dispatch = useDispatch()
    const {isFetched, isLoading, data} = useCompetitions()

    useEffect(() => {
        if (isFetched) return
        dispatch(getCompetitions())
    }, []);
    return (
        <div className={"flex flex-col"}>
            {isLoading && <Spinner type={"block"}/>}
            <Routes>
                <Route path={"/"} index={true} element={<CompetitionsIndexRoute />} />
                <Route path={"create"} element={<CompetitionsCreateRoute />} />
                <Route path={"edit/:id"} element={<CompetitionsEditRoute />} />
            </Routes>

        </div>
    )
}
