import {useNavigate, useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {useEvent, useEventPlayers, useHighlights} from "../../../../_utils/hooks";
import React, {useEffect, useState} from "react";
import {getFullTimeSeconds, getTimeSeconds, secondsToTime} from "../../../../_utils/helpers";
import {deleteHighlightDetail, updateHighlight} from "../../../../_api/event.api";
import {Spinner} from "../../../../_components/spinner";
import {TagForm} from "../../../../_forms/tag-create.form";
import {HighlightDetail} from "../../../../_components/highlight";
import {Button} from "../../../../_components/button";
import {CheckIcon} from "../../../../_components/icons";
import {Text} from "../../../../_components/text";
import {confirmToast, errorToast, permissionToast, successToast} from "../../../../_store/toast/toast.slice";
import {userHasPermission} from "../../../../_hooks/user.hooks";
import {NotAuthorizedComponent} from "../../../../_components/not-authorized.component";

export const EditHighlight = ({
                                  currentTime,
                                  changeTime,
                                  handleClose,
                                  handleOpen,
                                  getSectionVideo,
                                  handlePlaySingleDuration,
                                  isPlayingSingleDuration
                              }) => {
    const userCanEditHighlight = userHasPermission('highlight-edit')
    const userCanDeleteHighlightTag = userHasPermission('highlight-tag-delete')
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const event = useEvent()
    const {selectedEvent, selectedSection, isStoringHighlight} = useSelector(state => state.event)
    const {highlightId, tagId} = useParams()
    const [highlight, setHighlight] = useState({})
    const [tag, setTag] = useState(undefined)
    const [form, setForm] = useState({})
    const tagTypes = useSelector(state => state.setting.tags)
    const players = useEventPlayers()
    const highlights = useHighlights()


    useEffect(() => {
        handleOpen()
    }, [])
    useEffect(() => {
        const foundHighlight = highlights.find(item => Number(item.id) === Number(highlightId))
        const foundHighlightIndex = highlights.findIndex(item => Number(item.id) === Number(highlightId))

        if (!foundHighlight) return
        setHighlight({
            ...foundHighlight,
            index: foundHighlightIndex
        })
        if (tagId) {
            navigate(`/v3/events/${event.id}/tagger/highlight/edit/${foundHighlight.id}/${tagId}`)
            setTag(foundHighlight.details.find(d => Number(d.id) === Number(tagId)))
        } else {
            navigate(`/v3/events/${event.id}/tagger/highlight/edit/${foundHighlight.id}`)
            setTag({})
        }
        if (selectedSection.id !== foundHighlight.section_id) {
            setTimeout(() => {
                getSectionVideo(foundHighlight.section_id, getFullTimeSeconds(foundHighlight.section_start_time))
            }, 50)
        }


    }, [highlightId, tagId])


    useEffect(() => {
        if (!highlight.id) return
        let form = {
            comment: highlight.comment,
            start: secondsToTime(getFullTimeSeconds(highlight.section_start_time)),
            end: secondsToTime(getFullTimeSeconds(highlight.section_until_time)),
            max:
                !selectedSection.video_duration ?
                    secondsToTime(getFullTimeSeconds(highlight.section_until_time))
                    : (
                        selectedSection.video_duration >= getFullTimeSeconds(highlight.section_until_time) + 30
                            ? secondsToTime(getFullTimeSeconds(highlight.section_until_time) + 30)
                            : secondsToTime(selectedSection.video_duration)),
            min: getFullTimeSeconds(highlight.section_start_time) - 30 < 0
                ? secondsToTime(0)
                : secondsToTime(getFullTimeSeconds(highlight.section_start_time) - 30),

            starred: highlight.is_starred,
            flagged: highlight.has_video_problem,
            tags: highlight.details.map(detail => ({
                id: detail.id,
                comment: detail.comment,
                highlight_id: detail.highlight_id,
                exactTime: detail.exactTime ?? '00:00',
                mainTag: tagTypes.find(tagType => tagType.id === detail.tag_id),
                subTags: detail.subTags,
                selectedPlayer: players.find(p => p.id === detail.player_id),
            }))
        }


        setForm(form)
        changeTime(getFullTimeSeconds(highlight.section_start_time))
    }, [highlight.id])
    const onDeleteFromDraft = (detail) => {
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this tag?',
            callback: () => {
                const foundTagIndex = form.tags.findIndex(tag => tag.index === detail.index)
                let newTags = form.tags
                newTags.splice(foundTagIndex, 1)
                setForm(prevForm => ({
                    ...prevForm,
                    tags: newTags
                }))
                setTimeout(() => dispatch(successToast('Tag Deleted Successfully!')), 100)
            }
        }))

    }
    const onDeleteFromDatabase = (detail) => {
        if (!userCanDeleteHighlightTag) {
            dispatch(permissionToast())
            return
        }
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this tag?',
            callback: () => {
                dispatch(deleteHighlightDetail({
                    eventId: event.id,
                    sectionId: highlight.section_id,
                    highlightId: highlight.id,
                    highlightDetailId: detail.id
                }))
                const foundTagIndex = form.tags.findIndex(tag => tag.id === detail.id)
                let newTags = form.tags
                newTags.splice(foundTagIndex, 1)
                setForm(prevForm => ({
                    ...prevForm,
                    tags: newTags
                }))
            }
        }))

    }
    const checkExactTimes = () => {
        let invalidTags = form.tags.filter(tag => {
            if (!tag.exactTime) return false;
            return getTimeSeconds(tag.exactTime) < getTimeSeconds(form.start)
                || getTimeSeconds(tag.exactTime) > getTimeSeconds(form.end)
        })
        if (invalidTags.length > 0) {
            dispatch(errorToast(invalidTags[0].mainTag.name + ' has an invalid time comparing to highlight start and end time'))
            return false;
        }
        return true;
    }
    const onHighlightUpdate = (e) => {

        if (!checkExactTimes()) return

        dispatch(updateHighlight({
            ...form,
            eventId: selectedEvent.id,
            sectionId: selectedSection.id,
            highlightId: highlight.id
        }))
        navigate(`/v3/events/${selectedEvent.id}/tagger/highlight`)
    }

    const onCancel = () => {
        navigate(`/v3/events/${selectedEvent.id}/tagger/highlight`)
    }
    if (!userCanEditHighlight) {
        return <NotAuthorizedComponent/>
    }
    if (form.tags === undefined) {
        return <Spinner type={"block"}/>
    }

    return (<div className={"flex flex-col gap-2"}>
            <TagForm
                handleClose={handleClose}
                handleOpen={handleOpen}
                changeTime={changeTime}
                currentTime={currentTime}
                form={form}
                handlePlaySingleDuration={handlePlaySingleDuration}
                isPlayingSingleDuration={isPlayingSingleDuration}
                selectedHighlight={highlight}
                onHighlightSave={onHighlightUpdate}
                selectedTag={tag}
                setForm={setForm}
                setSelectedTag={setTag}
            />
            {form.tags.map((formTag, key) => <HighlightDetail
                setTag={setTag}
                onDeleteFromDraft={onDeleteFromDraft}
                onDeleteFromDatabase={onDeleteFromDatabase}
                selected={tag ? (tag.id ? tag.id === formTag.id : (tag.index ? tag.index === formTag.index : false)) : false}
                key={key}
                detail={formTag}/>)}
            {form.tags.length > 0 && (
                <div className={"flex flex-row items-center justify-between gap-2"}>
                    <Button
                        className={"flex flex-row flex-1 items-center justify-center gap-2 bg-white border border-gray-300 rounded-md"}
                        isLoading={isStoringHighlight}
                        onClick={onCancel}
                    >
                        <CheckIcon color={"black"}/>
                        <Text className={"text-black"}>Cancel</Text>
                    </Button>
                    <Button
                        className={"flex flex-row flex-1 items-center justify-center gap-2 bg-blue-100 rounded-md"}
                        isLoading={isStoringHighlight}
                        onClick={onHighlightUpdate}
                    >
                        <CheckIcon color={"white"}/>
                        <Text className={"text-white"}>Update Highlight</Text>
                    </Button>

                </div>
            )}
        </div>

    )
}
