import React, {useEffect, useRef, useState} from "react";
import {ArrowDownIcon, ArrowUpIcon, CheckCircleIcon, EmptyCircleIcon, SearchIcon} from "../_components/icons";
import {useHotKeys, useOutsideClick} from "../_utils/hooks";
import moment from "moment";
import {InputMinute, InputText} from "./input";
import {getTimeSeconds, secondsToTime, secondsToTimeView} from "../_utils/helpers";
import {Caption, Text, Title} from "./text";


export const SelectMenu = (props) => {
    const {options, changeOption, selectRef, isOpen, setIsOpen, isDisabled, isSelected} = useSelect(props)
    const classList = "absolute w-full bg-white border border-gray-600 rounded-lg flex flex-col max-h-[360px] overflow-y-auto  " +
        (!isOpen ? "hidden opacity-0 -z-10" : "visible opacity-100 z-10")
        + " " + props.className

    const [searchValue, setSearchValue] = useState("")
    const handleChange = (e, option) => {
        e.stopPropagation()
        changeOption(option)
        props.onChange && props.onChange(option)
    }
    const handleSearch = (value) => {
        setSearchValue(value.target.value)
        props.onSearch(value.target.value)
    }
    const groupedRender = () => {
        return Object.entries(props.groupedOptions).map((group, key) => (
            <React.Fragment key={key}>
                <div className={"flex items-center justify-center p-2 border-t border-gray-500 bg-gray-300"}>
                    {group[0]}
                </div>
                {group[1].map((option, key) => (
                        <div
                            className={"flex flex-row items-center justify-between border-t border-gray-300 p-2"}
                            key={option.value}
                            onClick={(e) => handleChange(e, option)}
                        >
                            <div className={"flex flex-col gap-1 text-left "}>
                                <Title>{option.name}</Title>
                                <Caption>{option.caption}</Caption>
                            </div>
                            {!isDisabled(option) &&
                                <div className={"p-2 flex items-center justify-center"}>
                                    {isSelected(option) ? <CheckCircleIcon/> : <EmptyCircleIcon/>}
                                </div>
                            }
                        </div>
                    )
                )}
            </React.Fragment>
        ))
    }
    useEffect(() => {
        setIsOpen(props.isOpen)
    }, [props.isOpen]);

    return (
        <div
            ref={selectRef}
            className={classList}
        >
            <div className={"p-2 flex flex-row"} onClick={(e) => e.stopPropagation()}>
                <InputText value={searchValue} onChange={handleSearch}
                           className={"bg-gray-700 rounded-lg p-2 w-full pl-8 "}
                           placeholder={"Search"}/>
                <div className={"absolute left-4 top-5"}>
                    <SearchIcon/>
                </div>
            </div>

            {props.groupedOptions && groupedRender()}
            {!props.groupedOptions && options.filter(option => searchValue !== "" ? option.name.toLowerCase().includes(searchValue.toLowerCase()) : true).map(option =>
                <div className={"flex flex-row items-center justify-between border-t border-gray-300 p-2"}
                     key={option.value}
                     onClick={(e) => handleChange(e, option)}
                >
                    <div className={"flex flex-col gap-1 text-left "}>
                        <Title>{option.name}</Title>
                        <Caption>{option.caption}</Caption>
                    </div>
                    {!isDisabled(option) &&
                        <div className={"p-2 flex items-center justify-center"}>
                            {isSelected(option) ? <CheckCircleIcon/> : <EmptyCircleIcon/>}
                        </div>
                    }
                </div>
            )}
        </div>
    )
}

export const SelectColor = (props) => {
    const onChange = (e) => props.onChange({
        color: e.target.value,
        id: props.id
    })
    const selected = props.selected ? props.selected : undefined
    const inputRef = useRef()
    return (
        <div className={"flex flex-row items-center gap-2 cursor-pointer"} onClick={() => inputRef.current.click()}>
            <input value={selected} type={"color"} onChange={onChange} className={"rounded-full h-5"} ref={inputRef}/>
            <ArrowDownIcon/>
        </div>
    )
}

