export const SelectVideoTime = (props) => {
    const disabled = props.disabled ?? false
    const min = props.min ?? 0
    const max = props.max ?? 150000

    const [hour, setHour] = useState(props.value ? props.value.split(':')[0] : "00")
    const [minute, setMinute] = useState(props.value ? props.value.split(':')[1] : "00")

    const allowLengthOne = props.allowLengthOne ?? false
    const computeTime = (nextHour, nextMinute) => {
        return (nextHour ? nextHour : hour) + ':' + (nextMinute ? nextMinute : minute)
    }
    const checkMaxMin = nextTime => {
        const nextTimeSeconds = getTimeSeconds(nextTime)
        console.log('nextTimeSeconds', nextTimeSeconds)
        if (nextTimeSeconds < min) {
            const split = secondsToTime(min).split(":")
            setHour(split[0])
            setMinute(split[1])
            props.onChange(computeTime(split[0], split[1]))

        } else if (nextTimeSeconds > max) {
            const split = secondsToTime(max).split(":")
            setHour(split[0])
            setMinute(split[1])
            props.onChange(computeTime(split[0], split[1]))
        } else {
            props.onChange(nextTime)

        }
    }
    const onHourChange = (e, eventKey = null) => {
        setHour(e)


        if (allowLengthOne) {
            if (parseInt(e).toString().length > 1) {
                checkMaxMin(computeTime(e, null))
            }
        } else if (allowLengthOne === false) {
            if (eventKey === 'ArrowUp' || eventKey === 'ArrowDown') {
                checkMaxMin(computeTime(e, null))
            }
        }


    }
    const onMinuteChange = (e, eventKey = null) => {
        setMinute(e)
        console.log(eventKey)


        if (allowLengthOne) {
            if (parseInt(e).toString().length > 1) {
                checkMaxMin(computeTime(null, e))
            }
        } else if (allowLengthOne === false) {
            if (eventKey === 'ArrowUp' || eventKey === 'ArrowDown') {
                checkMaxMin(computeTime(null, e))
            }
        }

    }
    const onFocusHourOut = (e) => {
        setHour(e)
        // console.log('HourChangeFocus', e)
        checkMaxMin(computeTime(e, null))

    }
    const onFocusMinuteOut = (e) => {
        setMinute(e)

        checkMaxMin(computeTime(null, e))

    }
    const baseTabIndex = props.tabIndex
    const nextTabIndex = baseTabIndex ? props.tabIndex + 1 : false
    const classList = 'px-3  bg-gray-700 rounded-lg flex flex-row items-center gap-1'
        + (disabled ? " !bg-gray-500" : "")
        + " " + props.className

    const isPressed = useHotKeys((e) => {
        if (e.target.tagName !== 'INPUT') return

        switch (e.key) {
            case 'Enter':
                // e.stopPropagation();
                e.target.blur()
                // const nextElement = document.querySelector('[tabindex="' + (parseInt(e.target.tabIndex) + 1) + '"]');
                // if (nextElement) {
                //     nextElement.focus();
                // }
                break;
        }
    })

    useEffect(() => {
        const split = props.value.split(':')
        setHour(split[0])
        setMinute(split[1])
    }, [props.value, props.max]);
    return (
        <div
            className={classList}>
            <InputMinute
                tabIndex={baseTabIndex > 0 ? baseTabIndex : 0}
                className={props.inputClass}
                disabled={disabled}
                value={hour}
                min={'00'}
                max={150}
                onChange={onHourChange}
                onFocusOut={onFocusHourOut}
                onFocus={props.onFocus ? props.onFocus() : null}

            />
            <Text>:</Text>
            <InputMinute
                tabIndex={nextTabIndex > 0 ? nextTabIndex : 0}
                className={props.inputClass}
                disabled={disabled}
                value={minute}
                min={'00'}
                max={59}
                onChange={onMinuteChange}
                onFocusOut={onFocusMinuteOut}
                onFocus={props.onFocus ? props.onFocus() : null}

            />
        </div>

    )
}
export const SelectVideoTimeBetween = (props) => {

    const startTime = props.start !== null ? props.start : '00:00'
    const endTime = props.end !== null ? props.end : '00:00'
    const [totalHours, setTotalHours] = useState()
    const onChangeStart = (e) => props.onChangeStart(e)
    const onChangeEnd = (e) => props.onChangeEnd(e)

    const allowLengthOne = props.allowLengthOne ?? false

    useEffect(() => {
        const durationSeconds = getTimeSeconds(endTime) - getTimeSeconds(startTime)

        // console.log('DurationSeconds',durationSeconds)
        setTotalHours(secondsToTimeView(durationSeconds))


    }, [startTime, endTime])
    return (
        <div className={"flex flex-row items-center gap-2"}>
            <Text>{totalHours}</Text>
            <SelectVideoTime
                tabIndex={1000}
                allowLengthOne={allowLengthOne}
                value={startTime}
                onChange={onChangeStart}
                min={0}
                max={endTime !== '00:00' ? getTimeSeconds(endTime) : null}
            />
            <Text>To</Text>
            <SelectVideoTime
                tabIndex={1002}
                allowLengthOne={allowLengthOne}
                value={endTime}
                min={getTimeSeconds(startTime)}

                onChange={onChangeEnd}
            />
        </div>
    )
}
export const SelectTimeBetween = (props) => {

    const startTime = props.start
    const endTime = (props.end)
    const [totalHours, setTotalHours] = useState()

    useEffect(() => {
        const total = Number(((moment(endTime, "H:i:s").diff(moment(startTime, 'H:i:s'))) / (60 * 60 * 1000)))
        if (total < 1) {
            setTotalHours(Math.round(total * 60 * 100, 1) + 'm')
        } else {
            setTotalHours(total + 'h')
        }
    }, [startTime, endTime])
    return (
        <div className={"flex flex-row items-center gap-2 "}>
            <Text>{totalHours}</Text>
            <SelectTime value={startTime} onChange={props.onChangeStart}/>
            <Text>To</Text>
            <SelectTime value={endTime} onChange={props.onChangeEnd}/>
        </div>
    )
}
export const SelectTime = (props) => {
    const [value, setValue] = useState(props.value)
    const onChange = (e) => {
        setValue(e.target.value)
        props.onChange(e.target.value)
    }
    return (
        <input step={0} type={'time'} onChange={onChange} value={value} className={'p-3 py-2 bg-gray-700 rounded-lg'}/>
    )
}
