const useSelect = (props) => {
    const disabled = props.disabled ?? false
    const options = props.options
    const [selectedOptions, selectOptions] = useState([])
    const [isOpen, setIsOpen] = useState(false)
    const isMultiple = props.multiple ? props.multiple : false
    const selectRef = useOutsideClick(() => {
        setIsOpen(false)
        if (props.setIsOpen) {
            props.setIsOpen(false)
        }
    })
    const selected = props.selected
    const isSelected = (option) => {
        return selectedOptions.filter(item => item?.value === option.value).length > 0
    }
    const isDisabled = (option) => option?.disabled === true
    const changeOption = (option) => {
        if (option.disabled) return
        props.onChange(option)
        if (!isMultiple) {
            if (isSelected(option)) {
                selectOptions([])
                return
            }
            selectOptions([options.find(o => o.value === option.value)])
            return
        }
        if (isSelected(option)) {
            let newSelectedOptions = [...selectedOptions]
            newSelectedOptions.splice(newSelectedOptions.findIndex(o => o.value === option.value), 1)
            selectOptions(newSelectedOptions)
            return
        }
        selectOptions([...selectedOptions, options.find(o => o.value === option.value)])
    }
    useEffect(() => {
        if (selected === undefined || selected === null) {
            selectOptions([])
            return
        }
        if (!isMultiple) {
            const newSelected = options.find(o => o.value === selected)
            selectOptions([newSelected])
            return
        }
        selectOptions(options.filter(o => selected.filter(s => s.value === o.value).length > 0))
    }, [selected])
    const toggleDropdown = () => {
        if (!disabled) {
            setIsOpen(!isOpen)
        }
    }
    return {
        selected,
        options,
        selectOptions,
        selectedOptions,
        isOpen,
        setIsOpen,
        disabled,
        selectRef,
        toggleDropdown,
        isSelected,
        isDisabled,
        changeOption
    }
}

export const Select = (props) => {
    const {
        options,
        selectedOptions,
        isOpen,
        disabled,
        selectRef,
        toggleDropdown,
        isSelected,
        isDisabled,
        changeOption
    } = useSelect(props)
    const classList = "min-w-[120px] flex flex-row gap-2 justify-between items-center cursor-pointer "
        + (disabled ? "bg-gray-500 " : "")
        + props.className
    const leftOffset = (selectRef.current?.getBoundingClientRect().left < 100 ? " !-right-16" : "")
    const label = props.label ?? "Please Select"
    const searchable = props.searchable ?? false
    const [searchValue, setSearchValue] = useState("")

    const handleSearch = (value) => {
        setSearchValue(value.target.value)
    }


    return (
        <div className={"relative"} ref={selectRef}>
            <div className={classList} onClick={toggleDropdown}>
                {selectedOptions && selectedOptions.length === 1 && selectedOptions[0] ? selectedOptions[0].name : label}
                <ArrowUpIcon color={"black"} className={isOpen ? '' : 'rotate-180'}></ArrowUpIcon>
            </div>
            {isOpen && (
                <div
                    className={"absolute top-12 right-0 bg-white border border-gray-600 flex flex-col shadow-lg rounded-lg min-w-[160px] z-10 max-h-[360px] overflow-y-auto " + leftOffset}>
                    {searchable && <div className={"p-2 flex flex-row"} onClick={(e) => e.stopPropagation()}>
                        <InputText value={searchValue} onChange={handleSearch}
                                   className={"bg-gray-700 rounded-lg w-full p-2 !pl-8 "} placeholder={"Search"}/>
                        <div className={"absolute left-4 top-5"}>
                            <SearchIcon/>
                        </div>
                    </div>}
                    {options.filter(option => searchValue !== "" ? option.name.toLowerCase().includes(searchValue.toLowerCase()) : true).map((option, key) => (
                        <div key={key} onClick={() => changeOption(option)}
                             className={"p-2 border-b border-gray-600 last-of-type:border-b-none last-of-type:rounded-b-lg first-of-type:rounded-t-lg cursor-pointer" +
                                 " flex flex-row justify-between items-center "
                                 + (isSelected(option) ? 'bg-blue-400 border border-blue-200' : '')
                                 + (isDisabled(option) ? 'bg-gray-400 py-4 cursor-not-allowed' : '')
                             }>
                            <Text>{option.name}</Text>
                            {!isDisabled(option) &&
                                <div className={"p-2 flex items-center justify-center"}>
                                    {isSelected(option) ? <CheckCircleIcon/> : <EmptyCircleIcon/>}
                                </div>
                            }
                        </div>
                    ))}
                </div>

            )}
        </div>
    )
}

