import {useDispatch, useSelector} from "react-redux";
import React from "react";
import {closeModal} from "../_store/modal/modal.slice";
import {SectionModal} from "../_modals/section.modal";
import {Button} from "./button";
import {Text} from "./text";
import {CheckIcon, TimesIcon} from "./icons";
import {AddPlayersModal} from "../_modals/add-players.modal";
import {CreateClipModal} from "../_modals/create-clip.modal";
import {ChangeHighlightLinkModal} from "../_modals/change-highlight-link.modal";
import {ChangeHighlightDescriptionModal} from "../_modals/change-highlight-description.modal";

export const Modal = (props) => {
    const dispatch = useDispatch()

    const modal = useSelector(state => state.modal)

    return (
        <React.Fragment>
            <div
                className={"fixed border border-gray-600 shadow-xl mx-auto sm:w-3/4 md:w-3/4 lg:w-1/2 inset-x-0 top-10 container w-full bg-gray-700 rounded-2xl " + (modal.isOpen ? 'visible opacity-1 z-50' : 'hidden opacity-0 -z-1')}>
                {modal.element === 'SectionModal' && <SectionModal title={modal.title} data={modal.elementProps} />}
                {modal.element === 'AddPlayersModal' && <AddPlayersModal title={modal.title} data={modal.elementProps} />}
                {modal.element === 'CreateClip' && <CreateClipModal title={modal.title} data={modal.elementProps} />}
                {modal.element === 'ChangeHighlightLinkModal' && <ChangeHighlightLinkModal title={modal.title} data={modal.elementProps} />}
                {modal.element === 'ChangeHighlightDescription' && <ChangeHighlightDescriptionModal title={modal.title} data={modal.elementProps} />}

            </div>
            {modal.isOpen &&
                <div onClick={() => dispatch(closeModal())} className={"fixed top-0 left-0 w-full h-full z-10"}
                     style={{background: "rgba(0,0,0,.6)"}}></div>}
        </React.Fragment>
    )
}

export const ModalButtons = (props) => {
    return (
        <div className={"p-4 flex flex-row gap-2 items-center border-t border-gray-600"}>
            <Button className={"flex-1 bg-white p-3 rounded-lg"} icon onClick={props.onClose}>
                <Text>Cancel</Text>
                <TimesIcon/>
            </Button>
            <Button className={"flex-1 bg-blue-100 p-3 rounded-lg text-white"} icon onClick={props.onDone}
                    isLoading={props.isLoading}>
                <Text>Done</Text>
                <CheckIcon/>
            </Button>
        </div>
    )
}
