import {setSectionDuration} from "../_store/event/event.slice";
import axios from 'axios'

export const secondsToTime = (seconds) => {
    // console.log('Seconds', seconds)

    let minute
    if (seconds < 0) {
        minute = Math.floor(Number(-seconds / 60))
    } else {
        minute = Math.floor(Number(seconds / 60))
    }
    const second = seconds % 60
    return (seconds < 0 ? '-' : '') + normalizeTime(minute) + ':' + normalizeTime(second)
}
export const secondsToTimeView = (seconds) => {
    const exploded = secondsToTime(seconds).split(':')
    if (exploded[0] === '00') {
        return `${exploded[1]}s`
    }
    return `${exploded[0]}m ${exploded[1]}s`
}

export const SubtractTimes = (start, end) => {
    const startSeconds = getTimeSeconds(start)
    const endSeconds = getTimeSeconds(end)
    return secondsToTime(endSeconds - startSeconds)

}
export const SubtractFullTimes = (start, end) => {
    const startSeconds = getFullTimeSeconds(start)
    const endSeconds = getFullTimeSeconds(end)
    return secondsToTime(endSeconds - startSeconds)

}
export const SubtractFullTimesPure = (start, end) => {
    const startSeconds = getFullTimeSeconds(start)
    const endSeconds = getFullTimeSeconds(end)
    return endSeconds - startSeconds
}

export const AddToTime = (time, toAdd) => {
    const timeSeconds = getTimeSeconds(time)
    const toAddSeconds = getTimeSeconds(toAdd)
    return secondsToTime(timeSeconds + toAddSeconds)

}
export const normalizeTime = (string) => string.toString().length === 1 ? '0' + string : string
export const getTimeSeconds = (string) => {
    const minute = Number(string.toString().split(':')[0])
    const second = Number(string.toString().split(':')[1])
    return minute * 60 + second
}
export const getFullTimeSeconds = (string) => {
    const hour = Number(string.toString().split(':')[0])
    const minute = Number(string.toString().split(':')[1])
    const second = Number(string.toString().split(':')[2])
    return (hour * 60 * 60) + (minute * 60) + second
}
export const computedTime = (time) => {
    if (!time) return '00:00:00'
    const hours = Math.floor(time / 3600);
    const minutes = Math.floor((time % 3600) / 60);
    const secs = time % 60;
    const formattedHours = String(hours).padStart(2, '0');
    const formattedMinutes = String(minutes).padStart(2, '0');
    const formattedSeconds = String(secs).padStart(2, '0');
    return `${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
}


export const sectionFilterFn = section => section.video_url && section.video_start_time
