import React, {useEffect, useRef, useState} from "react";
import {Text, Title} from "./text";
import {
    ArrowLeftIcon,
    BackOneSecondIcon,
    BackTenSecondIcon, FolderIcon,
    ForwardOneSecondIcon,
    ForwardTenSecondIcon,
    OneXIcon,
    PauseIcon, PlayIcon
} from "./icons";
import {Button, ButtonBackToMain, ButtonDropdown} from "./button";
import {Route, Routes, useLocation, useNavigate} from "react-router-dom";
import {useSelector} from "react-redux";
import {ListItem} from "./list";
import {Spinner} from "./spinner";
import {useEventVideos, useVideoDimensions, useVideoHandles, VideoHandleBar} from "../_utils/video.hooks";
import {PlayerSidebar} from "../_sidebars/player.sidebar";
import {TaggerSidebar} from "../_sidebars/tagger.sidebar";
import {getTimeSeconds, secondsToTime, sectionFilterFn} from "../_utils/helpers";
import {LocalVideoModal} from "../_modals/local-video.modal";
import {HighlightTopBox} from "./highlight-top-box";
import {Team} from "./team";


export const VideoPlayer = (props) => {
    const videoRef = useRef()
    const localVideoRef = useRef()
    const timeInterval = useRef()

    const location = useLocation()
    const navigate = useNavigate()
    const {selectedEvent, selectedSection} = useSelector(state => state.event)
    const sections = selectedEvent.sections
    const [sectionHasEnded, setSectionHasEnded] = useState(false)

    const [dimensions, onLoadedData] = useVideoDimensions()

    const [isPlayingSingleDuration, setIsPlayingSingleDuration] = useState(false)
    const [customDuration, setCustomDuration] = useState({
        start: null,
        end: null,
        highlight: null

    })
    const highlightPlayerInterval = useRef()
    const [singleId, setSingleId] = useState("")
    const handlePlaySingleDuration = (duration, id = "") => {
        const prevId = singleId
        setSingleId(id)
        if (duration.start === null) {
            setSingleId(null)
            setIsPlayingSingleDuration(false)
            return
        }
        if (isPlayingSingleDuration) {
            setIsPlayingSingleDuration(false)
            setCustomDuration({
                start: null,
                end: null,
                highlight: null
            })
            handlePause()
            if (prevId !== id) {
                setTimeout(() => {
                    playSingleDuration(duration)
                }, 50)
            }
            return
        }
        playSingleDuration(duration)

    }
    const playSingleDuration = (duration) => {
        setIsPlayingSingleDuration(true)
        setCustomDuration(duration)
        setCurrentTime(getTimeSeconds(duration.start))
        videoRef.current.currentTime = currentSection().real_video_start_at + getTimeSeconds(duration.start)
        handlePlay()

    }
    const resetSingleDuration = () => {
        setIsPlayingSingleDuration(false)
        setCustomDuration({
            start: null,
            end: null,
            highlight: null
        })
    }

    const {isLoaded, sectionArray, currentTime, setCurrentTime, loadSection} = useEventVideos({
        sections,
        videoRef
    })


    const [localVideoModalIsOpen, setLocalVideoModalIsOpen] = useState(false)

    const videoHandles = useVideoHandles({videoRef, setCurrentTime})
    const {
        isPlaying,
        setIsPlaying,
        handlePlay,
        handlePause,
        handleRewind,
        handleForward,
        handlePlaybackSpeed,
    } = videoHandles


    useEffect(() => {
        if (!isPlayingSingleDuration) {
            clearInterval(highlightPlayerInterval.current)
        }
    }, [isPlayingSingleDuration]);
    useEffect(() => {
        clearInterval(highlightPlayerInterval.current)
    }, [customDuration]);
    useEffect(() => {
        if (!isPlayingSingleDuration) {
            return
        }
        if (currentTime > getTimeSeconds(customDuration.end)) {
            videoRef.current.currentTime = currentSection().real_video_start_at + getTimeSeconds(customDuration.start)
            setCurrentTime(getTimeSeconds(customDuration.start))
        }
        return () => clearInterval(highlightPlayerInterval.current)
    }, [currentTime]);


    const getSectionVideo = (sectionId, duration = undefined) => {
        if(isPlayingSingleDuration){
            resetSingleDuration()
        }
        handlePause()
        const openDBRequest = indexedDB.open('taggerDB', 1);
        openDBRequest.onupgradeneeded = function (event) {
            const db = event.target.result;
            db.createObjectStore('videos', {keyPath: 'id'});
        };
        openDBRequest.onsuccess = function (event) {
            const sectionArrayItem = sectionArray.find(s => s.id === sectionId)
            if (!sectionArrayItem) return
            const db = event.target.result;
            const transaction = db.transaction('videos', 'readonly');
            const store = transaction.objectStore('videos');
            store.get(sectionArrayItem.id).onsuccess = function (event) {
                if (!event.target.result) {
                    loadSection(sectionArrayItem, undefined, duration)
                    return
                }
                const videoBlob = event.target.result.blob;
                const blobUrl = URL.createObjectURL(videoBlob);
                loadSection(sectionArrayItem, blobUrl, duration);
            };
        };
    }


    useEffect(() => {
        if (!selectedSection || sectionArray.length === 0) return
        getSectionVideo(selectedSection.id)
    }, [sectionArray]);

    const onLoadLocalVideoClick = (e) => setLocalVideoModalIsOpen(true)
    const countTime = () => {
        let currentTime = videoRef.current.currentTime
        if (currentTime < currentSection().real_video_start_at) {
            currentTime = currentSection().real_video_start_at
            videoRef.current.currentTime = currentTime
        }
        if (currentTime > currentSection().real_video_end_at) {
            currentTime = currentSection().real_video_end_at
            setSectionHasEnded(true)
            videoRef.current.currentTime = currentTime
        }
        const videoCurrentTime = videoRef.current.currentTime
            - currentSection().real_video_start_at


        setCurrentTime(Number(Math.round(videoCurrentTime)))

        handleSavingVideoState(Number(Math.round(videoCurrentTime)))
    }
    const handleSavingVideoState = (time) => {
        localStorage.setItem(`video_state_section_${selectedSection.id}`, JSON.stringify({
            time
        }))
        localStorage.setItem(`current_section_id`, selectedSection.id)
    }
    const handlePlayFromSavedState = () => {
        const playedSectionId = Number(localStorage.getItem('current_section_id'))
        if (!playedSectionId) return
        const time = JSON.parse(localStorage.getItem(`video_state_section_${playedSectionId}`))?.time
        if (!time) return
        getSectionVideo(playedSectionId, time)
    }

    const onChangeDuration = (e) => {
        videoRef.current.currentTime = Math.round(e.target.value) + currentSection().real_video_start_at
        setCurrentTime(e.target.value)
    }
    const changeTime = (value) => {
        if(!value) return
        if (value <= 1) return
        videoRef.current.currentTime = Math.round(value) + currentSection().real_video_start_at
        setCurrentTime(value)
    }


    const isThereNextSection = () => {
        const currentSectionArrayItem = sectionArray.find(s => s.id === selectedSection.id)
        const nextSections = sectionArray.filter(s => s.sort > currentSectionArrayItem.sort)
        return nextSections.length > 0
    }
    const getNextSection = () => {
        const currentSectionArrayItem = sectionArray.find(s => s.id === selectedSection.id)
        const nextSections = sectionArray.filter(s => s.sort > currentSectionArrayItem.sort)
        if (nextSections.length > 0) {
            return nextSections[0]
        }
        return false;
    }
    const getSection = (sectionId) => sectionArray.find(s => s.id === sectionId)

    const handlePlayNextSection = () => {
        if (isThereNextSection()) {
            getSectionVideo(getNextSection().id)
            setSectionHasEnded(false)
            setIsPlaying(true)
            videoRef.current.play()
        }
    }

    const currentSection = () => sections.find(section => section.id === selectedSection.id)

    const durationToPlay = props.durationToPlay
    const title = props.title ?? ""


    useEffect(() => {
        if (!sectionHasEnded) return
        if (currentTime < currentSection().real_video_end_at) {
            setSectionHasEnded(false)
        }
    }, [currentTime])
    useEffect(() => {
        if (isPlaying && !sectionHasEnded) {
            timeInterval.current = setInterval(countTime, 1000)
        }
        if (!isPlaying || sectionHasEnded) clearInterval(timeInterval.current)
    }, [isPlaying, sectionHasEnded]);
    useEffect(() => {
        if (selectedSection === null) return
        if (!durationToPlay?.start) return
        let toPlay = durationToPlay.start - currentSection().real_video_start_at
        let toEnd = durationToPlay.end - currentSection().real_video_start_at
        if (currentSection().id !== durationToPlay.sectionId) {
            getSectionVideo(getSection(durationToPlay.sectionId).id)
            onChangeDuration({target: {value: toPlay}})
        } else {
            if (currentTime < toPlay || currentTime > toEnd) {
                onChangeDuration({target: {value: toPlay}})
            }
        }

    }, [
        durationToPlay
    ]);

    useEffect(() => {
        if (!isLoaded) return
        handlePlayFromSavedState()
    }, [isLoaded])
    useEffect(() => {
        return () => clearInterval(timeInterval.current)
    }, [])


    const pages = [
        {
            id: 1,
            name: 'Tagging',
            route: `/v3/events/${selectedEvent.id}/tagger/highlight`
        },
        {
            id: 2,
            name: 'Players',
            route: `/v3/events/${selectedEvent.id}/tagger/player`
        },
    ]
    const currentPage = () => {
        const pathName = location.pathname
        if (pathName.includes('player')) {
            return pages[1]
        } else if (pathName.includes('highlight')) {
            return pages[0]
        }
    }
    const changePage = (page) => {
        navigate(page.route)
    }

    if (!isLoaded) {
        return (
            <Spinner type={"block"}/>
        )
    }
    // console.log('currentVideo' ,videoRef.current?.currentTime)
    // console.log('realStart',currentSection().real_video_start_at)
    // console.log('cond',videoRef.current?.currentTime < currentSection().real_video_start_at)
    return (
        <React.Fragment>
            <div style={{height: dimensions.height + 'px'}} className={"relative bg-white"} tabIndex={-1}>
                <div className={"absolute top-6 left-20 z-20"}>
                    <ButtonDropdown
                        title={currentPage()?.name}
                        items={pages.map((page, key) => (
                            <ListItem
                                onClick={() => changePage(page)}
                                key={key}
                                className={`text-white p-3 px-6 w-full hover:bg-black flex items-center justify-center z-20 ` + (currentPage()?.id === page.id ? '!bg-black' : "")}
                            >
                                <Text>{page.name}</Text>
                            </ListItem>
                        ))}
                    >

                    </ButtonDropdown>
                </div>
                <div className={"top-0 w-full flex flex-row items-center justify-between absolute p-5 z-10"}>
                    <ButtonBackToMain/>

                    <div className={"flex flex-row justify-center items-center gap-4"}>
                        <ButtonDropdown
                            title={currentSection().name}
                            items={sections.filter(sectionFilterFn).map(section => (
                                <ListItem
                                    onClick={() => getSectionVideo(section.id)}
                                    key={section.id}
                                    className={"text-white p-3 px-6 w-full hover:bg-black flex items-center justify-center z-20 " + (currentSection().id === section.id ? "!bg-black" : "")}
                                >
                                    <Text>{section.name}</Text>
                                </ListItem>
                            ))}
                        >
                            <div className={"flex flex-row items-center justify-center gap-2"}>
                                <img src={selectedEvent.homeTeam.logo} className={"w-6 h-6 bg-white rounded-full"}/>
                                <Text
                                    className={"text-white"}>{secondsToTime((selectedSection.past_section_time * 60) + currentTime)}</Text>
                                <img src={selectedEvent.awayTeam.logo} className={"w-6 h-6 bg-white rounded-full"}/>
                            </div>
                        </ButtonDropdown>
                        {customDuration?.highlight &&
                            <HighlightTopBox highlight={customDuration?.highlight} handleCancel={resetSingleDuration}/>}

                    </div>
                    <div></div>
                </div>
                {videoRef.current?.currentTime < currentSection().real_video_start_at && <Spinner type={"block"}/>}
                {sectionHasEnded && (
                    <div
                        className={"absolute w-full h-full flex flex-col items-center justify-center gap-4 bg-gray-950 opacity-950"}>
                        <Title className={"text-white"}>{currentSection().name} has ended.</Title>
                        {isThereNextSection() &&
                            <Button className={"bg-white rounded-full flex items-center justify-center gap-2"}
                                    onClick={handlePlayNextSection}
                            >
                                <Text>Play Next Section</Text>
                                <PlayIcon/>
                            </Button>}
                    </div>
                )}
                <video className={"top-0 mx-auto"}
                       tabIndex={-1}
                       style={{height: dimensions.height + 'px', width: dimensions.width + 'px'}}
                       disablePictureInPicture={true}
                       playsInline={true}
                       ref={videoRef}
                       onClick={handlePlay}
                       onLoadedData={onLoadedData}
                >
                    <source src={selectedSection.video_url} type="video/mp4"/>
                    <p>
                        Your browser doesn't support HTML video. Here is a
                        <a href={selectedSection.video_url}>link to the video</a> instead.
                    </p>

                </video>
                <Button className={"p-3 text-white absolute bottom-0 right-16 z-20"} onClick={onLoadLocalVideoClick}>
                    <FolderIcon color={"white"}/>
                </Button>
                <Routes>
                    <Route path={'/highlight/*'} element={
                        <TaggerSidebar
                            getSectionVideo={getSectionVideo}
                            videoHandles={videoHandles}
                            handlePlaySingleDuration={handlePlaySingleDuration}
                            isPlayingSingleDuration={isPlayingSingleDuration}
                            currentTime={currentTime}
                            changeTime={changeTime}
                            height={dimensions.height}
                        />}
                    />
                    <Route path={'/player/*'} element={<PlayerSidebar
                        height={dimensions.height}
                        currentTime={currentTime}
                    />}/>
                </Routes>
                <VideoHandleBar
                    onChangeDuration={onChangeDuration}
                    currentSection={currentSection()}
                    currentTime={currentTime}
                    handleRewind={handleRewind}
                    handleForward={handleForward}
                    playerBackSpeed={videoRef.current?.playbackRate}
                    handlePlaybackSpeed={handlePlaybackSpeed}
                    handlePlay={handlePlay}
                    isPlaying={isPlaying}
                    isPlayingSingle={isPlayingSingleDuration}
                    singleDuration={customDuration}
                />
            </div>
            <LocalVideoModal
                setIsOpen={setLocalVideoModalIsOpen}
                isOpen={localVideoModalIsOpen}
                loadSection={loadSection}
                sectionArray={sectionArray}
            />
        </React.Fragment>
    )
}

