export const VideoOld = (props) => {
    const [sourceUrl, setSourceUrl] = useState(props.src)
    const selectedEvent = useSelector(state => state.event.selectedEvent)
    const navigate = useNavigate()
    const videoRef = useRef()
    const localVideoRef = useRef()
    const [currentTime, setCurrentTime] = useState('00')
    const [duration, setDuration] = useState()
    const [isPlaying, setIsPlaying] = useState(false)

    const [width, setWidth] = useState(0)
    const [height, setHeight] = useState(0)
    let timeInterval


    const onLoadLocalVideo = (e) => {
        setSourceUrl(URL.createObjectURL(e.target.files[0]))
        videoRef.current.load()
    }
    const onLoadLocalVideoClick = (e) => {
        localVideoRef.current.click()
    }
    const setTime = (e) => {
        let time = Math.round(e.nativeEvent.target.currentTime)
        setCurrentTime(time)
    }


    const handlePause = (e) => {
        videoRef.current.pause()
        setIsPlaying(false)
    }

    const handlePlay = (e) => {
        if (isPlaying) {
            handlePause(e)
            return
        }
        videoRef.current.play()
        setIsPlaying(true)
    }
    const handleRewind = (amount) => {
        videoRef.current.currentTime -= amount
    }
    const handleForward = (amount) => {
        videoRef.current.currentTime += amount

    }
    const handlePlaybackSpeed = () => {
        switch (videoRef.current.playbackRate) {
            case 1:
                videoRef.current.playbackRate = 1.5
                break;
            case 1.5:
                videoRef.current.playbackRate = 2
                break
            case 2.0:
                videoRef.current.playbackRate = 2.5
                break;
            case 2.5:
                videoRef.current.playbackRate = 3.0
                break;
            case 3.0:
                videoRef.current.playbackRate = 1.0
                break;
        }
    }
    const onPlay = (e) => {
        timeInterval = setInterval(() => {
            setTime(e)
        }, 1000)
    }

    const onPause = (e) => {
        setTime(e)
        clearInterval(timeInterval)
    }

    const onChangeDuration = (e) => {
        setCurrentTime(e.target.value)
        videoRef.current.currentTime = e.target.value
    }
    const onLoadedData = (e) => {
        const videoWidth = e.target.videoWidth
        const videoHeight = e.target.videoHeight
        const aspectRatio = videoWidth / videoHeight

        const windowHeight = window.innerHeight
        const windowWidth = window.innerWidth
        let newWidth, newHeight;

        if (windowWidth / windowHeight < aspectRatio) {
            newWidth = windowWidth;
            newHeight = windowWidth / aspectRatio;
        } else {
            newHeight = windowHeight;
            newWidth = windowHeight * aspectRatio;
        }
        setWidth(newWidth)
        setHeight(newHeight)
        setDuration(Math.round(videoRef.current.duration))
    }

    const computedTime = (time) => {
        if (!time) return '00:00:00'
        const hours = Math.floor(time / 3600);
        const minutes = Math.floor((time % 3600) / 60);
        const secs = time % 60;
        const formattedHours = String(hours).padStart(2, '0');
        const formattedMinutes = String(minutes).padStart(2, '0');
        const formattedSeconds = String(secs).padStart(2, '0');
        return `${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
    }

    const navigateBack = () => navigate(`/v3/events/${selectedEvent.id}/tagger`)

    const onSetStartTime = () => {
        props.changeStart(secondsToTime(Math.round(videoRef.current.currentTime)))
    }

    const onSetEndTime = () => {
        props.changeEnd(secondsToTime(Math.round(videoRef.current.currentTime)))
    }

    useEffect(() => {
        setSourceUrl(props.src)
        if (videoRef.current) {
            videoRef.current.load()
        }
    }, [props.src]);

    return (
        <div style={{height: height + 'px'}} className={"relative bg-white"}>
            <div className={"top-0 w-full flex flex-row items-center justify-between absolute p-5 z-20"}>
                <Button className={"bg-[#00000080] rounded-full items-center flex justify-center p-4"}
                        onClick={navigateBack}>
                    <ArrowLeftIcon color={"white"}/>
                </Button>
            </div>
            <video className={"top-0 mx-auto"} style={{height: height + 'px', width: width + 'px'}}
                   disablePictureInPicture={true}
                   ref={videoRef}
                   onPlay={onPlay}
                   onPause={onPause}
                   onClick={handlePlay}
                   onLoadedData={onLoadedData}
            >
                <source src={sourceUrl} type="video/mp4"/>
                <p>
                    Your browser doesn't support HTML video. Here is a
                    <a href={sourceUrl}>link to the video</a> instead.
                </p>

            </video>

            <div
                className={"w-full absolute bottom-12 p-3  bg-black min-h-12 flex flex-col align-items-center justify-center gap-1 opacity-85"}>
                <input className={""} type="range" min="0" value={currentTime} onChange={onChangeDuration}
                       max={duration}/>
                <div className={"flex flex-row items-center justify-between"}>
                    <Text className={"text-white"}>{computedTime(currentTime)}</Text>
                    <Text className={"text-white"}>{computedTime(duration)}</Text>
                </div>
            </div>
            <div

                className={"w-full absolute bottom-0 bg-white border border-gray-300 border-t-none flex items-center justify-start"}>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={handlePlay}>
                    {isPlaying ? <PauseIcon/> : <PlayIcon/>}
                </Button>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={() => handleRewind(1)}>
                    <BackOneSecondIcon/>
                </Button>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={() => handleRewind(10)}>
                    <BackTenSecondIcon/>
                </Button>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={() => handleForward(10)}>
                    <ForwardTenSecondIcon/>
                </Button>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={() => handleForward(1)}>
                    <ForwardOneSecondIcon/>
                </Button>
                <Button className={"p-3 px-6 border-r border-gray-300"} onClick={handlePlaybackSpeed}>
                    <OneXIcon speed={videoRef.current?.playbackRate}/>
                </Button>
                <Button className={"p-3 px-6 border-l border-gray-300 ml-auto"} onClick={onSetStartTime}>
                    <Text>Set Start Time</Text>
                </Button>
                <Button className={"p-3 px-6 border-l border-gray-300 "} onClick={onSetEndTime}>
                    <Text>Set End Time</Text>
                </Button>
            </div>


        </div>
    )
}

