import {Caption, Text, Title} from "../_components/text";
import React, {useEffect, useRef, useState} from "react";

import {closeModal} from "../_store/modal/modal.slice";
import {useEvent} from "../_utils/hooks";
import {Button} from "../_components/button";
import {DeleteIcon, FolderIcon, PlusIcon} from "../_components/icons";
import {InputRadio} from "../_components/input";
import {Spinner} from "../_components/spinner";


export const LocalVideoModal = (props) => {

    const event = useEvent()
    const [selectedSection, setSection] = useState({})
    const localVideoRef = useRef()
    const onClose = () => {
        props.setIsOpen(false)
    }
    const [videos, setVideos] = useState([])
    const [isLoading, setIsLoading] = useState(false)

    const getVideo = (section) => {
        const openDBRequest = indexedDB.open('taggerDB', 1);
        openDBRequest.onupgradeneeded = function (event) {
            const db = event.target.result;
            db.createObjectStore('videos', {keyPath: 'id'});
        };
        openDBRequest.onsuccess = function (event) {

            const db = event.target.result;
            const transaction = db.transaction('videos', 'readonly');
            const store = transaction.objectStore('videos');
            store.get(section.id).onsuccess = function (event) {
                if (!event.target.result) return
                setVideos(prevState => ([
                    ...prevState,
                    {
                        id: section.id,
                        video: event.target.result
                    }
                ]))
            };
        };
    }
    const storeVideo = (blob) => {
        setIsLoading(true)
        const blobUrl = URL.createObjectURL(blob)
        const sectionArrayItem = props.sectionArray.find(s => s.id === selectedSection.id)
        if (!selectedSection.video_url) return
        let tempVideo = document.createElement('video')
        console.log(blob)
        const tempListener = (event) => {

            if (Math.round(Number(tempVideo.duration)) === Number(sectionArrayItem.real_duration)) {
                const openDBRequest = indexedDB.open('taggerDB', 1);
                openDBRequest.onupgradeneeded = function (event) {
                    const db = event.target.result;
                    db.createObjectStore('videos', {keyPath: 'id'});
                };
                openDBRequest.onsuccess = function (event) {
                    const db = event.target.result;
                    const transaction = db.transaction('videos', 'readwrite');
                    const store = transaction.objectStore('videos');
                    const dataToAdd = {
                        id: selectedSection.id,
                        name: blob.name,
                        size: blob.size,
                        inUse: true,
                        blob: blob
                    }
                    const addRequest = store.add(dataToAdd)
                    addRequest.onsuccess = function (event) {
                        setVideos(prevState => ([
                            ...prevState,
                            {...dataToAdd, video: blob}
                        ]))
                        props.loadSection(sectionArrayItem, blobUrl)
                        setIsLoading(false)
                    }
                };
            } else {
                alert('Durations are not the same')
                setIsLoading(false)
            }
            tempVideo.removeEventListener('loadedmetadata', tempListener)
            tempVideo.remove()
        }
        tempVideo.src = blobUrl
        tempVideo.addEventListener("loadedmetadata", tempListener)
        tempVideo.load()


    }

    const deleteVideo = (section) => {
        const openDBRequest = indexedDB.open('taggerDB', 1);
        openDBRequest.onupgradeneeded = function (event) {
            const db = event.target.result;
            db.createObjectStore('videos', {keyPath: 'id'});
        };

        openDBRequest.onsuccess = function (event) {

            const db = event.target.result;
            const transaction = db.transaction('videos', 'readwrite');
            const store = transaction.objectStore('videos');
            const deleteRequest = store.delete(section.id);

            deleteRequest.onsuccess = function () {
                setVideos(prevState => prevState.filter(video => video.id !== section.id));
                getVideo(section)
                props.loadSection(section,section.video_url)
            };


            deleteRequest.onerror = function (event) {
                console.error("Error deleting video:", event.target.error);
            };

        };
    }
    // const updateVideo = (section, data) => {
    //     setIsLoading(true)
    //     const openDBRequest = indexedDB.open('taggerDB', 1);
    //     openDBRequest.onupgradeneeded = function (event) {
    //         const db = event.target.result;
    //         db.createObjectStore('videos', {keyPath: 'id'});
    //     };
    //
    //     openDBRequest.onsuccess = function (event) {
    //
    //         const db = event.target.result;
    //         const transaction = db.transaction('videos', 'readwrite');
    //         const store = transaction.objectStore('videos');
    //         const updateRequest = store.put({
    //             id: section.id,
    //             video: data,
    //         });
    //
    //         updateRequest.onsuccess = function () {
    //             setVideos(prevState => prevState.map(video =>
    //                 video.id === section.id ? {...video, video: data} : video
    //             ))
    //             setIsLoading(false)
    //         }
    //         getVideo(section)
    //     };
    //
    // }

    const withoutVideoFilterFn = section => !videos.map(video => (video.id)).includes(section.id)
    const withVideoFilterFn = section => videos.map(video => (video.id)).includes(section.id)

    // const onUseVideo = (section) => {
    //     const videoIndex = videos.findIndex(v => v.id === section.id)
    //     if (videoIndex < 0) return
    //     let newVideos = [...videos]
    //     let video = newVideos[videoIndex]
    //     video.video.inUse = !video.video.inUse
    //     if (video.video.inUse) {
    //         props.loadSection(section, URL.createObjectURL(video.video.blob))
    //     } else {
    //         props.loadSection(section, section.video_url)
    //     }
    //     setVideos(newVideos)
    //     updateVideo(section, video.video)
    //
    // }
    useEffect(() => {
        event.sections.forEach(section => {
            getVideo(section)
        })
    }, [])
    if (!props.isOpen) return

    return (
        <React.Fragment>

            <input type={"file"} className={"hidden"} ref={localVideoRef}
                   onChange={(e) => storeVideo(e.target.files[0])}
                   accept="video/mp4,.qt"
            />
            <div
                className={"fixed border border-gray-600 shadow-xl mx-auto sm:w-3/4 md:w-3/4 lg:w-1/2 inset-x-0 top-10 container w-full bg-gray-700 rounded-2xl visible opacity-1 z-50"}>
                <Title className={"p-4 border-b border-gray-600"}>Local Videos</Title>
                {isLoading && <Spinner type={"block"} className={"rounded-xl"}/>}
                <div className={"p-4 flex flex-col gap-4"}>
                    <div className={"flex flex-col rounded-lg bg-white"}>
                        {event.sections
                            .filter(withoutVideoFilterFn).map(section => (
                                <div key={section.id}
                                     className={"first-of-type:border-none border-t border-gray-300 flex flex-row items-center justify-between"}>
                                    <Text className={"text-gray-100 p-4"}>{section.name}</Text>
                                    <Button icon onClick={() => {
                                        setSection(section)
                                        localVideoRef.current.click()
                                    }}>
                                        <Text className={"text-blue-100"}>Load Video</Text>
                                        <PlusIcon color={"#0571FC"}/>
                                    </Button>
                                </div>
                            ))}
                    </div>
                    {event.sections
                        .filter(withVideoFilterFn).map(section => (
                            <div key={section.id}
                                 className={"flex flex-col rounded-lg bg-white"}>
                                <div
                                    className={"flex flex-row items-center justify-between border-b border-gray-300 pr-3"}>
                                    <Title className={"text-gray-100 p-3"}>{section.name}</Title>
                                    {/*<Button*/}
                                    {/*    className={"flex flex-row items-center justify-between gap-2"}*/}
                                    {/*    icon*/}
                                    {/*    onClick={() => onUseVideo(section)}>*/}
                                    {/*    <InputRadio*/}
                                    {/*        className={"!bg-white"}*/}
                                    {/*        selected={videos.find(v => v.id === section.id).video.inUse}*/}
                                    {/*        onChange={() => {*/}
                                    {/*        }}*/}
                                    {/*    />*/}
                                    {/*    <Text className={"text-gray-100"}>Use This Video</Text>*/}
                                    {/*</Button>*/}
                                </div>
                                <div className={"flex flex-row items-center justify-between pr-3"}>
                                    <div className={"flex flex-col gap-1 p-3"}>
                                        <Caption
                                            className={"font-semibold"}>{videos.find(v => v.id === section.id).video.name}</Caption>
                                        <Caption
                                            className={"text-gray-200"}>{(videos.find(v => v.id === section.id).video.size / 1000000000).toFixed(2)}GB</Caption>
                                    </div>
                                    <div className={"flex flex-row items-center"}>
                                        {/*<Button icon className={"border-r border-gray-300 text-gray-100"}*/}
                                        {/*        onClick={() => {*/}
                                        {/*            setSection(section)*/}
                                        {/*            localVideoRef.current.click()*/}
                                        {/*        }}>*/}
                                        {/*    <Text>Replace</Text>*/}
                                        {/*    <FolderIcon color={"#5E6163"}/>*/}
                                        {/*</Button>*/}
                                        <Button onClick={() => deleteVideo(section)}>
                                            <DeleteIcon color={"#5E6163"}/>
                                        </Button>
                                    </div>

                                </div>
                            </div>
                        ))}

                </div>
            </div>
            <div onClick={() => props.setIsOpen(false)} className={"fixed top-0 left-0 w-full h-full z-10"}
                 style={{background: "rgba(0,0,0,.6)"}}></div>
        </React.Fragment>
    )
}
