import React, {useEffect} from "react";
import {EventApi} from "../../../_utils/api";

import {useNavigate, useParams} from "react-router-dom";
import {Caption, Title} from "../../../_components/text";
import {Team} from "../../../_components/team";
import {Text} from "../../../_components/text";
import {Button} from "../../../_components/button";
import {PlusIcon} from "../../../_components/icons";
import {Select} from "../../../_components/select";
import {useDispatch, useSelector} from "react-redux";
import {fetchEventById, setEventStatusById} from "../../../_api/event.api";
import {TeamPlayers} from "../../../_components/team";
import {Box} from "../../../_components/box";
import {SelectColor, SelectWrapper} from "../../../_components/select";
import {ArrowRightIcon} from "../../../_components/icons";
import {setEventTeamColor} from "../../../_api/event.api";
import {openModal} from "../../../_store/modal/modal.slice";
import {SectionModal} from "../../../_modals/section.modal";
import {getSettings} from "../../../_api/setting.api";
import Header from "../../../_components/header";
import {Card} from "../../../_components/card";
import {TagsAndHighlightsBox} from "../../../_boxes/tags-and-highlights.box";
import {userHasPermission} from "../../../_hooks/user.hooks";
import {errorToast} from "../../../_store/toast/toast.slice";

const IndexRoute = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userCanEditEvent = userHasPermission('event-edit')
    const userCanCreateSection = userHasPermission('section-create')
    const userCanEditSection = userHasPermission('section-edit')

    const isLoading = useSelector(state => state.event.isLoading)
    const event = useSelector(state => state.event.selectedEvent)

    const {eventId} = useParams()


    const onStatusChange = async (e) => {
        if (e.value === 0 && !userCanEditEvent) {
            dispatch(errorToast('Not Enough Permissions'))
            return
        }
        await dispatch(setEventStatusById({
            eventId,
            status: e.value
        }))
    }
    const onColorChange = async (e) => {
        if (!userCanEditEvent) {
            dispatch(errorToast('Not Enough Permissions'))
            return
        }
        await dispatch(setEventTeamColor({
            eventId,
            color: e.color,
            team_id: e.id
        }))
    }

    const onCreateSection = () => {
        if(!userCanCreateSection) {
            dispatch(errorToast('Not Enough Permissions'))
            return
        }
        dispatch(openModal({
            element: 'SectionModal',
            props: null,
            title: 'Add Section'
        }))
    }
    const onEditSection = (section) => {
        if(!userCanEditSection) {
            dispatch(errorToast('Not Enough Permissions'))
            return
        }
        navigate(`sections/edit/${section.id}`)

    }

    useEffect(() => {
        (async () => {
            await dispatch(fetchEventById(eventId))
            await dispatch(getSettings(eventId))
        })()
    }, []);

    if (isLoading) {
        return (
            'Loading...'
        )
    }
    return (
        <div className={"container mx-auto"}>
            <Header/>

            <Card header={(
                <>
                    <div className={"flex flex-col gap-1 "} style={{flex: '0 0 40%'}}>
                        <Team
                            team={event.homeTeam}
                            playerCount={false}
                        />
                    </div>
                    <div className={"flex flex-col items-center justify-center gap-2"} style={{flex: '0 0 20%'}}>
                        <Title className={"text-2xl"}>{event.result}</Title>
                        <Caption
                            className={"max-w-32 text-justify text-wrap flex items-center justify-center text-gray-200"}>
                            {event.date}
                        </Caption>
                    </div>
                    <div className={"flex flex-col gap-1 "} style={{flex: '0 0 40%'}}>

                        <Team className={"flex flex-col gap-1 "}
                              imageClass={"self-end"}
                              textClass={"text-right"}
                              playerCount={false}
                              team={event.awayTeam}/>
                    </div>

                </>
            )}
                  body={(
                      <>
                          <SelectWrapper title={"Status"} className={"border border-gray-600 rounded-md"}>
                              <Select multiple={false}
                                      onChange={onStatusChange} className={"p-4"}
                                      options={[
                                          {name: 'Not Started', value: 0},
                                          {name: 'In Progress', value: 2},
                                          {name: 'Finished', value: 1},
                                      ]}
                                      selected={event.status.id}></Select>
                          </SelectWrapper>

                          <Box title={"Colors"}
                               body={[event?.homeTeam, event?.awayTeam].map((team, key) => (
                                   <div key={key}
                                        className={"bg-white p-4 border-b border-gray-600 last-of-type:rounded-b-md flex flex-row items-center justify-between"}>
                                       <Team imageClass={"w-6 h-6"}
                                             className={"flex flex-row items-center text-gray-100 gap-2"}
                                             textClass={"font-size-14 leading-[18px] !font-normal"}
                                             playerCount={false}
                                             team={team}/>
                                       <SelectColor onChange={onColorChange} selected={team.color} id={team.id}/>
                                   </div>
                               ))}

                          />


                          <Box title={"Sections"}
                               actions={[
                                   <Button className={"flex flex-row gap-2 items-center justify-center w-36 px-4"}
                                           onClick={onCreateSection}>
                                       <Text className={"text-blue-100 font-bold"}>Add Section</Text>
                                       <PlusIcon color={"#0571FC"}/>
                                   </Button>
                               ]}
                               body={event?.sections.map((section, key) => (
                                   <div key={key}
                                        className={"flex flex-row items-center justify-between bg-white p-4 border-b border-gray-600 last-of-type:rounded-b-md"}>
                                       <div className={"flex flex-col items-start justify-center gap-2"}>
                                           <Text>{section.name}</Text>
                                           <Caption>{section.video_url}</Caption>
                                       </div>
                                       {section.video_url
                                           ? (section.show_duration
                                               ? <div className={'flex flex-row gap-2 items-center'}>
                                                   <Button
                                                       className={"text-gray-100 flex flex-row gap-2 items-center pr-0"}
                                                       onClick={()=>onEditSection(section)}>
                                                       <Text>{section.show_duration}</Text>
                                                       <ArrowRightIcon/>
                                                   </Button>
                                               </div>
                                               : <div className={'flex flex-row gap-2 items-center'}>
                                                   <Button className={"text-blue-100"}
                                                           onClick={()=>onEditSection(section)}>
                                                       <Text>Set Time</Text>
                                                   </Button>
                                               </div>)
                                           : ""}
                                   </div>
                               ))}
                          />

                          <TeamPlayers team={event.homeTeam}/>
                          <TeamPlayers team={event.awayTeam}/>

                          <TagsAndHighlightsBox/>
                          {/*<Box title={"Video Processing Status"}*/}
                          {/*     body={*/}
                          {/*         <React.Fragment>*/}

                          {/*         </React.Fragment>*/}
                          {/*     }*/}
                          {/*/>*/}
                      </>
                  )}
            />
        </div>
    )
}


export default IndexRoute
