import {Title, Text} from "../_components/text";
import React, {useState} from "react";
import {Button} from "../_components/button";
import {Select, SelectTime, SelectTimeBetween, SelectVideoTimeBetween, SelectWrapper} from "../_components/select";
import {useDispatch, useSelector} from "react-redux";
import {InputMinute, InputText, InputTextCopy} from "../_components/input";
import {CheckIcon, TimesIcon} from "../_components/icons";
import {closeModal} from "../_store/modal/modal.slice";
import {setEventVideoUrls, storeSection} from "../_api/event.api";
import {ModalButtons} from "../_components/modal";
import {errorToast} from "../_store/toast/toast.slice";
import {Spinner} from "../_components/spinner";


export const SectionModal = (props) => {

    const dispatch = useDispatch()
    const settings = useSelector(state => state.setting)
    const selectedEvent = useSelector(state => state.event.selectedEvent)
    const isLoading = useSelector(state => state.event.isLoading)
    const data = props.data

    const [isFetching, setIsFetching] = useState(false)

    const close = (e) => {
        dispatch(closeModal())
    }
    const onDone = async (e) => {
        setIsFetching(true)
        const results = await Promise
            .all(form.sections.filter(section => section.video_url).map(section =>
                fetch(section.video_url)
                    .then(response => ({
                        url: section,
                        status: response.status,
                        ok: response.ok
                    }))
                    .catch(() => ({
                        url: section,
                        status: 'Error',
                        ok: false
                    }))
            ))
        const allValid = results.every(result =>
            result.ok
        );
        if (allValid) {
            setIsFetching(false);
            dispatch(dispatch(storeSection({
                eventId: form.eventId,
                sections: form.sections.filter(section => section.video_url)
            })));
            dispatch(closeModal());
        } else {
            setIsFetching(false);
            dispatch(errorToast('Invalid links provided. Please recheck them.'));
            dispatch(closeModal());
        }
    }


    const onChange = (e) => {
        let newForm = {...form}
        const foundIndex = newForm.sections.findIndex(s => s.id === e.id)
        if (foundIndex > -1) {
            newForm.sections[foundIndex].video_url = e.value
        }
        setForm(newForm)
    }
    const [form, setForm] = useState({
        eventId: selectedEvent.id,
        sections: settings.types.map(type => ({
            id: type.value,
            name: type.name,
            video_url: selectedEvent.sections.find(section => section.section_type_id === type.value)?.video_url
        }))
    })

    return (
        <React.Fragment>
            {isFetching && <Spinner type={"block"} className={"rounded-2xl"}/>}
            <Title className={"p-4 border-b border-gray-600"}>{props.title}</Title>
            <div className={"p-4 flex flex-col"}>
                <div className={"p-4 flex flex-col"}>
                    {form.sections?.map((section, key) => <InputTextCopy
                        className={"first-of-type:rounded-t-lg last-of-type:rounded-b-lg rounded-none"}
                        id={section.id}
                        key={key}
                        placeholder={"Please enter video URL..."}
                        label={section.name}
                        value={section.video_url}
                        onChange={onChange}/>)}

                </div>

            </div>
            <ModalButtons isLoading={isFetching} onClose={close} onDone={onDone}/>
        </React.Fragment>
    )
}
