import {Button, ButtonBackToMain, ButtonDropdown} from "./button";
import React, {useEffect, useRef, useState} from "react";
import {useEvent} from "../_utils/hooks";
import {useVideoDimensions, useVideoHandles, VideoHandleBar} from "../_utils/video.hooks";
import {Spinner} from "./spinner";
import {Text, Title} from "./text";
import {getTimeSeconds, sectionFilterFn} from "../_utils/helpers";
import {ArrowDownIcon, PlayIcon, TimesIcon} from "./icons";
import {ListItem} from "./list";
import {HighlightTopBox} from "./highlight-top-box";

export const HighlightVideoPlayer = ({selectedPlay, resetSelectedPlay}) => {
    const event = useEvent()
    const [selectedSection, setSelectedSection] = useState()
    const videoRef = useRef()
    const timeInterval = useRef()
    const [dimensions, onLoadedData] = useVideoDimensions()

    const [isEnded, setIsEnded] = useState(false)
    const [currentTime, setCurrentTime] = useState("")

    const onChangeDuration = (e) => {
        videoRef.current.currentTime = Math.round(e.target.value) + selectedPlay.start
        setCurrentTime(e.target.value)
    }
    const videoHandles = useVideoHandles({videoRef, setCurrentTime})
    const {
        isPlaying,
        setIsPlaying,
        handlePlay,
        handlePause,
        handleRewind,
        handleForward,
        handlePlaybackSpeed,
    } = videoHandles

    const goToVideoTime = (start, end) => {
        setIsEnded(false)
        handlePause()
        videoRef.current.currentTime = start
        setCurrentTime(0)
        handlePlay()
    }
    const sectionStartSecond = () => selectedSection?.video_start_time ? getTimeSeconds(selectedSection.video_start_time) : 0
    const sectionEndSecond = () => selectedSection?.video_end_time ? getTimeSeconds(selectedSection.video_end_time) : 0
    const countTime = () => {
        if(!videoRef.current) return
        let currentTimeShow = videoRef.current.currentTime - selectedPlay.start
        let videoCurrentTime = videoRef.current.currentTime


        if (videoCurrentTime > selectedPlay.end) {
            videoCurrentTime = selectedPlay.start
            currentTimeShow = 0
            setIsEnded(true)
            videoRef.current.currentTime = videoCurrentTime
            handlePause()
        }


        setCurrentTime(Number(Math.round(currentTimeShow)))

    }
    useEffect(() => {
        if (!event) return
        if (!selectedPlay.sectionId) return
        let section = event.sections.find(section => section.id === selectedPlay.sectionId)
        setSelectedSection(section)
        videoRef.current.currentTime = getTimeSeconds(section.video_start_time)
    }, [selectedPlay.sectionId, event]);
    useEffect(() => {
        if (selectedPlay.start === null) return
        goToVideoTime(selectedPlay.start, selectedPlay.end)
    }, [selectedPlay.start, selectedPlay.end]);
    useEffect(() => {
        if (isPlaying) {
            timeInterval.current = setInterval(countTime, 1000)
        }
        if (!isPlaying) clearInterval(timeInterval.current)
    }, [isPlaying]);
    const handleCancelPlay = () => {
        resetSelectedPlay()
        setIsEnded(false)
        handlePause()
    }
    return (
        <div style={{height: dimensions.height + 'px'}} className={"relative bg-white"}>

            <div className={"top-0 w-full flex flex-row items-center justify-between absolute p-5 z-10"}>
                <ButtonBackToMain/>
                {selectedPlay?.highlight && <HighlightTopBox highlight={selectedPlay?.highlight} handleCancel={handleCancelPlay} />}

                <div></div>
            </div>
            {videoRef.current?.currentTime < selectedPlay?.start && <Spinner type={"block"}/>}
            {(isEnded || selectedPlay.start === null) && (
                <div
                    className={"absolute w-full h-full flex flex-col items-center justify-center gap-4 bg-gray-950 opacity-950"}>
                    <Title className={"text-white"}>Highlight {selectedPlay.id} has ended.</Title>

                    {isEnded && <Button className={"bg-white rounded-full flex items-center justify-center gap-2"}
                                        onClick={() => {
                                            setIsEnded(false)
                                            goToVideoTime(selectedPlay.start, selectedPlay.end)
                                        }}
                    >
                        <Text>Replay Highlight</Text>
                        <PlayIcon/>
                    </Button>}
                    {selectedPlay.start === null &&
                        <Text className={"text-white font-semibold"}>Please select a highlight to play</Text>}
                </div>
            )}
            <video className={"top-0 mx-auto z-10"}
                   style={{height: dimensions.height + 'px', width: dimensions.width + 'px'}}
                   disablePictureInPicture={true}
                   ref={videoRef}
                   onClick={handlePlay}
                   onLoadedData={onLoadedData}
            >
                {selectedSection && <source src={selectedSection.video_url} type="video/mp4"/>}
                <Title>
                    Please select a highlight to Play
                </Title>

            </video>
            {selectedSection && <VideoHandleBar
                onChangeDuration={onChangeDuration}
                currentSection={{
                    video_duration: selectedPlay.end - selectedPlay.start
                }}
                currentTime={currentTime}
                handleRewind={handleRewind}
                handleForward={handleForward}
                playerBackSpeed={videoRef.current?.playbackRate}
                handlePlaybackSpeed={handlePlaybackSpeed}
                handlePlay={handlePlay}
                isPlaying={isPlaying}
                isPlayingSingle={!!selectedPlay.sectionId}
                singleDuration={selectedPlay}
            />}
        </div>
    )
}
