import {useDispatch, useSelector} from "react-redux";
import React, {useState} from "react";
import {closeModal, setResetSignal} from "../_store/modal/modal.slice";
import {errorToast} from "../_store/toast/toast.slice";
import {changeHighlightDescription, storeCustomHighlight} from "../_api/event.api";
import {Title} from "../_components/text";
import {InputDescription, InputText, TextArea} from "../_components/input";
import {ModalButtons} from "../_components/modal";

export const ChangeHighlightDescriptionModal = () => {
    const dispatch = useDispatch()
    const modal = useSelector(state => state.modal)
    const props = modal.elementProps
    const [form, setForm] = useState({
        highlight_id: props.highlight.id,
        eventId: props.eventId,
        description: ""
    })
    const {isLoading} = useSelector(state => state.event.isLoading)
    const onClose = () => dispatch(closeModal())
    const onDone = () => {
        if (!form.description) {
            dispatch(errorToast('Please fill description'))
            return
        }
        dispatch(changeHighlightDescription(form))

    }
    return (
        <React.Fragment>
            <Title className={"p-4 border-b border-gray-600"}>{modal.title}</Title>
            <div className={"flex flex-col p-4 gap-2"}>
                <TextArea
                    placeholder={"Description"}
                    value={form.description}
                    onChange={(v) => setForm({...form, description: v})}
                />
            </div>
            <ModalButtons isLoading={isLoading} onClose={onClose} onDone={onDone}/>

        </React.Fragment>
    )
}
