import {useNavigate} from "react-router-dom";
import {useSelector} from "react-redux";
import React, {useState} from "react";
import {useFilterMenu, useHighlightFilter, useHighlights, useTags} from "../../../../_utils/hooks";
import {Caption, Text, Title} from "../../../../_components/text";
import {Button} from "../../../../_components/button";
import {
    DeleteIcon,
    FilterIcon,
    PanelSideOpenIcon,
    PlusCircleIcon, SearchIcon,
    SortAscending,
    SortDescending
} from "../../../../_components/icons";
import {TeamHighlights} from "../../../../_components/highlight";
import {SearchButton, SearchButtonInput} from "../../../../_components/search-button";
import {FilterBar, FilterMenu} from "../../../../_components/filter-menu";
import {userHasPermission} from "../../../../_hooks/user.hooks";
import {NotAuthorizedComponent} from "../../../../_components/not-authorized.component";

export const IndexHighlight = ({setIsOpen, videoHandles, isPlayingSingleDuration, handlePlaySingleDuration}) => {
    const navigate = useNavigate()
    const selectedSection = useSelector(state => state.event.selectedSection)
    const [selectedTeam, setSelectedTeam] = useState("")
    const userCanListHighlight = userHasPermission('highlight-list')
    const userCanAddHighlight = userHasPermission('highlight-create')


    const [sort, setSort] = useState(true)

    const [searchMenuIsOpen, setSearchMenuIsOpen] = useState(false)
    const {
        filterMenu,
        filterMenuIsOpen,
        setFilterMenuIsOpen,
        onSelectFilter,
        onSearchQueryChange,
        selectedFilters,
        filterFn,
        searchQuery,
        clearAllFilters
    } = useHighlightFilter()
    const highlights = useHighlights().filter(highlight => highlight.section_id === selectedSection.id)
    const tags = useTags(highlights.filter(filterFn))
    const sortFn = (prevHighlight, nextHighlight) => {
        if (sort) {
            return prevHighlight.i - nextHighlight.i
        } else {
            return nextHighlight.i - prevHighlight.i
        }
    }
    if(!userCanListHighlight){
        return <NotAuthorizedComponent />
    }
    return (<React.Fragment>
        <div className={"flex flex-row items-center justify-between"}>
            <Title>Tags ({selectedSection.name})</Title>

            <Button
                className={"flex justify-center"}
                onClick={() => {
                    setIsOpen(false)
                }}
            >
                <PanelSideOpenIcon/>
            </Button>
        </div>

        <div className={"flex flex-col"}>
            <div className={`bg-[#FFFFFFB2] flex-1 flex flex-row items-center justify-between p-2 ` + (selectedFilters.length > 0 || searchMenuIsOpen ? 'rounded-t-2xl' : 'rounded-2xl')}>
                {userCanAddHighlight && <Button className={"p-2 flex flex-row items-center justify-between gap-2"}
                                                onClick={() => navigate('create')}
                >
                    <PlusCircleIcon color={"#0571FC"}/>
                    <Text className={"text-blue-100"}>New Highlight</Text>
                </Button>}
                <div className={"flex flex-row items-center justify-between"}>

                    <div className={"relative"}>
                        <Button className={"flex flex-row items-center justify-between gap-2 border-r border-gray-300"}
                                onClick={() => setFilterMenuIsOpen(!filterMenuIsOpen)}
                        >
                            <FilterIcon/>
                            <Text>Filter</Text>
                        </Button>
                        <FilterMenu
                            isVisible={filterMenuIsOpen}
                            selectedFilters={selectedFilters}
                            onSelectFilter={onSelectFilter}
                            onClose={() => setFilterMenuIsOpen(false)}
                            filterOptions={filterMenu}/>
                    </div>
                    <Button className={"border-r border-gray-300"} onClick={() => setSort(!sort)}>
                        {sort ? <SortDescending/> : <SortAscending/>}
                    </Button>
                    <Button className={"border-r border-gray-300"}>
                        <DeleteIcon/>
                    </Button>
                    <Button onClick={() => setSearchMenuIsOpen(!searchMenuIsOpen)}>
                        <SearchIcon/>
                    </Button>
                </div>

            </div>
            {searchMenuIsOpen && <SearchButtonInput className={"bg-[#FFFFFFB2] rounded-none"}
                                                    query={searchQuery}
                                                    onChange={onSearchQueryChange}
                                                    onClose={() => setSearchMenuIsOpen(false)}/>}
            {selectedFilters?.length > 0 && (
                <FilterBar className={"bg-[#FFFFFFB2] mt-0 rounded-t-none"} onSelectFilter={onSelectFilter} selectedFilters={selectedFilters} clearAllFilters={clearAllFilters}/>
            )}
        </div>
        <div className={"flex flex-row items-start my-2 gap-1"}>
            <Caption className={"text-gray-200"}>{highlights.filter(filterFn).length} Highlights,</Caption>
            <Caption className={"text-gray-200"}>{tags.length} Tags</Caption>
        </div>
        <TeamHighlights
            highlights={highlights.sort(sortFn).filter(filterFn)}
            tags={tags}
            handlePlaySingleDuration={handlePlaySingleDuration}
            isPlayingSingleDuration={isPlayingSingleDuration}
        />
    </React.Fragment>)
}
