import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";
import React, {useCallback, useEffect, useRef, useState} from "react";
import {storeHighlight} from "../../../../_api/event.api";
import {getTimeSeconds, secondsToTime} from "../../../../_utils/helpers";
import {TagForm} from "../../../../_forms/tag-create.form";
import {HighlightDetail} from "../../../../_components/highlight";
import {Button} from "../../../../_components/button";
import {CheckIcon, DeleteIcon} from "../../../../_components/icons";
import {Text} from "../../../../_components/text";
import {confirmToast, errorToast} from "../../../../_store/toast/toast.slice";
import {userHasPermission} from "../../../../_hooks/user.hooks";
import {NotAuthorizedComponent} from "../../../../_components/not-authorized.component";

export const CreateHighlight = ({
                                    currentTime,
                                    changeTime,
                                    handleClose,
                                    handleOpen,
                                    isOpen,
                                    handlePlaySingleDuration,
                                    isPlayingSingleDuration
                                }) => {
    const userCanCreateHighlight = userHasPermission('highlight-create')
    const dispatch = useDispatch()

    const navigate = useNavigate()
    const {isStoringHighlight, selectedSection, selectedEvent} = useSelector(state => state.event)
    const [loaded, setLoaded] = useState(false)
    const [form, setForm] = useState({
        tags: [],
        isEditing: false,
    })
    const [tag, setTag] = useState()


    useEffect(() => {
        if (isOpen) return
        handleOpen()
    }, [])

    useEffect(() => {
        if (typeof currentTime !== 'number') return
        if (loaded) return
        onClear(currentTime, false)
        setLoaded(true)

    }, [currentTime])


    useEffect(() => {
        if (!loaded) false
        if (!form.isEditing && !isOpen) {
            navigate(`/v3/events/${selectedEvent.id}/tagger/highlight`)
        }
    }, [isOpen])


    const checkExactTimes = () => {
        let invalidTags = form.tags.filter(tag => {
            return getTimeSeconds(tag.exactTime) < getTimeSeconds(form.start)
                || getTimeSeconds(tag.exactTime) > getTimeSeconds(form.end)
        })
        if (invalidTags.length > 0) {
            dispatch(errorToast(invalidTags[0].mainTag.name + ' has an invalid time comparing to highlight start and end time'))
            return false;
        }
        return true;
    }
    const onHighlightSave = (e) => {

        if (!checkExactTimes()) return

        dispatch(confirmToast({
            message: 'Are sure you want to save this highlight?',
            callback: () => {
                dispatch(storeHighlight({
                    ...form,
                    eventId: selectedEvent.id,
                    sectionId: selectedSection.id
                }))
                navigate(`/v3/events/${selectedEvent.id}/tagger/highlight`)
                handleClose()
            }
        }))
    }


    const onClear = (currentTime, withConfirm = true) => {
        const func = () => {
            let form = {
                exactTime: secondsToTime(currentTime),
                start: currentTime - 10 < 0 ? secondsToTime(0) : secondsToTime(currentTime - 10),
                end: selectedSection.video_duration >= currentTime + 10 ? secondsToTime(currentTime + 10) : secondsToTime(selectedSection.video_duration),
                min: currentTime - 23 < 0 ? secondsToTime(0) : secondsToTime(currentTime - 23),
                max: selectedSection.video_duration >= currentTime + 23 ? secondsToTime(currentTime + 23) : secondsToTime(selectedSection.video_duration),
                tags: [],
            }
            setForm(form)
        }
        if (!withConfirm) {
            func()
            return
        }
        dispatch(confirmToast({
            message: 'Are sure you want to clear this highlight?',
            callback: () => {
                func()
            }
        }))

    }
    const tagFormRef = useRef()
    useEffect(() => {
        if (!tagFormRef.current) return
        tagFormRef.current.focus()
    }, [tagFormRef.current])
    const onDeleteTag = (detail) => {
        dispatch(confirmToast({
            message: 'Are sure you want to delete this tag?',
            callback: () => {
                let newTags = [...form.tags]
                const foundIndex = newTags.findIndex(i => i.index === detail.index)
                if (foundIndex > -1) {
                    newTags.splice(foundIndex, 1)
                }
                setForm(prevForm => ({
                    ...prevForm,
                    tags: newTags
                }))
            }
        }))

    }
    if (!userCanCreateHighlight) {
        return <NotAuthorizedComponent/>
    }
    if (!form.start) return
    return (<div className={"flex flex-col gap-2"} ref={tagFormRef}>
            <TagForm
                handlePlaySingleDuration={handlePlaySingleDuration}
                isPlayingSingleDuration={isPlayingSingleDuration}
                currentTime={currentTime}
                handleClose={handleClose}
                handleOpen={handleOpen}
                changeTime={changeTime}
                form={form}
                setForm={setForm}
                selectedTag={tag}
                setSelectedTag={setTag}
                onHighlightSave={onHighlightSave}
            />
            {form.tags.map((tag, key) => <HighlightDetail
                setTag={setTag}
                key={key}
                detail={tag}
                onDeleteFromDraft={onDeleteTag}
            />)}

            {form.tags.length > 0 && (
                <div className={"flex flex-row items-center justify-between gap-2"}>
                    <Button className={"flex flex-row flex-1 items-center gap-2 justify-center bg-white rounded-md"}
                            onClick={() => onClear(currentTime)}
                            isLoading={isStoringHighlight}
                    >
                        <DeleteIcon/>
                        <Text>Clear Highlight</Text>
                    </Button>
                    <Button

                        className={"flex flex-row flex-1 items-center justify-center gap-2 bg-blue-100 rounded-md"}
                        isLoading={isStoringHighlight}
                        onClick={onHighlightSave}
                    >
                        <CheckIcon color={"white"}/>
                        <Text className={"text-white"}>Save Highlight</Text>
                    </Button>
                </div>
            )}
        </div>

    )
}
