import React, {useEffect, useState} from "react";
import {Button} from "../_components/button";
import {PanelSideIcon} from "../_components/icons";
import {useEvent} from "../_utils/hooks";
import {Route, Routes, useNavigate, useParams} from "react-router-dom";
import {IndexPlayer} from "../_routes/events/tagger/player/index.player.route";
import {EditPlayer} from "../_routes/events/tagger/player/edit.player.route";

export const PlayerSidebar = (props) => {
    const height = props.height
    const navigate = useNavigate()

    const selectedEvent = useEvent()

    const [selectedTeam, setSelectedTeam] = useState({})
    const [isOpen, setIsOpen] = useState(false)

    const onDeletePlayer = (e, player) => {
        e.stopPropagation()
        console.log(player)
    }


    useEffect(() => {
        if (!selectedTeam) return
        if (selectedTeam.id === selectedEvent.homeTeam.id) {
            setSelectedTeam(selectedEvent.homeTeam)
        } else {
            setSelectedTeam(selectedEvent.awayTeam)
        }
    }, [selectedEvent.homeTeam, selectedEvent.awayTeam]);




    return (
        <div className={"flex flex-row items-center justify-between gap-4 absolute top-0 right-0 z-20"}>
            {!isOpen && (
                <Button
                    className={"bg-[#00000080] rounded-full items-center flex justify-center absolute top-4 right-4 z-20 p-3"}
                    onClick={() => setIsOpen(true)}
                >
                    <PanelSideIcon/>
                </Button>
            )}

            <div
                className={"absolute top-3 right-0 bg-[#EDEEF0B2] min-w-[482px] z-30 rounded-l-2xl flex flex-col border-b border-gray-600 overflow-y-auto " +
                    (!isOpen && 'hidden opacity-0')}
                style={{
                    backdropFilter: 'blur(2px)',
                    boxShadow: '-4px 0px 17px 0px #00000033',
                    minHeight: Math.round(height - 32) + 'px',
                    maxHeight: Math.round(height - 32) + 'px',
                }}
            >
                <div className={"p-4 flex flex-col gap-4"}>
                    <Routes>
                        <Route path={''} index element={<IndexPlayer setIsOpen={setIsOpen} selectedTeam={selectedTeam}
                                                                     setSelectedTeam={setSelectedTeam}/>}/>
                        <Route path={'edit/:playerId'} element={<EditPlayer currentTime={props.currentTime} setIsOpen={setIsOpen}/>}/>
                    </Routes>
                </div>
            </div>

        </div>
    )
}

