import {Button} from "./button";
import {Text} from "./text";
import {ArrowDownIcon, TimesIcon} from "./icons";
import React, {useState} from "react";
import {Highlight} from "./highlight";

export const HighlightTopBox = ({highlight, handleCancel}) => {
    const [isOpen, setIsOpen] = useState(false)
    const openHighlight = () => {
        setIsOpen(!isOpen)
    }
    if (highlight === null) {
        return
    }
    return (
        <div className={"flex flex-row items-center justify-between gap-4"}>


            <div className={"flex flex-col gap-2"}>
                <Button
                    onClick={openHighlight}
                    className={"bg-white border border-blue-200 rounded-full flex flex-row items-center justify-center gap-2 px-2"}>
                    <Text className={"text-gray-200"}>{highlight.id} - </Text>
                    <Text
                        className={"font-bold"}>{highlight.details.map((detail, key) => (detail.mainTag.name +
                        (detail.exactTime ? `(${detail.exactTime})` : '')
                        + (key !== highlight.details.length - 1 ? ' - ' : '')))}</Text>
                    <ArrowDownIcon color={"black"}/>
                </Button>
                {isOpen && <Highlight highlight={highlight} withoutButtons/>}

            </div>
            <Button onClick={handleCancel}
                    className={"bg-white rounded-full w-8 h-8 border border-blue-200 flex items-center justify-center self-start"}>
                <TimesIcon color={"black"}/>
            </Button>

        </div>
    )
}
