import {configureStore} from '@reduxjs/toolkit'
import eventReducer from '@tagger/store/event/event.slice'
import modalReducer from '@tagger/store/modal/modal.slice'
import settingReducer from '@tagger/store/setting/setting.slice'
import toastReducer from '@tagger/store/toast/toast.slice'
import competitionReducer from '@tagger/store/competition/competition.slice'
import organizationReducer from '@tagger/store/organization/organization.slice'
import authReducer from "./_store/auth/auth.slice";

export const store = configureStore({
    reducer: {
        organization:organizationReducer,
        competition:competitionReducer,
        event: eventReducer,
        modal: modalReducer,
        setting: settingReducer,
        toast: toastReducer,
        auth: authReducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware({
        serializableCheck: false
    }),
})
