import {createBrowserRouter, createHashRouter} from "react-router-dom";
import React from "react";
import IndexRoute from "./events/tagger/index.route";
import {SectionsRoute} from "./events/tagger/sections.route";
import {HighlightRoute} from "./events/tagger/highlight.route";
import {TaggingRoute} from "./events/tagger/tagging.route";
import {ClipsRoute} from "./events/tagger/clips.route";
import {PanelLayout} from "../_layouts/panel.layout";
import {CompetitionsRoute} from "./competitions/competitions.route";

const router = createBrowserRouter([
    {
        path: "/v3/events/:eventId/tagger",
        index: true,
        element: <IndexRoute/>,
    },
    {
        path: "/v3/events/:eventId/tagger/sections/edit/:sectionId",
        index: true,
        element: <SectionsRoute/>,
    },
    {
        path: "/v3/events/:eventId/tagger/highlights/:filter",
        index: true,
        element: <HighlightRoute/>,
    },
    {
        path: "/v3/events/:eventId/tagger/clips",
        index: true,
        element: <ClipsRoute/>,
    },
    {
        path: "/v3/events/:eventId/tagger/*",
        index: true,
        element: <TaggingRoute/>,
    },

    {
        path: "/v3/*",
        element: <PanelLayout/>,
        children: [
            {
                path: 'competitions/*',
                index: true,
                element: <CompetitionsRoute />
            }
        ]
    },
]);

export default router
