import axios from 'axios'
import {store} from "../store";
import {errorToast, successToast} from "../_store/toast/toast.slice";
import {closeModal} from "../_store/modal/modal.slice";
import {changeHighlightDownloadLink, replacePlayer} from "../_api/event.api";

export const Api = axios.create({

   baseURL: 'http://192.168.100.100:9200/v3',
    // baseURL: 'http://192.168.100.85:4000/v3',
    // baseURL: 'http://127.0.0.1:5000/v3',
    // baseURL: 'http://192.168.100.166:8003/v3',
    // baseURL: 'https://operator.smartrunners.ai:2096/v3',
    // baseURL: 'https://operator.smartrunners.ai/v3',
    headers: {
        'HTTP-REQUEST-FROM': 'App-Tagger'
    }
})
Api.defaults.headers.common['X-CSRF-TOKEN'] = document.querySelector('meta[name="csrf-token"]').getAttribute('content');

Api.interceptors.response.use(response => {
    let message = response.data.message
    if (message) {
        store.dispatch(successToast(message))
    }
    if(!response.data.status_code) {
        store.dispatch(closeModal())
    }

    return response
}, error => {
    let message
    switch (error.response.status) {
        case 500:
            message = error.response.data.message
            break;
        case 422:
            message = error.response.data.message
            break;
        case 419:
            message = error.response.data.message
            break;
    }
    store.dispatch(errorToast(message))
    return Promise.reject(error);
})
export const OrganizationApi = {
    get: () => Api.get('api/organizations/index'),
}
export const CompetitionApi = {
    get: () => Api.get('api/competitions/index'),
    store: (data) => Api.post('api/competitions/store', data),
    update: (data) => Api.patch('api/competitions/update', data),
    delete: (data) => Api.patch('api/competitions/delete', data)
}
export const AuthApi = {
    get: () => Api.get('api/auth'),
}
export const FootbarApi = {
    searchByTracker: (data) => Api.post('footbar/search/tracker', data)
}
export const EventApi = {
    getSettings: (eventId) => Api.get(`settings/${eventId}`),
    getData: (eventId) => Api.get(`events/${eventId}`),
    setStatus: (data) => Api.patch(`events/${data.eventId}/setStatus`, {
        status: data.status
    }),
    setTeamColor: (data) => Api.patch(`events/${data.eventId}/setTeamColor`, {
        color: data.color,
        team_id: data.team_id
    }),
    storeSection: (data) => Api.patch(`events/${data.eventId}/section/store`, data),
    setEventVideoUrls: (data) => Api.patch(`events/${data.eventId}/section/videos/set`, {
        links: data.links,
    }),
    updateSection: (data) => Api.patch(`events/${data.eventId}/section/update`, {
        video_url: data.section.video_url,
        section_id: data.section.id,
        video_start_time: data.section.video_start_time ?? '00:00',
        video_end_time: data.section.video_end_time ?? '00:00',
        duration: data.section.duration ?? 0,
    }),
    deleteSection: (data) => Api.patch(`events/${data.eventId}/section/delete`, {
        section_id: data.section_id,
    }),
    addPlayer: (data) => Api.patch(`events/${data.eventId}/player/store`, data),
    deletePlayer: (data) => Api.patch(`events/${data.eventId}/player/delete`, data),
    replacePlayer: (data) => Api.patch(`events/${data.eventId}/player/replace`, data),
    storeHighlight: (data) => Api.patch(`events/${data.eventId}/highlight/store`, data),
    updateHighlight: (data) => Api.patch(`events/${data.eventId}/highlight/update`, data),
    deleteHighlight: (data) => Api.patch(`events/${data.eventId}/highlight/delete`, data),
    deleteHighlightDetail: (data) => Api.patch(`events/${data.eventId}/highlight/detail/delete`, data),
    storeCustomHighlight: (data) => Api.patch(`events/${data.eventId}/highlight/custom/store`, data),
    deleteCustomHighlight: (data) => Api.patch(`events/${data.eventId}/highlight/custom/delete`, data),
    separateHighlights: (data) => Api.patch(`events/${data.eventId}/highlight/separate`, data),
    changeHighlightDownloadLink: (data) => Api.patch(`events/${data.eventId}/highlight/update/download_link`, data),
    changeHighlightDescription: (data) => Api.patch(`events/${data.eventId}/highlight/update/description`, data),
}
