import {Caption, Text, Title} from "../_components/text";
import {ActionCard, Card} from "../_components/card";
import {Button} from "../_components/button";
import {DeleteIcon, PlusIcon, ReplaceIcon} from "../_components/icons";
import {
    Select,
    SelectMenu,
    SelectTime,
    SelectVideoTime,
    SelectVideoTimeWithCaption,
    SelectWrapper
} from "../_components/select";
import {InputMinute, InputRadio, InputText} from "../_components/input";
import {getTimeSeconds, secondsToTime} from "../_utils/helpers";
import {Box} from "../_components/box";
import React, {useEffect, useState} from "react";
import {FootbarApi} from "../_utils/api";
import {addPlayer, deletePlayer} from "../_api/event.api";
import {
    useEventPlayer, useEventPlayers,
    useEventSectionOptions,
    usePositionDirectionOptions,
    usePositionOptions,
    useTeamPlayerOptions, useTeamPlayers
} from "../_utils/hooks";
import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";
import {groupBy} from "lodash/collection";
import {userHasPermission} from "../_hooks/user.hooks";
import {confirmToast, errorToast, permissionToast} from "../_store/toast/toast.slice";
import ReplacePlayerForm from "./replace-player.form";

export const PlayerForm = (props) => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {selectedEvent, isAddingPlayer, selectedSection} = useSelector(state => state.event)
    const {team, player} = useEventPlayer(selectedEvent.id, props.playerId)
    const userCanDeletePlayer = userHasPermission('player-delete')
    const userCanEditPlayer = userHasPermission('player-edit')
    const userCanReplacePlayer = userHasPermission('player-merge-by-event')

    const positionOptions = usePositionOptions(selectedEvent.id)
    const directionOptions = usePositionDirectionOptions()
    const sectionOptions = useEventSectionOptions(selectedEvent.id)
    const [footbarMenuIsOpen, setFootbarMenuIsOpen] = useState(false)
    const [footbarOptions, setFootbarOptions] = useState([])
    const [footbarGroupedOptions, setFootbarGroupedOptions] = useState({})
    const [form, setForm] = useState({
        inMinute: "00:00",
        outMinute: "00:00",
        inSectionId: null,
        outSectionId: null,
        inStarting: false,
        outStarting: false,
        duration: '00:00',
        positionId: "",
        squadNumber: "",
        positionSide: "",
    })



    useEffect(() => {

        if (player.id) {
            setForm({
                ...form,
                inStarting: player.starting_lineup,
                outStarting: player.ending_lineup,
                inMinute: player.in_time ?? "00:00",
                outMinute: player.out_time ?? "00:00",
                inSectionId: player.in_section_id ?? null,
                outSectionId: player.out_section_id ?? null,
                duration: player.minutes_played ?? "00:00",
                positionId: player.position_id ?? "",
                squadNumber: player.squad_number ?? null,
                positionSide: player.position_side ?? null,
                footbars: player?.footbars ?? [],
                playerId: player.id,
                description: player.description ?? "",
                sensor_uptime: player.sensor_uptime
            })
            setFootbarOptions(player.footbars?.length > 0 ?
                player.footbars?.map(footbar => (
                    {
                        name: footbar.footbar_id.toString(),
                        // caption: item.start_at + ' - ' + item.stop_at,
                        caption: "",
                        value: footbar.id.toString(),
                    }
                )) :
                [])
        }
    }, [player]);

    const onSetTimeIn = () => {
        setForm(prevForm => ({
            ...prevForm,
            inSectionId: selectedSection.id,
            inMinute: secondsToTime(props.currentTime),
            inStarting: false,
        }))
    }
    const onSetTimeOut = () => {
        setForm(prevForm => ({
            ...prevForm,
            outSectionId: selectedSection.id,
            outMinute: secondsToTime(props.currentTime),
            outStarting: false
        }))
    }
    const onSave = (e) => {
        if (form.positionId === null) {
            dispatch(errorToast('Please Select a Position'))
            return
        }

        dispatch(addPlayer({
            ...form,
            eventId: selectedEvent.id,
            teamId: team.id
        }))
        setFootbarOptions([])
        setFootbarGroupedOptions([])

    }
    const onDelete = (player) => {
        if (!userCanDeletePlayer) {
            dispatch(permissionToast())
            return
        }
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this player?',
            callback: () => {
                dispatch(deletePlayer({
                    eventId: selectedEvent.id,
                    playerId: player.id,
                    teamId: team.id
                }))
                navigate(`/v3/events/${selectedEvent.id}/tagger/player`)
            }
        }))
    }
    const handleAddFootbars = (footbar) => {
        let newForm = {...form}
        const nextFootbars = [...newForm.footbars]
        const foundIndex = nextFootbars.findIndex(s => s.footbar_id == footbar.footbar_id)
        if (foundIndex > -1) {
            nextFootbars.splice(foundIndex, 1)
            newForm.footbars = nextFootbars
            setForm(newForm)
            return
        }
        nextFootbars.push(footbarOptions.find(s => s.footbar_id === footbar.footbar_id))
        newForm.footbars = nextFootbars

        setForm(newForm)

    }
    const handleSearchFootbars = async (query) => {
        if (query.length < 3) return
        const formatTime = (time) => {
            const date = new Date(time);
            const hours = String(date.getUTCHours()).padStart(2, '0'); // Get hours in UTC and pad with leading zero if needed
            const minutes = String(date.getUTCMinutes()).padStart(2, '0'); // Get minutes in UTC and pad with leading zero if needed
            return `${hours}:${minutes}`;

        }
        try {
            const response = await FootbarApi.searchByTracker({
                tracker_number: query,
                date: selectedEvent.date.substring(0, 10)
            })

            const receivedOptions = response.data.data.res.map(item => ({
                footbar_id: item.id,
                start_at: formatTime(item.start_date),
                stop_at: formatTime(item.stop_date),
                tracker_number: query,
                name: query,
                date: (new Date(item.start_date)).toDateString(),
                caption: formatTime(item.start_date) + ' - ' + formatTime(item.stop_date),
                value: item.id,
            }))
            const groupedOptions = groupBy(receivedOptions, 'date')
            setFootbarOptions(receivedOptions)
            setFootbarGroupedOptions(groupedOptions)
        } catch (e) {
            console.error(e)
        }
    }
    const handleDeleteFootbar = (session) => {
        setForm(prevForm => {
            let newForm = {...prevForm}
            const foundIndex = form.footbars.findIndex(s => s.id === session.value)
            const nextFootbars = [...form.footbars]
            nextFootbars.splice(foundIndex, 1)
            newForm.footbars = nextFootbars
            return newForm
        })
    }
    const handleUptimeFootbar = (value) => {
        setForm(prevForm => ({
                ...prevForm,
                sensor_uptime: value
            })
        )
    }


    const render = () => (
        <React.Fragment>
            <ActionCard
                title={(
                    <div className={"flex flex-row items-center gap-2  p-4"}>
                        <img src={team.logo} className={"w-8 h-8 "}
                             alt={team.name}/>
                        <Title>{player.full_name}</Title>
                    </div>
                )}
                actions={(
                    <Button className={"p-4"} onClick={() => onDelete(player)}>
                        <DeleteIcon/>
                    </Button>
                )}
            />
            <SelectWrapper
                className={"rounded-t-md border border-gray-600 mt-4"}
                title={"Number"}
            >
                <InputText className={" p-4 bg-transparent border-none"}
                           value={form.squadNumber}
                           onChange={(e) => setForm({
                               ...form,
                               squadNumber: e.target.value
                           })}
                />
            </SelectWrapper>
            {positionOptions.length > 0 && <SelectWrapper
                className={"border-x border-gray-600"}
                title={"Position"}
            >
                <Select multiple={false}
                        selected={form.positionId}
                        onChange={(e) => setForm({
                            ...form,
                            positionId: e.value
                        })}
                        className={"p-4" + " min-w-[240px]"}
                        options={positionOptions}
                ></Select>
            </SelectWrapper>}
            <SelectWrapper
                className={"rounded-b-md border border-gray-600"}
                title={"L/R"}
            >
                <Select multiple={false}
                        disabled={form.positionId === undefined || form.positionId === ""}

                        selected={form.positionSide}
                        onChange={(e) => setForm({
                            ...form,
                            positionSide: e.value
                        })}
                        className={"p-4" + " min-w-[240px]"}
                        options={directionOptions}
                ></Select>
            </SelectWrapper>
            <SelectWrapper
                className={"rounded-t-md border border-gray-600 mt-4"}
                title={"In Time"}
            >
                <div className={"flex flex-col items-end gap-2 mb-2"}>
                    <Button icon onClick={onSetTimeIn}>
                        <Text class={"text-blue-200"}>Set Time</Text>
                        <PlusIcon color={"#0571FC"}/>
                    </Button>
                    <div className={"flex flex-row gap-2"}>
                        <InputRadio selected={form.inStarting ?? false}
                                    name={"Starting Lineup"}
                                    onChange={e => setForm({
                                        ...form,
                                        inStarting: e,
                                        inSectionId: e ? sectionOptions[0].value : null,
                                        inMinute: "00:00"
                                    })}/>
                        <Select multiple={false}
                                label={"Select Section"}
                                selected={form.inSectionId}
                                disabled={form.inStarting === true}
                                onChange={(e) => setForm({
                                    ...form,
                                    inSectionId: e.value
                                })}
                                className={"p-2" + " min-w-[160px] bg-gray-300 rounded-lg"}
                                options={sectionOptions}
                        ></Select>
                        <SelectVideoTime
                            max={selectedEvent.sections.find(s => s.id === form.inSectionId)?.video_duration}
                            value={form.inMinute}
                            disabled={form.inStarting === true || !form.inSectionId}
                            onChange={(e) => setForm({
                                ...form,
                                inMinute: e
                            })}
                        />
                    </div>
                </div>
            </SelectWrapper>
            <SelectWrapper
                className={"border-x border-gray-600"}
                title={"Out Time"}
            >
                <div className={"flex flex-col items-end gap-2 mb-2"}>
                    <Button icon onClick={onSetTimeOut}>
                        <Text class={"text-blue-200"}>Set Time</Text>
                        <PlusIcon color={"#0571FC"}/>
                    </Button>
                    <div className={"flex flex-row gap-2"}>
                        <InputRadio
                            selected={form.outStarting ?? false}
                            name={"Ending Lineup"} onChange={e => setForm({
                            ...form,
                            outStarting: e,
                            outSectionId: e ? sectionOptions.at(-1).value : null,
                            outMinute: secondsToTime(selectedEvent.sections.at(-1).video_duration),
                        })
                        }
                        />
                        <Select multiple={false}
                                label={"Section"}
                                selected={form.outSectionId}
                                disabled={form.outStarting === true}
                                onChange={(e) => setForm({
                                    ...form,
                                    outSectionId: e.value
                                })}
                                className={"p-2" + " min-w-[160px] bg-gray-300 rounded-lg"}
                                options={sectionOptions}
                        ></Select>
                        <SelectVideoTime
                            value={form.outMinute}
                            max={selectedEvent.sections.find(s => s.id === form.outSectionId)?.video_duration}
                            disabled={form.outStarting === true || !form.outSectionId}
                            onChange={(e) => setForm({
                                ...form,
                                outMinute: e
                            })}
                        />
                    </div>
                </div>
            </SelectWrapper>
            <SelectWrapper
                className={"rounded-b-md border border-gray-600"}
                title={"Duration"}
            >
                <div className={"flex flex-row gap-2 items-center"}>
                    <Text>{form.duration}m</Text>
                    <SelectVideoTime
                        value={form.duration}

                        onChange={(e) => setForm({
                            ...form,
                            duration: e
                        })}/>
                </div>
            </SelectWrapper>
            <Box
                className={"mt-4"}
                title={"Sensor Number (Footbar)"}
                actions={[
                    <Button className={"flex flex-row gap-2 items-center relative"}
                            onClick={() => setFootbarMenuIsOpen(!footbarMenuIsOpen)}>
                        <Text className={"text-blue-100"}>Add</Text>
                        <PlusIcon color={"#0571FC"}/>
                        <SelectMenu onChange={handleAddFootbars}
                                    onSearch={handleSearchFootbars}
                                    className={"min-w-[240px] right-0 top-0"}
                                    setIsOpen={setFootbarMenuIsOpen}
                                    multiple={true}
                                    isOpen={footbarMenuIsOpen}
                                    selected={form.footbars?.map(item => ({value: item.id}))}
                                    options={footbarOptions}
                                    groupedOptions={footbarGroupedOptions}
                        />
                    </Button>
                ]}
                body={
                    form.footbars?.map(footbar => (
                        <div key={footbar.footbar_id}
                             className={"bg-white p-4 border-b border-gray-600 last-of-type:rounded-b-md flex flex-col gap-2"}>
                            <div className={"flex flex-row items-center justify-between"}>
                                <div className={"flex flex-col text-left gap-1"}>
                                    <Title>{footbar.footbar_id}</Title>
                                    <Caption>{footbar.tracker_number}</Caption>
                                    <Caption>{footbar.start_at} - {footbar.stop_at}</Caption>
                                </div>
                                <SelectVideoTime
                                    value={player.sensor_uptime ?? '00:00'}
                                    className={"self-center ml-auto mr-4"}
                                    min={0}
                                    max={120000}
                                    onChange={handleUptimeFootbar}
                                />
                                <Button onClick={() => handleDeleteFootbar(footbar)}>
                                    <DeleteIcon color={"red"}/>
                                </Button>
                            </div>
                        </div>
                    ))
                }
            />
            <SelectWrapper
                className={"rounded-t-md border border-gray-600 mt-4"}
                title={"Description"}
            >
                <InputText className={" p-4 bg-transparent border-none w-full"}
                           value={form.description}
                           onChange={(e) => setForm({
                               ...form,
                               description: e.target.value
                           })}/>
            </SelectWrapper>
            {userCanReplacePlayer && (
                <ReplacePlayerForm player={player} />
            )}
            <Button className={"w-full bg-blue-100 text-white p-4 mt-4 rounded-full"}
                    isLoading={isAddingPlayer}
                    onClick={onSave}>
                <Text>Save</Text>
            </Button>
        </React.Fragment>
    )
    if (props.disabledCardView) {
        return render()
    }
    return (
        <Card
            className={"col-span-5"}
            header={<Title>Player Details</Title>}
            body={<div className={"flex flex-col"}>
                {render()}
            </div>
            }
        />
    )
}
