import {useEffect, useRef} from "react";
import {useHotKeys} from "./hooks";

export const PreventFocusOutOfWindow = () => {
    const ref = useRef()
    const isPressed = useHotKeys((e) => {
        if (e.key === 'Tab') {
            if (document.activeElement.id === ref.current.id) {
                e.preventDefault()

                let el = document.querySelector('#firstEl')
                el.focus()
            }
        }
    })


    return (
        <div id={"lastEl"} tabIndex={0} ref={ref}/>
    )
}
