import {Button} from "../../_components/button";
import {Box} from "../../_components/box";
import {useEffect} from "react";
import {useDispatch} from "react-redux";
import {useCompetitions} from "../../_utils/hooks";
import {deleteCompetition, getCompetitions} from "../../_api/competition.api";
import {Spinner} from "../../_components/spinner";
import {Text} from "../../_components/text";
import {useNavigate} from "react-router-dom";
import {confirmToast} from "../../_store/toast/toast.slice";
import {getOrganizations} from "../../_api/organization.api";

export const CompetitionsIndexRoute = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const {isFetched, isLoading, data} = useCompetitions()


    const onDelete = (competition) => {
        dispatch(confirmToast({
            message: 'Are you sure you want to delete this competition?',
            callback: () => {
                dispatch(deleteCompetition({
                    id: competition.id
                }))
            }
        }))
    }
    useEffect(() => {
        if (isFetched) return
        dispatch(getCompetitions())
    }, []);
    return (
        <div className={"flex flex-col"}>
            {isLoading && <Spinner type={"block"}/>}
            <Box className={"w-full mt-4"}
                 title={"Competitions"}
                 headerClass={"border-b !border-gray-500"}
                 actions={[
                     <Button icon isCreate className={"px-4 text-blue-100"} onClick={() => navigate('create')}>
                         <Text>Create</Text>
                     </Button>
                 ]}
                 body={(
                     <table className={" border-gray-600 bg-gray-800 mt-2"}>
                         <thead>
                         <tr>
                             <th colSpan={3} className={"!text-left"}>Name</th>
                             <th colSpan={3} className={"!text-left"}>Organization</th>
                             <th colSpan={1}>Action</th>
                         </tr>
                         </thead>
                         <tbody>
                         {data.map(competition => (
                             <tr>
                                 <td colSpan={3} className={"!text-left"}>{competition.name}</td>
                                 <td colSpan={3} className={"!text-left"}>{competition.organization?.name}</td>
                                 <td colSpan={1}>
                                     <div className={"flex flex-row gap-2 items-center justify-center"}>
                                         {competition.isEditable && (
                                             <Button icon className={"bg-gray-300"} roundedFull
                                                     onClick={() => navigate(`edit/${competition.id}`)}>
                                                 <Text>Edit</Text>
                                             </Button>
                                         )}

                                         {competition.isDeletable &&
                                             (
                                                 <Button icon className={"bg-red-200 text-white"} roundedFull
                                                         onClick={() => onDelete(competition)}>
                                                     <Text>Delete</Text>
                                                 </Button>
                                             )
                                         }
                                     </div>
                                 </td>
                             </tr>
                         ))}
                         </tbody>
                     </table>
                 )}
            />
        </div>
    )
}
