import {useEffect, useRef, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {getSettings} from "../_api/setting.api";
import {cloneDeep} from "lodash";


export const useEventSectionsAreReady = () => {
    const event = useEvent()
    if (event.sections.length === 0) {
        return false;
    }
    return event.sections.every(section => section.video_url !== null && section.video_start_time && section.video_end_time)
}
export const useCompetitions = () => {
    return useSelector(state => state.competition)
}
export const useOrganizations = () => {
    return useSelector(state => state.organization)
}
export const useOrganizationOptions = () => {

    const [options, setOptions] = useState([])
    const {data} = useOrganizations()
    useEffect(() => {
        if (!data) return
        setOptions(data.map(organization => ({
                name: organization.name,
                value: organization.id
            }))
        )
    }, [data]);
    return options
}

export const useCompetition = (id) => {
    const {data} = useCompetitions()
    return data.find(competition => Number(competition.id) === Number(id))
}


export const useEvent = () => {
    return useSelector(state => state.event.selectedEvent)
}
export const useTeams = () => {
    const event = useEvent()
    return {
        homeTeam: event.homeTeam,
        awayTeam: event.awayTeam
    }
}
export const useHighlights = () => {
    const selectedEvent = useEvent()
    let array = []
    selectedEvent.sections.forEach(
        section =>
            section.highlights.map(highlight => array.push(highlight))
    )
    return array
}
export const useTeamHighlights = (teamId) => {
    const allHighlights = useHighlights()

    return allHighlights.filter(highlight => {
        return highlight?.details.filter(detail => detail.team_id === teamId).length > 0 || highlight.details.length === 0
    })

}
export const useTeamTags = (teamId) => {
    const tags = useTags(useTeamHighlights(teamId))
    return tags.filter(detail => {
        return detail.team_id === teamId
    })
}
export const useTags = (allHighlights) => {
    let tags = []
    allHighlights.forEach(highlight => {
        highlight.details.forEach(detail => {
            tags.push(detail)
        })
    })
    return tags
}
export const useOutsideClick = (callback) => {
    const currentElement = useRef(null)
    const handleOutsideClick = (e) => {
        if (!currentElement.current.contains(e.target)) {
            callback()
        }
    }
    useEffect(() => {
        document.addEventListener('mousedown', handleOutsideClick)
        return () => document.removeEventListener('mousedown', handleOutsideClick)
    }, []);
    return currentElement
}


export const usePositionOptions = (eventId) => {
    const dispatch = useDispatch()
    const settings = useSelector(state => state.setting)
    const [options, setOptions] = useState([])
    useEffect(() => {
        if (settings.positions.length !== 0) {
            let array = settings.positions
                .map(position => ({
                    name: position.abbr,
                    value: position.id,
                }))
            array.push({
                name: 'Unknown Position',
                value: "",
            })
            setOptions(array)
            return
        }
        dispatch(getSettings(eventId))

    }, [settings?.positions])
    return options
}
export const usePositionDirectionOptions = () => {

    return [
        {name: 'Right', value: 'R'},
        {name: 'Left', value: 'L'},
    ]
}

export const useEventSection = (eventId, sectionId) => {
    const [section, setSection] = useState({})
    const {selectedEvent} = useSelector(state => state.event)
    useEffect(() => {
        if (selectedEvent !== null) {
            setSection(selectedEvent.sections.find(s => Number(s.id) === Number(sectionId)))
        }
    }, [])
    return section
}
export const useEventPlayers = () => {
    const {homeTeam, awayTeam} = useTeams()
    const players = []
    homeTeam.players.forEach(player => players.push(player))
    awayTeam.players.forEach(player => players.push(player))
    return players
}
export const useEventPlayer = (eventId, playerId) => {
    const [player, setPlayer] = useState({})
    const [team, setTeam] = useState({})
    const selectedEvent = useEvent()

    const findFn = p => Number(p.id) === Number(playerId)

    const findPlayer = (playerId) => {
        let found = selectedEvent.homeTeam.players.find(findFn)
        if (!found) {
            found = selectedEvent.homeTeam.allPlayers.find(findFn)
        }
        if (found) {
            setPlayer(found)
            setTeam(selectedEvent.homeTeam)
            return
        }
        found = selectedEvent.awayTeam.players.find(findFn)
        if (!found) {
            found = selectedEvent.awayTeam.allPlayers.find(findFn)
        }
        if (found) {
            setPlayer(found)
            setTeam(selectedEvent.awayTeam)
        }
    }
    useEffect(() => {
        if (!selectedEvent) return
        if (!playerId) return
        findPlayer(playerId)
    }, [playerId]);
    useEffect(() => {
        findPlayer(playerId)
    }, [selectedEvent]);
    return {team, player};
}
export const useEventSectionOptions = (eventId) => {

    const [options, setOptions] = useState([])
    const {selectedEvent} = useSelector(state => state.event)
    useEffect(() => {
        if (!selectedEvent) return
        setOptions(selectedEvent.sections.map(section => ({
                name: section.name,
                value: section.id
            }))
        )
    }, [selectedEvent]);
    return options
}

export const usePlayer = (playerId, team) => {
    const findFn = player => player.id === playerId
    let foundPlayer
    foundPlayer = team.players?.find(findFn)
    if (!foundPlayer) {
        foundPlayer = team.allPlayers?.find(findFn)
    }
    return foundPlayer

}


export const useTeamPlayers = (teamId) => {
    const [players, setPlayers] = useState([])
    const {selectedEvent} = useSelector(state => state.event)

    useEffect(() => {
        if (selectedEvent === null) return
        let foundTeam = selectedEvent.homeTeam.id === teamId ? selectedEvent.homeTeam : selectedEvent.awayTeam

        setPlayers(foundTeam.allPlayers.filter(aPlayer => !foundTeam.players.map(p => p.id).includes(aPlayer.id)))
    }, [selectedEvent]);

    return players
}
export const useTeamEventPlayers = (teamId) => {
    const [players, setPlayers] = useState([])
    const {selectedEvent} = useSelector(state => state.event)

    useEffect(() => {
        if (selectedEvent === null) return
        let foundTeam = selectedEvent.homeTeam.id === teamId ? selectedEvent.homeTeam : selectedEvent.awayTeam

        setPlayers(foundTeam.players)
    }, [selectedEvent]);

    return players
}

export const useTeamPlayerOptions = (players) => {
    return players.map(player => ({
        name: player.full_name_with_data,
        value: player.id,
    }))
}

export const useSteps = (props) => {
    const [steps, setSteps] = useState([])
    const [isOpen, setIsOpen] = useState(false)

    const goToLastStep = () => {
        setSteps(prevSteps => {
            let newSteps = [...prevSteps]
            let poppedStep = newSteps.pop()
            if (poppedStep.callback) {
                poppedStep.callback()
            }
            return newSteps
        })
    }
    const currentStep = () => steps.at(-1)
    const pushSteps = (step) => {
        setSteps(prevSteps => [
            ...prevSteps,
            step
        ])
    }
    useEffect(() => {
        if (!props.title) return
        pushSteps({id: props.title, title: props.title})
    }, [props.title]);
    return {steps, goToLastStep, currentStep, pushSteps, isOpen, setIsOpen}
}
export  const activeElementIsInput = () => {
    if (document.activeElement.tagName === 'INPUT'
        || document.activeElement.tagName === 'TEXTAREA'
        || document.activeElement.tagName === 'SELECT'
    ) {
        return true;
    }
    return false;
}
export const useHotKeys = (listeners) => {
    const [hotKeyPressed, setHotKeyPressed] = useState(false)

    const hotKeyListener = (e) => {
        setHotKeyPressed(true)

        if (
            e.target &&
            (e.target instanceof HTMLTextAreaElement ||
                (e.target instanceof HTMLInputElement && (!e.target.type || e.target.type === "text")))
        ) {
            e.stopPropagation()
            return
        }
        listeners(e)
    }
    useEffect(() => {
        setHotKeyPressed(false)
        document.removeEventListener("keydown", hotKeyListener)
        document.addEventListener('keydown', hotKeyListener)
        return () => document.removeEventListener("keydown", hotKeyListener)
    }, [hotKeyPressed]);
    return hotKeyPressed
}

export const useFilterMenu = () => {
    const selectedEvent = useEvent()
    const setting = useSelector(state => state.setting)
    return selectedEvent ? [
        {
            type: 'team',
            value: selectedEvent.homeTeam.id,
            name: selectedEvent.homeTeam.name,
            icon: selectedEvent.homeTeam.logo,
            childrenLabel: 'Players',
            children: selectedEvent.homeTeam.players.map(player => ({
                name: player.full_name_with_data,
                type: 'player',
                value: player.id,
            }))
        },
        {
            type: 'team',
            value: selectedEvent.awayTeam.id,
            name: selectedEvent.awayTeam.name,
            icon: selectedEvent.awayTeam.logo,
            childrenLabel: 'Players',
            children: selectedEvent.awayTeam.players.map(player => ({
                name: player.full_name_with_data,
                type: 'player',
                value: player.id,
            }))
        },
        {
            selectable: false,
            name: 'Tags',
            childrenLabel: 'Tags',
            children: setting.tags.map(tag => ({
                name: tag.name,
                type: 'tag',
                value: tag.id
            })),
        }
    ] : []
}
export const useHighlightFilter = () => {
    const filterMenu = useFilterMenu()
    const [filterMenuIsOpen, setFilterMenuIsOpen] = useState(false)
    const [selectedFilters, setSelectedFilters] = useState([])

    const [searchQuery, setSearchQuery] = useState("")
    const onSelectFilter = (option, isSelected) => {
        let newFilters = cloneDeep(selectedFilters)
        if (isSelected) {
            newFilters.push(option)
        } else {
            const foundIndex = newFilters.findIndex(filter => filter.value === option.value && filter.type === option.type)
            if (foundIndex > -1) {
                newFilters.splice(foundIndex, 1)
            }
        }
        setSelectedFilters(newFilters)
    }
    const onSearchQueryChange = (e) => setSearchQuery(e.target.value)


    const clearAllFilters = () => {
        setSelectedFilters([])
    }
    const filterFn = (highlight) => {
        const cleanString = (string) => string.toString().toLowerCase().replaceAll(' ', '')
        let comparisonArray = []
        if (searchQuery) {
            let explodedQuery = searchQuery.split(' ')
            if (explodedQuery.length > 0) {
                explodedQuery.forEach(searchQuery => {
                    comparisonArray.push(
                        highlight.details.filter(detail => cleanString(detail.player).includes(cleanString(searchQuery))).length > 0
                        || highlight.details.filter(detail => cleanString(detail.mainTag.name).includes(cleanString(searchQuery))).length > 0
                        || (highlight.comment && cleanString(highlight.comment).includes(cleanString(searchQuery)))
                        || highlight.details.filter(detail => cleanString(detail.team).includes(cleanString(searchQuery))).length > 0
                        || highlight.details.filter(detail =>
                            detail.subTags.filter(subTag =>
                                cleanString(subTag.name).includes(cleanString(searchQuery))
                            ).length > 0
                        ).length > 0
                        || highlight.details.filter(detail =>
                            detail.comment && cleanString(detail.comment).includes(cleanString(searchQuery))
                        ).length > 0
                    )
                })
            }
        }
        if (selectedFilters.length > 0) {
            selectedFilters.forEach(filter => {
                switch (filter.type) {
                    case 'team':
                        comparisonArray.push(highlight.details.filter(detail => detail.team_id === filter.value).length > 0)
                        break;
                    case 'player':
                        comparisonArray.push(highlight.details.filter(detail => detail.player_id === filter.value).length > 0)
                        break;
                    case 'tag':
                        comparisonArray.push(highlight.details.filter(detail => detail.mainTag.id === filter.value).length > 0)
                        break;
                }
            })
        }

        return comparisonArray.every(item => item)
    }
    return {
        filterMenu,
        filterMenuIsOpen,
        setFilterMenuIsOpen,
        onSelectFilter,
        onSearchQueryChange,
        filterFn,
        searchQuery,
        selectedFilters,
        clearAllFilters
    }
}


export const useAuth = () => {
    return useSelector(state => state.auth)
}
