import {createSlice} from '@reduxjs/toolkit'


const initialState = {
    message: null,
    type: null,
    timer: 3000,
    isConfirm: false,
    callback: null
}

export const toastSlice = createSlice({
    name: 'event',
    initialState,
    reducers: {
        successToast: (state, action) => {
            state.message = action.payload
            state.type = 'success'
        },
        errorToast: (state, action) => {
            state.message = action.payload
            state.type = 'error'
        },
        permissionToast: (state, action) => {
            state.message = 'Insufficient Permissions'
            state.type = 'error'
        },
        clearToast: (state) => {
            state.message = null
            state.type = null
            state.isConfirm = false
            state.callback = null
        },
        confirmToast: (state, action) => {
            state.message = action.payload.message
            state.type = 'confirm'
            state.isConfirm = true
            state.callback = action.payload.callback
        },
    },
})

export const {
    successToast,
    clearToast,
    errorToast,
    confirmToast,
    permissionToast
} = toastSlice.actions

export default toastSlice.reducer
