import {useNavigate, useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import React, {useEffect} from "react";
import {fetchEventById} from "../../../_api/event.api";
import {VideoPlayer} from "../../../_components/video-player";
import {Spinner} from "../../../_components/spinner";
import {getSettings} from "../../../_api/setting.api";
import {useEvent} from "../../../_utils/hooks";

export const TaggingRoute = () => {
    const {eventId} = useParams()
    const dispatch = useDispatch()
    const event = useEvent()
    const {isLoading} = useSelector(state => state.event)

    useEffect(() => {

        if (event) return

        (async () => {
            await dispatch(fetchEventById(eventId))
            await dispatch(getSettings(eventId))
        })()

    }, [event]);


    if(isLoading) {
        return <Spinner type={"block"} />
    }

    return (
        <div>
            <VideoPlayer title={"Tags"}/>
        </div>
    )

}
