import {SearchIcon, TimesIcon} from "./icons";
import {Button} from "./button";
import React, {forwardRef, useState} from "react";
import {InputText} from "./input";

export const SearchButton = ({query, onChange}) => {
    const [isOpen, setIsOpen] = useState(false)
    return (
        <React.Fragment>
            {!isOpen && <Button className={"rounded-full border border-gray-300"} onClick={() => setIsOpen(true)}>
                <SearchIcon/>
            </Button>}
            {isOpen && <SearchButtonInput query={query} onChange={onChange} onClose={() => setIsOpen(false)}/>}
        </React.Fragment>
    )
}
export const SearchButtonInput = ({query, onChange, onClose, className = ""}) => {
    return (
        <div
            className={`p-2 border border-gray-600 flex flex-row items-center justify-between rounded-lg gap-2 ${className}`}>
            <InputText
                onKeyDown={(e) => e.stopPropagation()}
                className={"!p-1 w-full"}
                value={query}
                onChange={onChange}
            />
            <Button onClick={() => {
                onChange({
                    target: {
                        value: ""
                    }
                })
                onClose()
            }}>
                <TimesIcon/>
            </Button>

        </div>
    )
}
export const SearchInput = forwardRef(function SearchInput({query, onChange, className = ""},ref) {
        return (
            <div className={`relative flex flex-row ${className}`}>
                <div className={"absolute left-4 top-3"}>
                    <SearchIcon/>
                </div>
                <InputText
                    ref={ref}
                    onKeyDown={(e) => e.stopPropagation()}
                    className={"!p-2 !pl-10 w-full rounded-2xl bg-gray-700 hover:bg-gray-600 border border-transparent outline-0 active:bg-white focus:border focus:border-blue-200 "}
                    value={query}
                    placeholder={"Search.."}
                    onChange={onChange}
                />
            </div>
        )
    }
)
