import {SpinnerIcon} from "./icons";

export const Spinner = props => {
    const type = props.type ?? null
    const classList = (props.type === 'block' ? 'absolute w-full h-full bg-gray-950 opacity-95 z-30 top-0 left-0 ' : "")
        + "flex items-center justify-center "
        + props.className
    return (
        <div className={classList}>
            <SpinnerIcon className={"animate-spin"}  color="white" width={type==='block' ? 50 : 16} height={type==='block' ? 50 : 16} />
        </div>
    )
}
