import {createSlice} from '@reduxjs/toolkit'
import {deleteCompetition, getCompetitions, storeCompetition, updateCompetition} from "../../_api/competition.api";


const initialState = {
    data: [],
    selected: [],
    isSuccess: false,
    isError: false,
    isFetched: false,
    isLoading: false,
}

export const competitionSlice = createSlice({
    name: 'competition',
    initialState,
    reducers: {
        resetCompetition: (state) => {
            state.isSuccess = false
            state.isError = false
            state.isLoading = false
        }
    },
    extraReducers: (builder) => {
        // Get Competitions
        builder.addCase(getCompetitions.fulfilled, (state, action) => {
            state.data = action.payload
            state.isFetched = true
            state.isLoading = false
        })
        builder.addCase(getCompetitions.pending, (state, action) => {
            state.isLoading = true;
        })
        // Store Competitions
        builder.addCase(storeCompetition.fulfilled, (state, action) => {
            state.data.push(action.payload)
            state.isLoading = false
            state.isSuccess = true
            state.isError = false
        })
        builder.addCase(storeCompetition.rejected, (state, action) => {
            state.isLoading = false
            state.isSuccess = false
            state.isError = false

        })
        builder.addCase(storeCompetition.pending, (state, action) => {
            state.isLoading = true;

        })
        // Update Competition
        builder.addCase(updateCompetition.fulfilled, (state, action) => {
            const foundIndex = state.data.findIndex(competition => competition.id === action.payload.id)
            if (foundIndex > -1) {
                state.data[foundIndex] = action.payload
            }
            state.isLoading = false
            state.isSuccess = true

        })
        builder.addCase(updateCompetition.rejected, (state, action) => {
            state.isLoading = false
        })
        builder.addCase(updateCompetition.pending, (state, action) => {
            state.isLoading = true;
            state.isSuccess = false
        })
        // Delete Competition
        builder.addCase(deleteCompetition.fulfilled, (state, action) => {
            const foundIndex = state.data.findIndex(competition => competition.id === action.payload.id)
            if (foundIndex > -1) {
                state.data.splice(foundIndex, 1)
            }
            state.isLoading = false
        })
        builder.addCase(deleteCompetition.rejected, (state, action) => {
            state.isLoading = false
        })
        builder.addCase(deleteCompetition.pending, (state, action) => {
            state.isLoading = true;
        })
    }

})

export const {resetCompetition} = competitionSlice.actions
export default competitionSlice.reducer
