import {ArrowDownIcon, ArrowLeftIcon, ArrowUpIcon, PlusIcon, SpinnerIcon} from "./icons";
import {useNavigate} from "react-router-dom";
import {useSelector} from "react-redux";
import {Text, Title} from "./text";
import {forwardRef, useState} from "react";
import {List, ListItem} from "./list";
import {useOutsideClick} from "../_utils/hooks";

export const Button = forwardRef(function Button(props, ref) {
    let classList = "p-2 " + props.className
    const isLoading = props.isLoading
    if (props.icon === true) {
        classList += " flex flex-row items-center justify-center gap-2"
    }
    if (props.isCreate === true) {
        classList += " text-blue-100"
    }
    if (props.rounded === true) {
        classList += " rounded-xl"
    }
    if (props.roundedFull === true) {
        classList += " rounded-full px-4"
    }

    const disabled = props.disabled ?? false
    if (disabled) {
        classList += " bg-gray-500 !text-gray-disabled"
    }
    return (
        <button ref={ref} tabIndex={props.tabIndex ?? -1} type={"button"} onClick={props.onClick} className={classList}
                disabled={isLoading || disabled}>
            {!isLoading && props.isCreate && <PlusIcon color={"#0571FC"}/>}

            {isLoading ? <SpinnerIcon/> : props.children}
        </button>
    )

})
export const ButtonBackToMain = (props) => {
    const navigate = useNavigate()
    const {selectedEvent} = useSelector(state => state.event)
    const navigateBack = () => navigate(`/v3/events/${selectedEvent.id}/tagger`)
    return (
        <Button className={"bg-[#00000080] rounded-full items-center flex justify-center p-[10px] self-start"}
                onClick={navigateBack}>
            <ArrowLeftIcon width={24} height={24} color={"white"}/>
        </Button>
    )
}
export const ButtonBack = (props) => {
    const navigate = useNavigate()
    const navigateBack = () => navigate(-1)
    return (
        <Button icon
                className={"bg-white border border-gray-300 rounded-full items-center flex justify-center p-[10px]"}
                onClick={navigateBack}>
            <ArrowLeftIcon width={24} height={24} color={"black"}/>
            <Text>Back</Text>
        </Button>
    )
}

export const ButtonDropdown = (props) => {
    const [open, setOpen] = useState(false)
    const ref = useOutsideClick(() => setOpen(false))
    const title = props.title
    return (
        <Button ref={ref}
                className={"bg-[#00000080] rounded-full p-2 flex items-center justify-center gap-2 px-4 relative self-start"}
                onClick={(e) => setOpen(!open)}>
            <div className={"flex flex-row items-center justify-center gap-2"}>
                {props.children}
                <Title className={"text-white"}>{title}</Title>
            </div>

            {!open && <ArrowDownIcon color={"white"}/>}
            {open && <ArrowUpIcon color={"white"}/>}
            {open && <List className={"absolute top-10 bg-[#00000080] rounded-lg"}>
                {props.items}
            </List>}
        </Button>
    )
}
